'use strict';

module.exports = {
    // todo: remove in favor of _.merge
    copyOwnProperties: function copyOwnProperties(from, to, deep) {
        if (typeof from !== 'object' || typeof to !== 'object') {
            throw new TypeError('Not an object');
        }

        for (var prop in from) {
            if (from.hasOwnProperty(prop)) {
                if (deep && typeof from[prop] === 'object' && from[prop] !== null) {
                    to[prop] = {};
                    copyOwnProperties(from[prop], to[prop]);
                } else {
                    to[prop] = from[prop];
                }
            }
        }

        return to;
    },

    /**
     * Extends passed object with additional data.
     * Main aim - extend native IE modules.
     * @param {Object} module
     * @param {Object} additionalData
     * @returns {Object}
     */
    extendModule: function (module, additionalData) {
        return Object.keys(module)
            .reduce(function (newModule, fieldName) {
                if (!newModule[fieldName]) {
                    newModule[fieldName] = module[fieldName];
                }
                return newModule;
            }, additionalData);
    }

};



