/**
 *   
 * https://beta.wiki.yandex-team.ru/visualbookmarks/advertonvb/#statistika
 */
'use strict';

var clicker = require('./clicker');
var advertisement = require('../advertisement');
var Block = require('../advertisement/block');
var platformAbout = require('../../platform/about');
var logger = require('../../utils/logger').create('Statistics/advertisement');

var CID = 72582;

exports.init = function () {
    var blocks = advertisement.getAllBlocks();
    if (!blocks.length) {
        throw new Error('Blocks not initialized!');
    }
    blocks.forEach(function (block) {
        block.onStartShowing.addListener(onStartShowing);
        block.onRefuse.addListener(onRefuse);
    });
};

function onStartShowing(block) {
    send(block, 'show');
}

function onRefuse(block, reason) {
    send(block, getParamByRefuseReason(reason));
}

function send(block, param) {
    if (param) {
        var path = [platformAbout.id, clicker.getPlatformVersion(), block.getId(), param];
        clicker.send({path: path, cid: CID});
    } else {
        logger.warn('Empty param for block %s', block.getId());
    }
}

function getParamByRefuseReason(reason) {
    switch (reason) {
        case Block.REFUSE_REASON.ACCEPT: return 'accept';
        case Block.REFUSE_REASON.USERCLOSE: return 'close';
        case Block.REFUSE_REASON.DURATION: return 'timeoutclose';
        case Block.REFUSE_REASON.EXTERNAL: return 'externalclose';
    }
}



