/**
 * Debug-panel
 */
'use strict';

var settings = require('../settings/settings');
var messaging = require('./messaging');
var debug = require('../debug/debug');
var utils = require('../../utils/utils');
var logger = require('../../utils/logger').create('Frontend/debug-panel');
var tabUtils = require('../../utils/tabs');
var alarms = require('../../utils/alarms');
var platformAbout = require('../../platform/about');
var events = require('./events');
var thenChrome = require('then-chrome');

var commandsMap = {
    toggleDebugMode: function (tabId) {
        settings.set('debugMode', !settings.get('debugMode'));
        chrome.tabs.reload(tabId);
    },
    reinstall: function (tabId) {
        debug.reset()
            .then(() => thenChrome.tabs.reload(tabId))
            .then(() => debug.reload());
    },
    reload: function () {
        debug.reload();
    },
    show: function (tabId, param) {
        debug.show[param]();
    },
    'run|pickup': function () {
        alarms.trigger('pickup');
    },
    open: function (tabId, count, url) {
        openTabs(count, url);
    },
    clear: function (tabId, param) {
        debug.clear[param]();
    },
    'clear|history': function () {
        chrome.history.deleteAll(utils.noop);
    },
    'clear|tabs': function () {
        removeTabs();
    },
};

/**
 * 
 */
exports.init = function () {
    messaging.on(events.frontend.DEBUG_COMMAND, function (req, sender) {
        runCommand(req.command, sender.tab.id);
    });
    messaging.on(events.frontend.DEBUG_SHOW, function (req, sender) {
        exports.send(sender.tab.id);
    });
};

/**
 * 
 */
exports.send = function (tabId) {
    messaging.send(events.backend.DEBUG_PANEL, getItems(), tabId);
};

function getItems() {
    return [
        {text: ' ', command: 'reinstall'},
        {text: '  ', command: 'reload'},
        {text: '   (   extensions)', command: 'clear|tabs'},
        {text: (settings.get('debugMode') ? '' : '') + '  ', command: 'toggleDebugMode'},
        {text: '', children: [
            {text: '', command: 'show|log'},
            {text: '', command: 'show|settings'},
            {text: ' ', command: 'show|thumbs'},
            {text: ' ', command: 'show|pickupcache'},
            {text: ' ', command: 'show|blacklistUser'},
            {text: ' ', command: 'show|blacklistServer'},
            {text: ' ', command: 'show|adv'},
            {text: '', command: 'show|bg'},
            {text: '', command: 'show|branding'},
            {text: ' ', command: 'show|custombranding'},
            {text: ' ', command: 'show|files'},
            {text: '', command: 'show|experiments'},
        ]},
        {text: '', children: [
            {text: '', command: 'run|pickup'},
            // {text: ' ', command: 'run|bg'},
        ]},
        {text: '', children: [
            {text: '3 mail.ru', command: 'open|3|http://mail.ru'},
            {text: '3 lenta.ru', command: 'open|3|http://lenta.ru'},
            {text: '6 ./term/', command: 'open|6|http://./term/'},
            {text: '7 habrahabr.ru', command: 'open|7|http://habrahabr.ru'},
            {
                text: '8 www.java.com/ru/download/faq/remove_olderversions.xml',
                command: 'open|8|http://www.java.com/ru/download/faq/remove_olderversions.xml'
            },
            {
                text: '8 http://www.java.com/ru/download/help/disable_browser.xml',
                command: 'open|8|http://www.java.com/ru/download/help/disable_browser.xml'
            },
            {text: '9 ya.ru', command: 'open|9|https://ya.ru'},
            {
                text: '10 ton-2006.happy-masters.ru/slovar.html',
                command: 'open|10|http://ton-2006.happy-masters.ru/slovar.html'
            },
            {text: '6 about:config', command: 'open|6|about:config'},
            {text: '6 chrome://extensions', command: 'open|6|chrome://extensions'},
            {text: '7 www.dojki.com', command: 'open|7|http://www.dojki.com'},
            {text: '7 signin.ebay.com', command: 'open|7|https://signin.ebay.com'},
        ]},
        {text: '', children: [
            {text: '', command: 'clear|backup'},
            {text: '', command: 'clear|settings'},
            {text: '', command: 'clear|db'},
            {text: '', command: 'clear|history'},
            // {text: ' ', command: 'clear|files'},
            {text: '  (   extensions)', command: 'clear|tabs'},
        ]},
        // , 
    ];
}

function runCommand(command, tabId) {
    var parts = command.split('|');
    var baseCommand = parts.shift();
    if (commandsMap[command]) {
        commandsMap[command]();
    } else if (commandsMap[baseCommand]) {
        commandsMap[baseCommand].apply(null, [tabId].concat(parts));
    } else {
        logger.error('Command not found: %s', command);
    }
}

/**
 *  .       
 */
function openTabs(count, url) {
    for (var i = 0; i < count; i++) {
        chrome.tabs.create({url: url, active: false});
    }
}

/**
 *        
 */
function removeTabs() {
    tabUtils.getOpen(function (err, tabs) {
        var extensionsTabFound = false;
        tabs.forEach(function (tab) {
            if (tab.url === platformAbout.visbookmarksUrl) {
                return;
            }
            if (tab.url === 'chrome://extensions/' && !extensionsTabFound) {
                extensionsTabFound = true;
                return;
            }
            chrome.tabs.remove(tab.id, utils.noop);
        });
    });
}



