/**
 * Wrapper around utils/site-loader
 * Adds additional search params to urls
 */
'use strict';

var urlUtils = require('../../utils/url');
var xhr = require('../../utils/xhr');
var SaxParser = require('../../utils/sax-parser');
var ResourceLoader = require('../../utils/resource-loader');

var resourceLoader;

var PARSER_OPTIONS = {
    tagToParse: 'head',
    children: ['link', 'base', 'title', 'meta'],
    parseOptions: {
        lowercase: true,
        normalize: true,
        noscript: true
    }
};

/**
 * Parses HEAD tag of provided html string
 * @returns {Object|null} result
 */
function saxParse(htmlString) {
    try {
        var parser = new SaxParser(PARSER_OPTIONS);
        return parser.parse(htmlString);
    } catch (e) {
        return null;
    }
}

/**
 * Creates instance of ResourceLoader
 */
exports.init = function () {
    resourceLoader = new ResourceLoader();
};

/**
 * Loads site's markup
 * @param {String} url
 * @param {Function} callback
 */
exports.load = function (url, callback) {
    resourceLoader.load({
        id: url,
        loadMethod: loadMethod.bind(null, url),
        callback: callback
    });
};

/**
 * @param {String} url
 * @param {Function} callback
 */
function loadMethod(url, callback) {
    xhr({
        url: urlUtils.addTechnicalParam(url),
        parse: 'text'
    }, function (err, htmlString) {
        callback(err, saxParse(htmlString));
    });
}



