/**
 * Contains logic for updating and saving examples and current index
 */
'use strict';

var sort = require('lodash/collection/sortByOrder');
var storage = require('./state-storage');
var sum = require('lodash/math/sum');

var EXAMPLE_WEIGHT_FIELD = 'weight';

/**
 * @param {Array<Object>} examples
 */
exports.refreshExamples = function (examples) {
    var sortedList = sortExamples(examples);
    storage.setExamples(sortedList);
};

/**
 * @returns {{text: String, weight: Number}|null}
 */
exports.getExample = function () {
    var examples = storage.getExamples();
    return getRandomItem(examples);
};

/**
 * @param {Array<Object>} list
 * @returns {Array<Object>}
 */
function sortExamples(list) {
    return sort(list, ['weight'], ['desc']);
}

/**
 * @param {Array<Object>} list
 * @returns {Object}
 */
function getRandomItem(list) {
    var normalWeights = getNormalizedItems(list, EXAMPLE_WEIGHT_FIELD);
    var randomNumber = Math.random();
    var item = findRandomItem(normalWeights, randomNumber);
    if (!item) {
        return list[Math.floor(randomNumber * list.length)];
    } else {
        return list[item.index];
    }
}

/**
 * @param {Array<Numbers>} list
 * @param {String} field
 * @returns {Array<{value: Number, index: Number}>}
 */
function getNormalizedItems(list, field) {
    var totalSum = sum(list, field);
    if (!totalSum) {
        return [];
    }
    return list.map(function (item, index) {
        return {value: item[field] / totalSum, index: index};
    });
}

/**
 * @param {Array<{value: Number, index: Number}>} list
 * @param {Number} number
 * @returns {{value: Number, index: Number}|null}
 */
function findRandomItem(list, number) {
    var item = null;
    var currentLength = 0;
    for (var i = 0; i < list.length; i++) {
        item = list[i];
        currentLength += item.value;
        if (currentLength >= number) {
            return item;
        }
    }
    return item;
}



