/**
 * Synchronizer for GUID value
 * @see note in sync/state
 */
'use strict';

var platform = require('../../platform');

var UI_PREFIX = '__guid__';

/**
 * Returns snapshot of GUID data to sync
 * @see note in sync/state
 * @returns {Object}
 */
exports.snapshot = function () {
    var data = {};
    data[UI_PREFIX + platform.guid] = true;
    return data;
};

/**
 * @see note in sync/state
 */
exports.update = function () {};

/**
 * Returns guids from snapshot of sync data
 * @param {Object} snapshot
 * @returns {Array<String>}
 */
exports.getGUIDs = function (snapshot) {
    return Object.keys(snapshot).reduce(function (result, key) {
        if (key.startsWith(UI_PREFIX)) {
            result.push(key.replace(UI_PREFIX, ''));
        }
        return result;
    }, []);
};



