'use strict';

var _ = require('../../utils/lodash');
var error = require('../error/error');
var dateUtils = require('../../utils/date');

var DEFAULT_OPTIONS = {
    minVisits: 3,
    maxResults: 100,
    days: 20
};

function isValidItem(historyItem, options) {
    if (historyItem.visitCount < options.minVisits) {
        return false;
    }
    if (!historyItem.title) {
        return false;
    }
    if (!/^(http|ftp)s?/.test(historyItem.url)) {
        return false;
    }
    return true;
}

module.exports = function (options, callback) {
    options = _.defaults({}, options, DEFAULT_OPTIONS);
    chrome.history.search({
        text: '',
        maxResults: options.maxResults,
        startTime: Date.now() - options.days * dateUtils.DAY_MS
    }, error.wrap(function (records) {
        var output = records
            .filter(function (historyItem) {
                return isValidItem(historyItem, options);
            })
            .map(function (historyItem) {
                return {
                    url: historyItem.url,
                    title: historyItem.title,
                    lastVisitTime: historyItem.lastVisitTime,
                    visits: historyItem.visitCount,
                    history: true,
                };
            });
        callback(output);
    }));
};



