/**
 *    :   .
 *     ,   ,
 *  ,    .on
 *
 *      , .
 */
'use strict';

var browserInfo = require('../../utils/browser-info');
var dateUtils = require('../../utils/date');
var ONCE_HANDLER_LIFETIME = dateUtils.SECOND_MS * 10;

module.exports = Redirector;

function Redirector(checker) {
    this.check = checker;
    this.handlers = [];

    this._onCompleted = function ({url, tabId}) {
        this._act({
          url,
          id: tabId
        });
    }.bind(this);
}

Redirector.prototype.enable = function () {
    chrome.webNavigation.onCompleted.addListener(this._onCompleted);
    return this;
};

Redirector.prototype.disable = function () {
    chrome.webNavigation.onCompleted.removeListener(this._onCompleted);
    return this;
};

Redirector.prototype._act = function (tab) {
    if (tab.url && this.check(tab.url)) {
        this.handlers.some(function (handler) {
            var result = handler.method(tab);
            if (result && handler.once) {
                this._off(handler);
            }
            return result;
        }, this);
    }
};

/**
 * Adds handler that will be executed once.
 * It will prevent other handlers exec if triggered.
 * Hanler will be automatically unsubscribed after timout.
 * @param {string} tabId
 * @param {Function} handler
 */
Redirector.prototype.once = function (tabId, handler) {
    var off = this.on(this._onceHandler.bind(this, tabId, handler), true);
    setTimeout(off, ONCE_HANDLER_LIFETIME);
};

/**
 * Method calls handler and returns true, if it's called with right tab.
 * Otherwise returns false.
 * @param {String} tabId
 * @param {Function} handler
 * @param {Object} tab
 * @returns {Boolean}
 */
Redirector.prototype._onceHandler = function (tabId, handler, tab) {
    // In IE we can get id of hidden tab, not of tab with frontend.
    // So, skip tab id check for ie.
    if (tab.id === tabId || browserInfo.isIE()) {
        handler(tab);
        return true;
    }
    return false;
};

/**
 * Adds handler on trying to load url.
 *
 * TODO: use utils/channel
 * @param {Function} handler
 * @param {Boolean} once
 * @returns {Function} unsubscribe method
 */
Redirector.prototype.on = function (handler, once) {
    var handlerObject = {method: handler, once: once};
    this.handlers.unshift(handlerObject);
    return this._off.bind(this, handlerObject);
};

/**
 * Removes handler from list.
 *
 * @param {Object} handler
 */
Redirector.prototype._off = function (handler) {
    var handlerIndex = this.handlers.indexOf(handler);
    if (handlerIndex !== -1) {
        this.handlers.splice(handlerIndex, 1);
    }
};



