/**
 * Removes obsolete log levels preferences from settings
 */

'use strict';

var logger = require('../../utils/logger').create('migration-2.26.0');

exports.VERSION = '2.26.0';

/**
 * @param {Object} options
 * @param {Function} callback
 */
exports.run = function (options, callback) {
    logger.info('Starting...');
    var settingsToRemove = [
        'settings.logLevelConsole', //   consoleLogLevels
        'settings.logLevelFile', //   platformLogLevels
        'settings.experimentConfigs', // , ..         
        'settings.advertisementConfig', //     remote-config
        'settings.showStatModal', //    2.24    ,    
    ];
    try {
        chrome.storage.local.remove(settingsToRemove, handleRemoveEvent.bind(null, callback));
    } catch (e) {
        logger.error('Problem with chrome.storage.local api: %o', e);
        callback();
    }
};

/**
 * @param {Function} callback
 */
function handleRemoveEvent(callback) {
    if (chrome.runtime.lastError) {
        logger.error('runtime.lastError in handleRemoveEvent: %o', chrome.runtime.lastError);
    } else {
        logger.info('Settings have been removed');
    }
    callback();
}



