/**
 *         ( ).
 *
 *    :
 * 1.       ,     ,    
 * 2.        ,     
 *
 *  2   :
 * - options.succesLifetimeMs   
 * - options.errorLifetimeMs   
 *
 *  /      ,    .
 */

'use strict';

var LoaderItem = require('./resource-loader-item');

/**
 * @constructor
 * @param {Object} [options]
 * @param {Number} [options.succesLifetimeMs] lifetime of success cache
 * @param {Number} [options.errorLifetimeMs] lifetime of error cache
 */
var ResourceLoader = module.exports = function (options) {
    this._options = options;
    this._items = [];
};

/**
 * Loads resource using passed loadMethod.
 * If resource with given id exist in cache,
 * loader doesn't invoke loadMethod, and gives resource from cache.
 * If resource with given id is being loaded,
 * loader doesn't invoke loadMethod, and waits for load process to be completed.
 * In any case, callback will be called with lodaded/cached data (or errors)
 * Loader doesn't save errors to cache, but it can be overriden by cacheErrors option
 * @param {Object} options
 * @param {String} options.id
 * @param {Function} options.loadMethod
 * @param {Function} options.callback
 */
ResourceLoader.prototype.load = function (options) {
    var loaderItem = this._getItem(options.id);
    if (!loaderItem) {
        loaderItem = this._createItem(options.id);
    }
    loaderItem.addCallback(options.callback);
    loaderItem.loadAttempt(options.loadMethod);
};

/**
 * Clear items with expired lifetime
 */
ResourceLoader.prototype.clearExpired = function () {
    this._items = this._items.filter(function (item) {
        return !item.isExpired();
    });
};

/**
 * Returns cache for resource with given id.
 * If index is passed, returns data from particular cache position (argument position in loadMethod callback).
 * @param {String} id
 * @param {Number} [argumentIndex]
 * @returns {null|Array|*}
 */
ResourceLoader.prototype.getCache = function (id, argumentIndex) {
    var loaderItem = this._getItem(id);
    if (!loaderItem) {
        return null;
    }
    var data = loaderItem.getData();
    if (data && argumentIndex !== undefined) {
        return data[argumentIndex];
    }
    return data;
};

/**
 * Checks cache for resource with given id
 * @param {String} id
 * @returns {Boolean}
 */
ResourceLoader.prototype.hasCache = function (id) {
    var loaderItem = this._getItem(id);
    return loaderItem ? Boolean(loaderItem.getData()) : false;
};

/**
 * @param {String} id
 * @returns {LoaderItem|null}
 */
ResourceLoader.prototype._getItem = function (id) {
    for (var i = 0; i < this._items.length; i++) {
        if (this._items[i].id === id) {
            return this._items[i];
        }
    }
    return null;
};

/**
 * Create item
 */
ResourceLoader.prototype._createItem = function (id) {
    var item = new LoaderItem(id, this._options);
    this._items.push(item);
    return item;
};



