/**
 *         
 */

'use strict';

var utils = require('../../utils/utils');
var database = require('../database/database');
var sorter = require('./sorter');
var logger = require('../../utils/logger').create('Pickup/candidates-storage');

var TABLE = 'pickup';

module.exports = {
    /**
     *      
     * @param {Array} items
     * @param {Function} callback
     */
    save: function (items, callback) {
        callback = callback || utils.noop;
        if (!Array.isArray(items)) {
            var message = 'Can not save non-array';
            logger.error(message);
            callback(new Error(message));
        }
        var dbItems = items
            .filter(function (item) {
                return Boolean(item.url);
            })
            .map(function (item) {
                return {
                    url: item.url,
                    visits: item.visits,
                    index: item.index, // index required for sorting, VB-5156
                };
            });
        logger.info('Saving candidates cache, %i item(s)', dbItems.length);
        database.clearAndInsert(TABLE, dbItems, callback);
    },
    /**
     *      
     * @param {Function} callback (err, items)
     */
    get: function (callback) {
        database.get(TABLE, {}, function (err, records) {
            if (err) {
                callback(err);
            } else {
                logger.info('Loaded from db: %i', records.length);
                records.sort(sorter);
                callback(null, records);
            }
        });
    }
};



