/**
 * Platform log - writing to file
 */
'use strict';

var fs = require('../platform/file-storage');

// When file size is bigger than MAX_SIZE, copy it to debug-old.log and truncate
var DEBUG_FILENAME = 'debug.log';
var DEBUG_FILENAME_OLD = 'debug-old.log';
var DEBUG_MAX_SIZE = 5 * 1024 * 1024; // 5 Mb
var MAX_NESSAGES_QUEUE_LENGTH = 500;

var isRunning = false;
var messagesQueue = [];
var messagesQueueWriting = [];

module.exports = {
    //       ,   log-level    
    info: write,
    warn: write,
    error: write,
    getAsText: function (callback) {
        fs.readAsText(DEBUG_FILENAME, function (err, debug) {
            debug = debug || '';
            fs.readAsText(DEBUG_FILENAME_OLD, function (err, debugOld) {
                debugOld = debugOld || '';
                if (debugOld) {
                    debugOld = debugOld + '\n======= DEBUG-OLD END =======\n';
                }
                callback(debugOld + debug);
            });
        });
    }
};

function write(str) {
    if (arguments.length) {
        messagesQueue.push(str);
        truncateQueue(messagesQueue);
    }

    if (!messagesQueue.length || !fs.isInitialized || isRunning) {
        return;
    }

    isRunning = true;

    messagesQueueWriting = messagesQueueWriting.concat(messagesQueue);
    truncateQueue(messagesQueueWriting);
    messagesQueue.length = 0;

    fs.write(DEBUG_FILENAME, {
        data: '\n' + messagesQueueWriting.join('\n'),
        type: 'text/plain',
        append: true
    }, onWrite);
}

/**
 * On write handler
 */
function onWrite(err, fileEntry, fileWriter) {
    if (!err) {
        messagesQueueWriting.length = 0;
        if (fileWriter.length > DEBUG_MAX_SIZE) {
            fs.move(DEBUG_FILENAME, '.', DEBUG_FILENAME_OLD, function () {
                isRunning = false;
                //  -   ,  
                write();
            });
        } else {
            isRunning = false;
            //  -   ,  
            write();
        }
    } else {
        isRunning = false;
    }
}

/**
 * truncate queue if too may items
 */
function truncateQueue(queue) {
    if (queue.length > MAX_NESSAGES_QUEUE_LENGTH) {
        queue.splice(0, MAX_NESSAGES_QUEUE_LENGTH / 2);
    }
}



