/**
 *      ,  
 */
'use strict';

exports.toJSON = function (usersList) {
    return usersList && usersList.hasAuth
        ? usersList.users
            .sort(usersSorter.bind(null, usersList))
            .map(convertUserToFrontend.bind(null, usersList))
        : [];
};

/**
 * @see https://st.yandex-team.ru/VBCHROME-579
 *
 * @param {UsersList} usersList
 * @param {User} userA
 * @param {User} userB
 * @returns {number|*}
 */
function usersSorter(usersList, userA, userB) {
    // reverse!
    return (usersList.isDefault(userB) - usersList.isDefault(userA))
        || (userB.hasAuth - userA.hasAuth)
        || (userB.lastAuthTime - userA.lastAuthTime);
}

function getUserStatus(usersList, user) {
    return usersList.isDefault(user)
        ? 2
        : user.hasAuth
            ? 1
            : 0;
}

function convertUserToFrontend(usersList, user) {
    return {
        id: user.uid,
        displayName: user.displayName,
        state: getUserStatus(usersList, user),
        avatarURL: user.avatarUrl,
    };
}



