/**
 *  
 */

'use strict';

var branding = require('../branding');
var curly = require('../../utils/curly');
var platformAbout = require('../../platform/about');

/**
 *       
 */
exports.get = function () {
    return branding.thumbs.items.map(function (item, index) {
        return {
            url: curly.insertUrlVars(item.url, {vbID: platformAbout.type}),
            index: index,
            pinned: Boolean(item.pinned),
            visits: item.boost || branding.thumbs.boost,
            branded: true,
        };
    });
};



