/**
 * Clicker statistics for synchronization
 */
'use strict';

var logger = require('../../utils/logger').create('clicker-sync');
var clicker = require('./clicker');
var sync = require('../sync');

var SYNC_STAT_NAME = 'system.chromesync';

exports.init = function () {
    sync.onRemoteUpdated.addListener(checkRemote);
};

function checkRemote() {
    sync.checkRemote()
        .then(sendStat)
        .catch(logReject);
}

function sendStat() {
    clicker.send({path: SYNC_STAT_NAME});
}

/**
 * @param {Error} [reason]
 */
function logReject(reason) {
    if (reason) {
        logger.info('sync.checkRemote rejected with reason: %s', reason.message);
    }
}



