'use strict';

const messaging = require('./messaging');
const logger = require('../../utils/logger').create('Frontend/voice');
const Voice = require('../voice');
const voiceEvents = require('../voice/events');
const events = require('./events');

module.exports = {
    init() {
        let voice;

        messaging.on(events.frontend.VOICE_INIT, (request, sender) => {
            if (voice) {
                voice.finalize();
            }

            voice = Voice.createForWebNavigation(sender.tab.id)
                .record();
        });

        messaging.on(events.frontend.VOICE_STOP, () => {
            if (voice) {
                voice.finalize();
            }
        });

        voiceEvents.onProgress.addListener(({tabId, text}) => {
            messaging.send(events.backend.VOICE_PROGRESS, {text}, tabId);
        });

        voiceEvents.onStart.addListener(({tabId}) => {
            messaging.send(events.backend.VOICE_START, null, tabId);
        });

        voiceEvents.onError.addListener(({tabId, reason}) => {
            messaging.send(events.backend.VOICE_ERROR, {reason}, tabId);
        });

        voiceEvents.onRecordFinished.addListener(({tabId, text}) => {
            messaging.send(events.backend.VOICE_FINISH, {text}, tabId);
        });

        voiceEvents.onClose.addListener(({tabId}) => {
            messaging.send(events.backend.VOICE_CLOSE, null, tabId);
        });

        logger.info('Initialized');
    }
};



