/**
 *     speechkit,   .
 */
'use strict';

const logger = require('../../utils/logger').create('Voice');
const {REJECT_TIMESTAMP} = require('../common/constants');
const offscreenEvents = require('../offscreen/events');
const offscreenManager = require('../../utils/offscreen/offscreen-manager');
const events = require('./events');
const SerpNavigator = require('./consumers/serp-navigator');
const SiteNavigator = require('./consumers/site-navigator');
const Consumer = require('./consumers/consumer');
const thenChrome = require('then-chrome');
const settings = require('../settings/settings');
const {
    IS_VOICE_USED,
    ANIMATION_TIMEOUT_MS,
    ERROR_REASON,
    SILENCE_TIMEOUT_MS,
    STATUS,
} = require('./consts');

class Voice {
    /**
     * @constructor
     * @param tabId
     */
    constructor (tabId) {
        this.consumers = [];
        this.previousText = '';
        this.tabId = tabId;

        this._tabActivationHandlerBinded = this._tabActivationHandler.bind(this);
        this._updateNavigationHandlerBinded = this._updateNavigationHandler.bind(this);
        this._handleOffscreenMessageBinded = this._handleOffscreenMessage.bind(this)

        this.status = STATUS.READY;

        logger.info('Streamer is ready');
    }

    /**
     *   speechkit  .
     * 
     * @param {number} object
     * @param {number} object
     * @param {number} Function
     */
    _handleOffscreenMessage(req) {
        if (!req.data) {
            // Do nothing.
            return;
        }

        switch (req.type) {
            case offscreenEvents.offscreen.SPEECHKIT_DATA:
                this._dataHandler(req.data);
                break;
            case offscreenEvents.offscreen.SPEECHKIT_ERROR:
                this._throwError(req.data.reason);
                break;
            default:
                return;
        }
    }

    /**
     * -       .
     *
     * @param {message} object
     * @param {rejectTimeout} number
     * @private
     */
    _initSpeechkit() {
        const callback = (message) => {
            const {errObj} = message;
            if (errObj) {
                this._throwError(errObj.message);
            } else {
                this._initHandler();
            }
        };

        const msgBody =
            {type: offscreenEvents.backend.SPEECHKIT_INIT, data: {}, idle: true};
        offscreenManager.sendMessage(
            msgBody, 'Speechkit init timed out', callback);
    }

    /**
     * -       .
     *
     * @private
     */
    _stopSpeechkit() {
        const callback = (message) => {
            const {errObj} = message;
            if (errObj) {
                this._throwError(errObj.message);
            } else {
                offscreenManager.resetTimeout();
            }
        };
    
        const msgBody =
            {type: offscreenEvents.backend.SPEECHKIT_STOP, data: {}, idle: true};
        offscreenManager.sendMessage(
            msgBody, 'Speechkit stop timed out', callback);
    }

    /**
     *   
     *
     * @param {Consumer} consumer
     */
    addConsumer (consumer) {
        this.consumers.push(consumer);
    }

    /**
     *  
     *
     * @returns {Voice}
     */
    record () {
        this.status = STATUS.CONNECTING;

        this._initSpeechkit();

        offscreenManager.addOffscreenListener(this._handleOffscreenMessageBinded);
        chrome.tabs.onActivated.addListener(this._tabActivationHandlerBinded);
        chrome.windows.onFocusChanged.addListener(this._tabActivationHandlerBinded);
        chrome.webNavigation.onBeforeNavigate.addListener(this._updateNavigationHandlerBinded);
        //   ,   onBeforeNavigate  51    
        //   ,  onCommitted   onBeforeNavigate
        chrome.webNavigation.onCommitted.addListener(this._updateNavigationHandlerBinded);

        if (settings.get(IS_VOICE_USED) !== true) {
            settings.set(IS_VOICE_USED, true);
        }

        return this;
    }

    /**
     *  
     *
     * @returns {boolean}    
     */
    stopRecording () {
        if (!this.isRecording) {
            return;
        }

        clearTimeout(this.silenceTimeout);

        this.previousText = '';
        this._stopSpeechkit();

        logger.info('Recording was stopped');
    }

    /**
     *       
     * @param {number} status
     */
    finalize (status = STATUS.FINISHED) {
        if (this.isFinished) {
            return;
        }

        this.stopRecording();

        if (this.status === STATUS.PROCESSING) {
            this.consumers.forEach(consumer => consumer.stop());
        }

        this.status = status;

        offscreenManager.removeOffscreenListener(this._handleOffscreenMessageBinded);
        chrome.tabs.onActivated.removeListener(this._tabActivationHandlerBinded);
        chrome.windows.onFocusChanged.removeListener(this._tabActivationHandlerBinded);
        chrome.webNavigation.onBeforeNavigate.removeListener(this._updateNavigationHandlerBinded);
        chrome.webNavigation.onCommitted.removeListener(this._updateNavigationHandlerBinded);

        logger.info('Finalized');
    }

    /**
     *  ,      .
     *
     * @private
     */
    _initHandler () {
        this.status = STATUS.RECORD;
        this.silenceTimeout = setTimeout(() => this._throwError(ERROR_REASON.SILENCE), SILENCE_TIMEOUT_MS);

        events.onStart.dispatch({
            tabId: this.tabId,
        });
    }

    /**
     *       
     *
     * @private
     */
    _tabActivationHandler () {
        const tabId = this.tabId;

        //   ,    ,    
        //    ,     
        thenChrome.tabs
            .get(tabId)
            .then(() => {
                if (this.isReady || this.isRecording) {
                    this._interrupt(true);
                }
            })
            .catch(() => this._interrupt());
    }

    /**
     *    
     *
     * @param {boolean} sendOnClose
     * @private
     */
    _interrupt (sendOnClose = false) {
        this.finalize(STATUS.INTERRUPTED);

        if (sendOnClose) {
            events.onClose.dispatch({
                tabId: this.tabId,
            });
        }
    }

    /**
     *   onBeforeNavigate  onCommitted,      URL .
     *
     * @param {number} tabId
     * @private
     */
    _updateNavigationHandler ({tabId}) {
        if (tabId === this.tabId) {
            this._interrupt();
        }
    }

    _throwError (reason) {
        // ,      .
        if (this.inProcess) {
            this.finalize(STATUS.ERROR);

            events.onError.dispatch({
                tabId: this.tabId,
                reason,
            });
        }
    }

    /**
     *    .
     *     .
     *     
     *    .
     *
     * @param {params} object
     * @private
     */
    _dataHandler({text, done}) {
        if (this.status !== STATUS.RECORD) {
            return;
        }

        const isTextChanged = text !== this.previousText;
        const shouldNotify = isTextChanged || done;

        if (!shouldNotify) {
            return;
        }

        clearTimeout(this.silenceTimeout);

        const tabId = this.tabId;
        this.previousText = text;

        events.onProgress.dispatch({
            tabId,
            text,
        });

        if (done) {
            this.stopRecording();
            this.status = STATUS.PROCESSING;

            events.onRecordFinished.dispatch({
                tabId,
                text,
            });

            this._recognize(text)
                .catch(interceptedBy => {
                    if (interceptedBy instanceof Consumer) {
                        return Promise.resolve(interceptedBy);
                    }
                })
                .then(interceptBy => this._callPostRecognizeEvents(text, interceptBy))
                .then(interceptBy => interceptBy.run())
                .then(() => this.finalize())
                .catch(() => this.finalize(STATUS.INTERRUPTED));
        }
    }

    /**
     *   
     *
     * @param {string} text
     * @private
     */
    _recognize (text) {
        return this.consumers.reduce((promise, consumer) => {
            return promise.then(() => {
                return this._callConsumer(consumer, text);
            });
        }, Promise.resolve(null));
    }

    /**
     * @param {string} text
     * @param {Consumer} currentConsumer  
     * @returns {Promise}
     * @private
     */
    _callConsumer (currentConsumer, text) {
        //     ,   ,
        //          .
        if (this.isFinished) {
            return Promise.reject();
        }

        return currentConsumer.check(text)
            .then(isIntercepted => isIntercepted ? Promise.reject(currentConsumer) : null);
    }

    /**
     *      
     *
     * @param {text} text
     * @param {Consumer} interceptedBy
     * @private
     */
    _callPostRecognizeEvents (text, interceptedBy) {
        if (interceptedBy) {
            events.onRecognized.dispatch({
                tabId: this.tabId,
                text,
                consumer: interceptedBy,
            });
        } else {
            events.onNotRecognized.dispatch({
                tabId: this.tabId,
                text,
            });
        }

        return interceptedBy;
    }

    get isRecording () {
        return !this.isReady && this.status < STATUS.PROCESSING;
    }

    get inProcess () {
        return !this.isReady || !this.isFinished;
    }

    get isReady () {
        return this.status === STATUS.READY;
    }

    get isFinished () {
        return this.status >= STATUS.FINISHED;
    }

    /**
     *   
     *
     * @static
     * @param tabId
     * @returns {Voice}
     */
    static createForWebNavigation (tabId) {
        const voice = new Voice(tabId);

        voice.addConsumer(new SiteNavigator(tabId, ANIMATION_TIMEOUT_MS));
        voice.addConsumer(new SerpNavigator(tabId, ANIMATION_TIMEOUT_MS));

        return voice;
    }
}

module.exports = Voice;



