/**
 * Implementation of url utils for 'urn' type
 */
'use strict';

var createURI = require('./create-uri');

var DATA_URI_REG_EXP = /^data:/;
var PROTOCOL_SLASHES_REG_EXP = /:\/\//;

/**
 * @param {String} [string]
 * @returns {URI|null}
 */
exports.createURI = createURI;

/**
 * @param {String} [url]
 * @returns {String}
 */
exports.getNormalDomain = function (url) {
    try {
        var normalUri = createURI(url).normalize();
        return normalUri.protocol() + ':' + normalUri.path();
    } catch (e) {
        return '';
    }
};

/**
 * @param {String} [url]
 * @returns {String}
 */
exports.getReadableUrl = function (url) {
    try {
        return createURI(url)
            .readable()
            .replace(PROTOCOL_SLASHES_REG_EXP, ':');
    } catch (e) {
        return url;
    }
};

/**
 * @param {String} [url]
 * @returns {String}
 */
exports.getReadableDomain = exports.getNormalDomain;

/**
 * @param {String} url
 * @returns {String}
 */
exports.getNormalPath = function (url) {
    try {
        return createURI(url).normalize().path();
    } catch (e) {
        return '';
    }
};

/**
 * @returns {String}
 */
exports.getIndexPart = function () {
    return '';
};

/**
 * Is url similar to "foo:bar" (URN)
 * @returns {Boolean}
 */
exports.isSpecialUrl = function () {
    return true;
};

/**
 * Is passed url remote (has http/ftp protocols)
 * @returns {Boolean}
 */
exports.isRemote = function () {
    return false;
};

/**
 * Is url downloadable (has http/ftp/file protocols)
 * @returns {Boolean}
 */
exports.isAllowedForDownload = function () {
    return false;
};

/**
 * @param {String} url
 * @returns {Boolean}
 */
exports.isDataUri = function (url) {
    return DATA_URI_REG_EXP.test(url);
};

/**
 * Is passed url is top level (without path)
 * @param {String} url
 * @returns {Boolean}
 */
exports.isIndexPage = function (url) {
    try {
        var uri = createURI(url).normalize();
        return uri.query() === '';
    } catch (e) {
        return false;
    }
};



