/**
 *    YS,   key1.value1#key2.value2
 * https://beta.wiki.yandex-team.ru/Cookies/Y/
 */
'use strict';

var debounce = require('lodash/function/debounce');
var endsWith = require('lodash/string/endsWith');
var Channel = require('../../utils/channel');
var logger = require('../../utils/logger').create('Cookies/ys');
var YsCookie = require('yandex-elements-core/cookies/ys-cookie');
var {logUpdateResult} = require('./utils');

/**
 *  ,   
 */
var DOMAINS = [
    'yandex.ru',
    'yandex.kz',
    'yandex.ua',
    'yandex.by',
    'yandex.net',
    'yandex.com',
    'yandex.com.tr',
    'ya.ru',
];

/**
 *  
 */
var COOKIE_NAME = 'ys';

/**
 *    onChanged
 *   ,      onChanged
 */
var DISPATCH_ONCHANGED_DELAY_MS = 500;

/**
 *   "" chrome.cookies.onChanged
 *     ,      
 * @type {number}
 */
var INGONE_ONCHANGED_DELAY_MS = 100;

/**
 *    chrome.cookies.onChanged
 *    ,   ,   - 
 */
exports.init = function () {
    addOnChangedListener();
};

/**
 * /   
 */
exports.upsert = function (key, value) {
    removeOnChangedListener();
    batchUpsert(DOMAINS, key, value)
        .then(addOnChangedListenerDelayed)
        .catch(addOnChangedListenerDelayed);
};

exports.onChanged = Channel.create();

function addOnChangedListener() {
    chrome.cookies.onChanged.addListener(onChanged);
}

function removeOnChangedListener() {
    chrome.cookies.onChanged.removeListener(onChanged);
}

/**
 * @param {Array<String>} domains
 * @param {String} key
 * @param {String} value
 * @returns {Promise}
 */
function batchUpsert(domains, key, value) {
    var updatedDomains = [];
    var notUpdatedDomains = [];

    return Promise.all(domains.map(function (domain) {
        return new YsCookie(domain)
            .upsertPart(key, value)
            .then(result => {
                if (result) {
                    updatedDomains.push(domain);
                }
            })
            .catch(() => {
                notUpdatedDomains.push(domain);
            });
    }))
    .then(() => logUpdateResult(logger, updatedDomains, notUpdatedDomains))
    .catch(() => logUpdateResult(logger, updatedDomains, notUpdatedDomains));
}

/**
 *     -    ys
 * @param {String} domain  (     )
 */
function isYSDomain(domain) {
    return DOMAINS.some(function (d) {
        return d === domain || endsWith(domain, '.' + d);
    });
}

/**
 *   chrome.cookies.onChanged
 */
function onChanged(changeInfo) {
    var cookie = changeInfo.cookie;
    if (cookie.name === COOKIE_NAME && isYSDomain(cookie.domain)) {
        dispatchOnChangedDelayed();
    }
}

var addOnChangedListenerDelayed = debounce(
    addOnChangedListener,
    INGONE_ONCHANGED_DELAY_MS
);

var dispatchOnChangedDelayed = debounce(function () {
    exports.onChanged.dispatch();
}, DISPATCH_ONCHANGED_DELAY_MS);



