/**
*     
*/
'use strict';

/**
 *    
 * @param  {Logger} logger
 * @param  {Array} updatedDomains
 * @param  {Array} notUpdatedDomains
 */
exports.logUpdateResult = function (logger, updatedDomains, notUpdatedDomains) {
    var updatedDomainsCount = updatedDomains.length;
    var notUpdatedDomainsCount = notUpdatedDomains.length;

    if (updatedDomainsCount) {
        logger.info(
            'Positions (%d) were updated for %s',
            updatedDomainsCount,
            updatedDomains.join(', ')
        );
    }

    if (notUpdatedDomainsCount) {
        logger.error('Positions (%d) updating were faild for %s',
        notUpdatedDomainsCount,
        notUpdatedDomains.join(', '));
    }

    if (!updatedDomainsCount && !notUpdatedDomainsCount) {
        logger.info('Positions were not updated');
    }
}



