'use strict';

var collection = require('./collection');

module.exports = function (oldIndex, newIndex) {
    var thumb = collection.getItem(oldIndex);
    if (thumb && oldIndex !== newIndex) {
        thumb.pinned = true;
        collection
            .removeItem(oldIndex)
            .insertItem(newIndex, thumb);
    }
};



