/**
 * Downloading files
 */
'use strict';

var fs = require('../platform/file-storage');
var xhr = require('../utils/xhr');
var logger = require('../utils/logger').create('file-downloader');
var formatMessage = require('../utils/format-message');

var MAX_DOWNLOAD_TIMEOUT_MS = 1000 * 30;

/**
 *   download  file-storage
 *   ,    
 */
exports.init = function () {
    fs.download = exports.download;
};

/**
 *    
 * @param {string} url -     
 * @param {string} fileName -     .
 * @param {function} callback -   
 *   {object} error
 */
exports.download = function (url, fileName, callback) {
    var options = {url: url, fileName: fileName, callback: callback};
    xhr({
        url: url,
        timeout: MAX_DOWNLOAD_TIMEOUT_MS,
        parse: 'blob',
    }, function (err, response) {
        // TODO: purify
        if (err) {
            onDownloadError(options, err);
        } else if (response.size > 0) {
            onDownloadSuccess(options, response);
        } else {
            onDownloadEmpty(options);
        }
    }, this);
};

function onDownloadSuccess(options, response) {
    // Convert blob to data url to send it
    // to the offscreen page afterwards.
    const reader = new FileReader();
    reader.onload = (event) => {
        onDownloadSuccessInternal(options, event.target.result);
    };
    reader.onerror = (err) => {
        options.callback(err);
    };
    reader.readAsDataURL(response);
}

function onDownloadSuccessInternal(options, response) {
    fs.createDirectory(options.fileName, function () {
        fs.write(options.fileName, response, options.callback);
    }, options.callback);
}

function onDownloadEmpty(options) {
    var message = formatMessage('Empty response for url: %s', options.url);
    logger.error(message);
    options.callback(new Error(message));
}

function onDownloadError(options, err) {
    var message = formatMessage(
        'Download of %s failed for url: %s, with error: %s',
        options.fileName,
        options.url,
        err.message
    );
    logger.error(message);
    options.callback(new Error(message));
}



