/**
 * XMLHttpRequest util proxy override
 */
'use strict';

var URI = require('urijs');
var isPlainObject = require('lodash/lang/isPlainObject');

var fetch = require('../fetch');

exports.override = (Request) => {
    if (typeof Request !== 'function') {
        return;
    }

    Request.prototype.send = function () {
        this.options = this._transformToFetchOptions(this.options);
        if (this.options && this.options.body) {
            this.options.method = 'POST';
        }
    
        const evt = {target: {status: 'pending', url: this.options.url}};
    
        const request = fetch(this.options.url, this.options).then((data) => {
            evt.target.status = 'fullfilled';
            this.callback(null, data, Object.assign(evt, {target: request}));
        }).catch((err) => {
            evt.target.status = 'rejected';
            this.callback(err, null, Object.assign(evt, {target: request}));
        });
    
        return request;
    };
    
    Request.prototype._transformToFetchOptions = function (options) {
        return Object.assign(options, {
            responseType: options.parse || 'text',
            headers: this._getFetchHeaders(options)
        });
    }
    
    Request.prototype._getFetchHeaders = function () {
        var options = this.options;
        var headers = options.headers || {};
        if (options.bypassCache) {
            headers['If-Modified-Since'] = 'Sat, 1 Jan 2005 00:00:00 GMT';
        }
    
        if (typeof options.body === 'string') {
            if (!headers['Content-Type']) {
                headers['Content-Type'] = 'text/plain; charset=UTF-8';
            }
        } else if (isPlainObject(options.body)) {
            options.body = URI.buildQuery(options.body);
            if (!headers['Content-Type']) {
                headers['Content-Type'] = 'application/x-www-form-urlencoded; charset=UTF-8';
            }
        }
    
        return headers;
    };
}



