/**
 *  :      blacklist,
 *     .
 */

'use strict';

var database = require('../database/database');
var logger = require('../../utils/logger').create('Blacklist/local');
var pluck = require('lodash/collection/pluck');

var TABLE_NAME = 'blacklist';

module.exports = {
    get: function (callback) {
        database.get(TABLE_NAME, {}, function (error, result) {
            callback(error, pluck(result, 'domain'));
        });
    },

    upsertOne: function (domain, callback) {
        logger.info('Adding: %s', domain);
        database.upsert(TABLE_NAME, {domain: domain}, function (err) {
            if (err) {
                logger.error('Can not upsert domain %s %s', domain, err.message);
            }
            if (callback) {
                callback(err);
            }
        });
    },

    deleteOne: function (domain, callback) {
        logger.info('Removing: %s', domain);
        database.delete(TABLE_NAME, domain, function (err) {
            if (err) {
                logger.error('Can not delete domain %s %s', domain, err.message);
            }
            if (callback) {
                callback(err);
            }
        });
    }
};



