/**
 * Registers all loaded frontends and checks loading time
 */
'use strict';

const buildData = require('../debug/build-data');
const logger = require('../../utils/logger').create('Frontend/init-checker');
const clicker = require('../statistics/clicker');

const FRONTEND_LOADING_TIMEOUT_MS = 3000;
const frontends = new Map();

/**
 * Register new loading frontend
 * @param {Number} tabId
 */
exports.started = function (tabId) {
    getAndClearExistingItem(tabId);
    const item = {
        tabId: tabId,
        startTime: performance.now(),
        timer: setTimeout(timeoutElapsed.bind(null, tabId), FRONTEND_LOADING_TIMEOUT_MS),
    };
    frontends.set(tabId, item);
};

/**
 * Register frontend loaded
 * @param {Number} tabId
 */
exports.loaded = function (tabId) {
    const item = getAndClearExistingItem(tabId);
    if (item && !buildData.git.release) {
        const loadingTime = performance.now() - item.startTime;
        logger.info('Frontend loading time in tab %i: %i ms', tabId, loadingTime);
    }
};

function timeoutElapsed(tabId) {
    const item = getAndClearExistingItem(tabId);
    if (item) {
        logger.error('Frontend loading in tab %i takes more than %i ms', tabId, FRONTEND_LOADING_TIMEOUT_MS);
        clicker.send({path: 'system.frontdisplayfail'});
    }
}

function getAndClearExistingItem(tabId) {
    const item = frontends.get(tabId);
    if (item) {
        clearTimeout(item.timer);
        frontends.delete(tabId);
    }
    return item;
}



