/**
 *        .
 *
 *   Firefox   :
 *
 * 1.     "moz-extension://{uuid}/layout/newtab.html"
 *      ,  
 *        
 *      .    
 *  .
 *
 * 2.     ,   
 *       .
 *
 * 3.      
 *      ( uuid
 *  "moz-extension://{uuid}/layout/newtab.html").
 */
'use strict';

const browserInfo = require('../../utils/browser-info');
const {visbookmarksUrl} = require('../../platform/about');
const visbookmarksOldUrl = 'chrome://yandex-vb/content/fastdial/layout/newtab.html';
const VISBOOKMARKS_URL_RE = /^moz\-extension:\/\/[^/]+\/layout\/newtab\.html$/;

/**
 * Init
 */
exports.init = function () {
    if (!browserInfo.isFirefox()) {
        return;
    }

    setTimeout(reloadTabsIfNeeded, 0);
};

function reloadTabsIfNeeded() {
    chrome.windows.getAll({populate: true}, windows => {
        for (let windowItem of windows) {
            for (let tab of windowItem.tabs) {
                reloadTabIfNeeded(tab);
            }
        }
    });
}

function reloadTabIfNeeded(tab) {
    if (isBrokenTab(tab)) {
        chrome.tabs.update(tab.id, {url: visbookmarksUrl});
    }
}

function isBrokenTab(tab) {
    const {url} = tab;

    if (!url) {
        return false;
    }

    if (url === visbookmarksOldUrl) {
        return true;
    }

    if (url === visbookmarksUrl) {
        //    . - ,     ,
        //    ,    ""  ,   .
        //         2.32.    
        //           `false`.
        if (!tab.discarded && tab.status === 'complete' && tab.active && !tab.favIconUrl) {
            return true;
        }
    } else if (VISBOOKMARKS_URL_RE.test(url)) {
        return true;
    }

    return false;
}



