/**
 * ,   'logFrontendData'      .
 */
'use strict';

var logger = require('../../utils/logger').create('Frontend Logger');
var messaging = require('./messaging');
var events = require('./events');

module.exports.init = function () {
    messaging.on(events.frontend.LOG_FRONTEND_DATA, function (req) {
        if (typeof logger[req.level] === 'function') {
            logger[req.level](req.msg);
        } else {
            logger.error('Unknown logger level %s', req.level);
        }
    });
};



