/**
 * ,    ( ,    )
 */

'use strict';

var asyncLib = require('async');
var alarms = require('../../utils/alarms');
var frontendMessaging = require('../frontend/messaging');
var frontendInitSender = require('../frontend/init-sender');
var cookiesYasoft = require('../cookies/yasoft');
var auth = require('../auth');
var uninstall = require('./uninstall');
var settings = require('../settings/settings');
var settingsPlatformBridge = require('../settings/platform-bridge');
var database = require('../database/database');
var pickup = require('../pickup/pickup');
var collection = require('../thumbs/collection');
var branding = require('../branding');
var backgrounds = require('../backgrounds');
var closedTabs = require('../closed-tabs');
var platformConfigure = require('../../platform/configure');
var cookiesYs = require('../cookies/ys');
var gpauto = require('../cookies/gpauto');
var remoteConfig = require('../remote-config');
var advertisement = require('../advertisement');
var backup = require('../backup');
var formatMessage = require('../../utils/format-message');
var error = require('../error/error');
var buildInfo = require('../../platform/build-info');
var platformAbout = require('../../platform/about');
var platformClid = require('../../platform/clid');
var browserInfo = require('../../utils/browser-info');
var i18n = require('../i18n');
var buildData = require('../debug/build-data');
var logLevels = require('../settings/log-levels');
var experiments = require('../experiments');
var runstate = require('./runstate');

var logger = require('../../utils/logger').create('Startup');

// ,    init()    settings / thumbs / branding
var normalModules = [
    logLevels,
    // experiments should be upper as some modules may use it inside init
    experiments,
    collection,
    pickup,
    require('../site-info'),
    require('../favicon'),
    backgrounds,
    cookiesYs,
    cookiesYasoft,
    require('../blacklist/blacklist'),
    require('../screenshots/screenshots'),
    require('../maintenance'),
    require('../debug/log'),
    remoteConfig,
    advertisement,
    require('../debug/log'),
    require('../statistics'),
    require('../frontend'),
    backup,
    closedTabs,
    gpauto,
    require('../guide'),
    require('../custom-logo'),
    require('../sync'),
    require('../branding/applier'),
    require('../informers'),
    require('../host-permissions'),
    require('../host-permissions/notification'),
];

//    debug  
window.debug = require('../debug/debug').getters;

/**
 *   :
 * -   
 * -  
 * -   
 */
exports.prepare = function () {
    //   
    logLevels.prepare();

    //    
    logHeader();

    //   window.onerror
    error.attach(window);
};

/**
 *   ,    :
 * - 
 * - 
 * -  
 * - 
 * @param {Function} callback
 */
exports.initCore = function (callback) {
    asyncLib.series([
        platformClid.init.bind(platformClid),
        database.open.bind(database),
        settings.load.bind(settings),
        settingsPlatformBridge.init.bind(settingsPlatformBridge),
        branding.init.bind(branding),
    ], function (err) {
        if (err) {
            logger.error('Error during init core modules: %s', err.message);
        }

        try {
            platformConfigure({showZen: false});
            platformConfigure({fileDownloader: true});
            platformConfigure({browserAction: true});
            i18n.init(branding.vars.defaultLocale);
        } catch (err) {
            logger.error('Error during platform configuration: %s', err.message);
        } finally {
            callback();
        }
    });
};

/**
 *    ,    core (settings / thumbs / branding)
 */
exports.initNormal = function () {
    normalModules.forEach(function (module) {
        if (module.init) {
            module.init();
        } else {
            throw new Error(formatMessage('No `init` method for module %j', module));
        }
    });

    platformConfigure({removeIfAnotherInstance: true});
    platformConfigure({softExport: 'init'});
    platformConfigure({barNavig: 'init'});
    platformConfigure({ybImport: 'init'});
};

/**
 *  ,   ,     .
 * @param {Function} callback
 */
exports.finalize = function (callback) {
    //  ,    
    alarms.stopCaching();

    // Send 'init' to all (possibly opened) frontends and start messaging
    frontendMessaging.run();
    frontendInitSender.sendAll();

    //       ,

    // 
    // todo: move to initNormalModules or rename
    auth.init();

    //    
    alarms.logAll();

    //  yasoft  
    cookiesYasoft.set();

    //    
    uninstall.setup();

    //  ready 
    runstate.setReady();

    callback();
};

function logHeader() {
    logger.info('======================== Initializing.. ========================');
    logger.info('Browser: %s %s', browserInfo.name, browserInfo.version);
    logger.info('Platform: %s %s-%s %s',
        platformAbout.id,
        buildInfo.getVersion(),
        buildInfo.getRevision(),
        !buildInfo.isDebug() ? 'PROD' : 'DEV'
    );
    logger.info('Common backend: %s-%s (%s) %s',
        buildData.version,
        buildData.git.buildNumber,
        buildData.git.sha,
        buildData.git.release ? 'PROD' : 'DEV'
    );
}



