'use strict';

var urlUtils = require('./url');
var xhr = require('./xhr');

/**
 * @param {Object} options
 * @param {String} options.url
 * @param {Boolean} [options.noCache]
 * @param {Function} callback
 */
module.exports = function (options, callback) {
    options = options || {};
    const src = options.noCache ? urlUtils.addRandomParam(options.url) : options.url;
    xhr({url: src}, (err, img) => {
        if (!err) {
            onLoad(img, callback);
        } else {
            onError({src}, callback)
        }
    });
};

/**
 * @param {String} img
 * @param {Function} callback
 */
function onLoad(img, callback) {
    // setTimeout is for IE9
    // @see https://st.yandex-team.ru/VB-4833#1443021905000
    setTimeout(function () {
        callback(null, img);
    }, 0);
}

/**
 * @param {String} img
 * @param {Function} callback
 */
function onError(img, callback) {
    callback(new Error('Image load error: ' + img.src), img);
}



