/**
 * Logic for listening and dispatching tab/window close events.
 */
'use strict';

var channel = require('../../utils/channel');
var urlUtils = require('../../utils/url');
var tabsWatcherEvents = require('../../utils/tabs-watcher-2').events;
var platformAbout = require('../../platform/about');
var closedItem = require('./closed-item');
var isRegExp = require('lodash/lang/isRegExp');

var closedTabsFilter = [].concat(platformAbout.vbUrls || []);

/**
 * Start listening.
 */
exports.start = function () {
    tabsWatcherEvents.onRemoved.addListener(handleTabClosedEvent);
    chrome.windows.onRemoved.addListener(handleWindowClosedEvent);
    addHistoryRemovedListener();
};

exports.onTabClosed = channel.create();

exports.onWindowClosed = channel.create();

exports.onWindowCreated = channel.create();

exports.onHistoryCleared = channel.create();

/**
 * @param {Number} tabId
 * @param {Object} tabData
 * @param {Object} removeInfo
 */
function handleTabClosedEvent(tabId, tab, removeInfo) {
    var tabData = tab.data;
    var url = tabData.url;
    if (!isWatchingUrl(url)) {
        return;
    }
    var closedTab = closedItem.createClosedTab(url, tabData, removeInfo);
    exports.onTabClosed.dispatch(closedTab);
}

/**
 * @param {Number} windowId
 */
function handleWindowClosedEvent(windowId) {
    exports.onWindowClosed.dispatch(windowId);
}

/**
 * Filters new-tab-page urls.
 * @see https://st.yandex-team.ru/VB-5416
 * @see https://st.yandex-team.ru/VB-5425
 * @param {String} url
 * @returns {Boolean}
 */
function isWatchingUrl(url) {
    return !closedTabsFilter.some(pattern => {
        if (isRegExp(pattern)) {
            return pattern.test(url);
        }

        return urlUtils.isEqualUrls(pattern, url);
    });
}

/**
 * Adds listener to chistory remove event.
 * Not all platforms supports this feature.
 */
function addHistoryRemovedListener() {
    if (chrome.history && chrome.history.onVisitRemoved) {
        chrome.history.onVisitRemoved.addListener(handleHistoryRemovedEvent);
    }
}

/**
 * @param {Object} [removedInfo]
 */
function handleHistoryRemovedEvent(removedInfo) {
    if (removedInfo && removedInfo.allHistory) {
        exports.onHistoryCleared.dispatch();
    }
}



