/**
 * @description
 *    .     
 *  :
 * 1)     
 * 2)      
 *
 *    @see DELAY_MS .    
 *          .
 *
 *      .    
 *   ,      .
 *
 * URL         www.
 */
'use strict';

const Channel = require('../../utils/channel');
const urlUtils = require('../../utils/url');
const {watcher: tabsWatcher} = require('../../utils/tabs-watcher-2');

const urlsToWatchMap = new Map();
const tabsTimeouts = new Map();
let collection = null;

const DELAY_MS = 2000;

exports.init = function () {
    collection = tabsWatcher.storage.query(query);

    collection.forEach(createTabUpdateTask);
    collection.onUpdated.add((collection, changes) => onCollectionUpdated(changes));
};

exports.addToWatchList = function (url) {
    urlsToWatchMap.set(url, normalizeUrl(url));
    collection.reload();
};

exports.clearWatchList = function () {
    urlsToWatchMap.clear();
    collection.reload();
};

exports.onUpdated = new Channel();

/**
 *        complete,   
 *   ,       
 *
 * @param {Tab} tab
 * @returns {Boolean}
 */
function query(tab) {
    if (!tab.data.active || !tab.isComplete || tab.data.incognito) {
        return false;
    }

    return tab.navigation.getNavigationStack()
        .some(matchSingleUrl);
}

/**
 *   
 *
 * @param {Array} [inserted]
 * @param {Array} [excluded]
 * @param {Array} [removed]
 * @param {Array} [updated]
 */
function onCollectionUpdated({inserted = [], excluded = [], removed = [], updated = []}) {
    excluded.concat(removed)
        .forEach(releaseTabUpdateTask);

    inserted.concat(updated)
        .forEach(createTabUpdateTask);
}

/**
 *      
 *
 * @param {Number} tabId
 */
function releaseTabUpdateTask({id: tabId}) {
    if (!tabsTimeouts.has(tabId)) {
        return;
    }

    clearTimeout(tabsTimeouts.get(tabId));
    tabsTimeouts.delete(tabId);
}

/**
 *         
 *
 * @param {Tab} tab
 */
function createTabUpdateTask(tab) {
    const tabId = tab.id;
    clearTimeout(tabsTimeouts.get(tabId));

    const timeoutId = setTimeout(() => sendEvent(tab), DELAY_MS);

    tabsTimeouts.set(tabId, timeoutId);
}

/**
 * @param {Tab} tab
 */
function sendEvent(tab) {
    const urls = tab.navigation.getNavigationStack();
    exports.onUpdated.dispatch(tab.id, getMatchedUrls(urls));
}

/**
 *     URL   URL   urlsToWatchMap.
 *
 * @param {String} testUrl
 * @returns {Boolean}
 */
function matchSingleUrl(testUrl) {
    testUrl = normalizeUrl(testUrl);

    return Array.from(urlsToWatchMap.values())
        .some(url => {
            return urlUtils.isEqualUrls(url, testUrl);
        });
}

/**
 *    testUrls  urlsToWatchMap.
 *
 * @param {Array.<String>} testUrls
 * @returns {Array.<String>}
 */
function getMatchedUrls(testUrls) {
    testUrls = testUrls.map(normalizeUrl);

    return Array.from(urlsToWatchMap)
        .filter(([, url]) => {
            return testUrls.some(testUrl => urlUtils.isEqualUrls(url, testUrl));
        })
        .map(([key]) => {
            return key;
        });
}

/**
 *    www  URL
 *
 * @param  {String} url
 * @returns {String}
 */
function normalizeUrl(url) {
    return urlUtils.getNormalUrl(url, {www: true, protocol: true});
}



