'use strict';

var database = require('../database/database');
var platformConfigure = require('../../platform/configure');
var logger = require('../../utils/logger').create('Startup/uninstall');

exports.setup = function () {
    logger.info('Setup..');
    platformConfigure({onUninstall: onUninstall});
    platformConfigure({softExport: 'setUninstallURL'});
};

function onUninstall() {
    database.deleteDatabase();
}



