/**
 *      .     vtbNum
 */

'use strict';

var navigations = require('../navigations');
var settings = require('../settings/settings');
var tabsWatcher = require('../../utils/tabs-watcher-2');
var urlUtils = require('../../utils/url');

//            .
var CLEAR_DATA_DELAY_MS = 1000;

var thumbClicks = {};

/**
 * Init
 */
exports.init = function () {
    navigations.onThumbNavigated.addListener(onThumbNavigated);
    tabsWatcher.events.onCompleted.addListener(onTabLoaded);
};

/**
 * Returns thumb position if navigation url were made by thumb click
 * @param {Number} tabId
 * @param {String} url
 */
exports.getPosition = function (tabId, url) {
    var thumbClick = thumbClicks[tabId];
    if (thumbClick && urlUtils.isEqualUrls(thumbClick.url, url)) {
        var position = thumbClick.position;
        clearTimeout(thumbClick.timeout);
        clear(tabId);
        return position;
    } else {
        return null;
    }
};

function onThumbNavigated(data) {
    if (settings.get('sendUsageStat')) {
        thumbClicks[data.tabId] = {
            position: data.thumbPosition,
            url: data.url,
        };
    }
}

function onTabLoaded(tabId) {
    if (thumbClicks[tabId]) {
        thumbClicks[tabId].timeout = setTimeout(clear.bind(null, tabId), CLEAR_DATA_DELAY_MS);
    }
}

function clear(tabId) {
    delete thumbClicks[tabId];
}



