/**
 *  yasoft.version   YS
 *      , ..  ys - 
 */

'use strict';

var ys = require('./ys');
var buildInfo = require('../../platform/build-info');
var about = require('../../platform/about');
var logger = require('../../utils/logger').create('Cookies/yasoft');

exports.init = function () {
    ys.onChanged.addListener(onChanged);
};

exports.set = function () {
    var data = getData();
    logger.info('Setting yasoft = %s, version = %s', data.yasoft, data.version);
    ys.upsert(data.yasoft, data.version);
};

function onChanged() {
    var data = getData();
    logger.info('YS changed, trying to re-set yasoft = %s, version = %s', data.yasoft, data.version);
    ys.upsert(data.yasoft, data.version);
}

function getData() {
    return {
        version: buildInfo.getVersion().replace(/\./g, '-'),
        yasoft: about.yasoft
    };
}



