/**
 * Monitors tabs change events in browser
 * and emits events with full list of redirect urls and real-referer of tab
 */

'use strict';

var values = require('lodash/object/values');
var utils = require('./utils');
var channel = require('./channel');
var tabsModule = require('./tabs');
var logger = require('./logger').create('TabsWatcher');

var platformAbout;

// waiting time for js redirects
var JS_REDIRECTS_WAIT_MS = 1500;
// when tab is incative js execution can take much longer time
// (important for chrome)
var JS_REDIRECTS_WAIT_INACTIVE_MS = 3000;

//   
var tabs = {};

//     
var waitForCompleteHandlers = new Map();

var watcher = module.exports = {
    COMPLETE_STATUS: 'complete',
    LOADING_STATUS: 'loading',

    initialized: false,

    /**
     * Adds listeners to tabs events, creates channels
     * @param {Object} aboutModule
     * @param {String} aboutModule.visbookmarksUrl
     */
    init: function (aboutModule) {
        if (!this.initialized) {
            platformAbout = aboutModule;
            addListeners();
            // init on start: iterate all browser windows and collect tabs
            collectExistingTabsData();
            this.initialized = true;
        }
    },

    stop: function () {
        removeListeners();
    },

    log: utils.noop,

    getTabInfo: function (tabId) {
        return tabId ? tabs[tabId] : tabs;
    },

    /**
     * @param {Number} tabId
     * @param {Function} callback
     * @param {*} [context]
     */
    waitForComplete: function (tabId, callback, context) {
        var tabInfo = this.getTabInfo(tabId);
        if (tabInfo && tabInfo.status === 'complete') {
            callback.apply(context);
        } else {
            if (!waitForCompleteHandlers.has(tabId)) {
                waitForCompleteHandlers.set(tabId, []);
            }
            waitForCompleteHandlers.get(tabId).push({callback, context});
        }
    },

    /**
     * Triggers after all redirects
     * Passed params: tabId, tabData
     */
    onComplete: channel.create(),

    /**
     * Trigerrs after tab has been closed
     * Passed params: tabId, tabData, removeInfo
     * Isn't emitted for loading tabs
     */
    onClosed: channel.create(),

    /**
     * Triggers after tab with completed status has been activated
     * Passed params: tabId, tabData, activeInfo
     * Isn't emitted for loading tabs
     */
    onActivated: channel.create(),

    /**
     * tabs.onUpdate event
     * Passed params: tabId, tabData, changeInfo
     */
    onUpdate: channel.create(),

    /**
     * webNavigation.onBeforeNavigate event
     * Passed params: details
     */
    onBeforeNavigate: channel.create()
};

function initEmptyTabObject() {
    return {
        urls: [],
        possibleReferrers: [],
        uuid: utils.misc.guid(),
        navigationId: generateNavigationId()
    };
}

function generateNavigationId() {
    return Date.now() + Math.random();
}

function collectExistingTabsData() {
    tabsModule.getOpen(function (error, tabList) {
        if (error) {
            logger.error('Can not collect existing tabs data: %j', error);
        } else {
            fillTabMapFromExistingTabs(tabList);
        }
    });
}

function fillTabMapFromExistingTabs(tabList) {
    tabList.forEach(function (tab) {
        if (!tab.incognito) {
            var mytab = tabs[tab.id] = initEmptyTabObject();
            mytab.urls = [tab.url];
            mytab.status = watcher.COMPLETE_STATUS;
            mytab.windowId = tab.windowId;
            mytab.title = tab.title;
            mytab.favIconUrl = tab.favIconUrl;
        }
    });
}

function isBadReferrer(referrer) {
    return referrer === platformAbout.visbookmarksUrl;
}

// TODO real-referrer:
// All code for real-referrer is chrome related code,
// it works only with flat/referrer-inclode content-script.
// We can emulate this messages in ff and ie,
// or remove real-refferer code from this module
// function onReferrerMessage(message, sender/*, sendResponse*/) {
//    if (message.topic === 'real-referrer' && message.url && sender.tab) {
//        var tabId = sender.tab.id;
//        tabs[tabId] = tabs[tabId] || initEmptyTabObject();
//
//        // Add url to possibleReferrers stack.
//        // Later we can match top url from this stack
//        // with top url from tab[id].urls stack.
//        tabs[tabId].possibleReferrers.push(message.url);
//    }
// }

function onBeforeNavigation(details) {
    if (details.frameId === 0) {
        handleLoadingState(details.tabId, {url: details.url});
    }
    watcher.onBeforeNavigate.dispatch(details);
}

function onUpdated(tabId, updateInfo, tabData) {
    if (tabData.incognito) {
        return;
    }

    if (updateInfo.status === watcher.LOADING_STATUS) {
        handleLoadingState(tabId, tabData);
    } else if (updateInfo.status === watcher.COMPLETE_STATUS) {
        handleCompletedState(tabId, tabData);
    }

    var tab = tabs[tabId];

    if (tab) {
        tab.title = tabData.title;
        tab.favIconUrl = tabData.favIconUrl;
    }

    watcher.onUpdate.dispatch(tabId, tab, updateInfo);
}

function handleLoadingState(tabId, tabData) {
    tabs[tabId] = tabs[tabId] || initEmptyTabObject();
    var url = tabData.url;
    if (!tabs[tabId].status) {
        // new navigation in new tab
        tabs[tabId].urls = [url];
        tabs[tabId].navigationId = generateNavigationId();
        calcRealRefForNewTab(tabId, tabData);
    } else if (tabs[tabId].status === watcher.COMPLETE_STATUS) {
        // new navigation in existing tab
        calcRealRefForExistingTab(tabId, tabData);
        tabs[tabId].urls = [url];
        tabs[tabId].navigationId = generateNavigationId();
        tabs[tabId].possibleReferrers = [];
        tabs[tabId].navInExistingTab = true;
    } else if (tabs[tabId].urls.indexOf(url) === -1) {
        // redirect: add url to urls array
        tabs[tabId].urls.push(url);
    }

    tabs[tabId].status = watcher.LOADING_STATUS;

    // clear timeout if it is runnig (js redirect after complete)
    clearTimeout(tabs[tabId].timeout);
    delete tabs[tabId].timeout;
}

function calcRealRefForNewTab(tabId, tabData) {
    var opener = tabs[tabData.openerTabId];
    if (opener) {
        var stackRef = opener.urls[opener.urls.length - 1];
        // ,     possibleReferrers )
        var possibleRef = opener.possibleReferrers[opener.possibleReferrers.length - 1];
        if (stackRef === possibleRef) {
            // Delete last (already used) referrer from stack
            // to avoid bad real-referrers for navigates from opener's tab.
            // @see CHROME-1532
            // Do not delete all stack beacause other navigations might use it
            opener.possibleReferrers.pop();
            tabs[tabId].realRef = isBadReferrer(stackRef) ? null : stackRef;
        } else {
            tabs[tabId].realRef = null;
        }
    }
}

function calcRealRefForExistingTab(tabId) {
    var stackRef = tabs[tabId].urls.pop();
    var possibleRef = tabs[tabId].possibleReferrers.pop();

    if (stackRef === possibleRef && !isBadReferrer(stackRef)) {
        tabs[tabId].realRef = stackRef;
    } else {
        tabs[tabId].realRef = null;
    }
}

function handleCompletedState(tabId, tabData) {
    tabs[tabId] = tabs[tabId] || initEmptyTabObject();

    if (tabs[tabId].urls.indexOf(tabData.url) === -1) {
        tabs[tabId].urls.push(tabData.url);
    }

    // store some data about tab
    if (isNewWindow(tabData.windowId)) {
        tabs[tabId].navInNewWindow = true;
    } else {
        delete tabs[tabId].navInNewWindow;
    }
    tabs[tabId].windowId = tabData.windowId;

    // keep status `loading` during js redirects time
    tabs[tabId].status = watcher.LOADING_STATUS;

    clearTimeout(tabs[tabId].timeout);
    tabs[tabId].timeout = setTimeout(
        afterRedirectTimeout.bind(null, tabId),
        tabData.active ? JS_REDIRECTS_WAIT_MS : JS_REDIRECTS_WAIT_INACTIVE_MS
    );
}

function afterRedirectTimeout(tabId) {
    // tab may be already closed
    if (tabs[tabId]) {
        delete tabs[tabId].timeout;
        tabs[tabId].status = watcher.COMPLETE_STATUS;
        // TODO check that we really need to clear realReferrers here
        tabs[tabId].possibleReferrers = [];
        watcher.onComplete.dispatch(tabId, tabs[tabId]);
        triggerWaitForCompleteHandlers(tabId);
    }
}

function onRemoved(tabId, removeInfo) {
    if (!tabs[tabId]) {
        return;
    }

    watcher.onClosed.dispatch(tabId, tabs[tabId], removeInfo);

    delete tabs[tabId];
}

function onActivated(activeInfo) {
    var tabId = activeInfo.tabId;
    if (!tabs[tabId]) {
        return;
    }
    if (tabs[tabId].status === watcher.COMPLETE_STATUS) {
        watcher.onActivated.dispatch(tabId, tabs[tabId], activeInfo);
    }
}

function onReplaced(addedTabId, removedTabId) {
    tabs[addedTabId] = tabs[removedTabId];
    delete tabs[removedTabId];
}

/**
 * Adds or removes listeners to needed channels
 * @param {String} actionName - "addListener" | "removeListener"
 */
function performActionForChannels(actionName) {
    chrome.tabs.onUpdated[actionName](onUpdated);
    chrome.tabs.onRemoved[actionName](onRemoved);
    chrome.tabs.onReplaced[actionName](onReplaced);
    chrome.tabs.onActivated[actionName](onActivated);
    // We use webNavigation for Chrome.
    // Because chrome.tabs api can't give use enough information
    // for catching redirects, especially http -> https
    // In IE webNavigation isn't exist.
    if (chrome.webNavigation && chrome.webNavigation.onBeforeNavigate) {
        chrome.webNavigation.onBeforeNavigate[actionName](onBeforeNavigation);
    }
    // todo: move to platform
    // chrome.runtime.onMessage[actionName](onReferrerMessage);
}

/**
 * Checks if there is a tab with particular windowId
 * @param {Number} windowId
 * @returns {Boolean}
 */
function isNewWindow(windowId) {
    return values(tabs).every(tab => tab.windowId !== windowId);
}

/**
 * @param {Number} tabId
 */
function triggerWaitForCompleteHandlers(tabId) {
    var handlers = waitForCompleteHandlers.get(tabId);
    if (Array.isArray(handlers)) {
        handlers.forEach(handler => handler.callback.apply(handler.context));
    }
    waitForCompleteHandlers.delete(tabId);
}

var addListeners = performActionForChannels.bind(null, 'addListener');
var removeListeners = performActionForChannels.bind(null, 'removeListener');



