/**
 *    (-)      
 */

'use strict';

var brandedSittes = require('./branded-sites');
var collection = require('../thumbs/collection');
var Thumb = require('../thumbs/thumb');
var logger = require('../../utils/logger').create('Pickup/force-thumbs');

module.exports = {
    /**
     *       (   )
     * @param {Object} options
     * @param {Boolean} options.onlyFirst     (  ),
     *    
     */
    apply: function (options) {
        options = options || {};
        collection.onChanged.mute();
        var pinned = getBrandedPinned();
        if (options.onlyFirst && pinned.length) {
            addForceThumb(pinned[0]);
        } else {
            pinned.forEach(addForceThumb);
        }
        collection.onChanged.unmute();
    }
};

/**
 *     (-)   
 */
function getBrandedPinned() {
    return brandedSittes.get().filter(function (item) {
        return item.pinned;
    });
}

function addForceThumb(item) {
    var thumb = Thumb.fromJSON({
        url: item.url,
        visits: item.visits,
        pinned: true
    });
    collection.setItem(item.index, thumb);
    logger.info('Add force-thumb %s on position %i', thumb.url, item.index);
}



