/**
 * Yandex authorization
 * Implements login, logout and change user method (with multi-authorization support)
 * Links:
 * https://doc.yandex-team.ru/Passport/passport-modes/reference/auth.xml
 * https://doc.yandex-team.ru/Passport/passport-modes/reference/logout.xml
 * https://beta.wiki.yandex-team.ru/passport/frontend/embeddedauth-multiauth/
 */
'use strict';

var yauthCookies = require('./yauth-cookies');
var branding = require('../branding');
var xhr = require('../../utils/xhr');
var urlUtils = require('../../utils/url');

var CHANGE_DEFAULT = 'change_default';
var LOGOUT = 'logout';

module.exports = {
    _sendPostRequest: null,

    cookies: yauthCookies,

    /**
     * Open auth page in tab
     */
    login: function (tabId, user) {
        chrome.tabs.update(tabId, {
            url: URL_FACTORY.login(user)
        });
    },

    /**
     * Logouts particular uid
     */
    getLogoutData: function (uid, callback) {
        getRequestData(LOGOUT, uid, callback);
    },

    /**
     * Sets active uid
     */
    getSwitchUserData: function (uid, callback) {
        getRequestData(CHANGE_DEFAULT, uid, callback);
    },

    /**
     * Load currently authorized accounts
     * @param {Function} callback
     */
    loadAccounts: function (callback) {
        authLoadAccounts(callback);
    }
};

var URL_FACTORY = {
    start: function () {
        return createUrl(
            branding.auth.passportApi,
            '/passport',
            {
                mode: 'embeddedauth'
            }
        );
    },
    login: function (user) {
        return createUrl(
            branding.auth.passportApi,
            '/auth',
            {
                retpath: branding.auth.redirect,
                login: user ? user.login : undefined,
            }
        );
    },
    accounts: function (yandexuid) {
        return createUrl(
            branding.auth.passApi,
            '/accounts',
            {
                yu: yandexuid
            }
        );
    },
};

function createUrl(base, path, search) {
    var uri = urlUtils.createURI(base);
    return uri ? uri.resource(path).setSearch(search).toString() : '';
}

function getRequestData(action, uid, callback) {
    yauthCookies.getYandexuidCookie(function (yandexuid) {
        callback(URL_FACTORY.start(), {
            uid: uid,
            yu: yandexuid,
            action: action,
            retpath: branding.auth.redirect,
        });
    });
}

function authLoadAccounts(callback) {
    yauthCookies.getYandexuidCookie(function (yandexuid) {
        if (!yandexuid) {
            callback(new Error('No yandexuid cookie'));
        } else {
            xhr({
                withCredentials: true,
                url: URL_FACTORY.accounts(yandexuid),
                parse: 'json',
            }, callback);
        }
    });
}



