'use strict';

const debounce = require('lodash/function/debounce');
const parse = require('user-agent-parser');
const i18n = require('../i18n');
const browser = require('../../utils/browser-info');
const backgrounds = require('../frontend/backgrounds');
const auth = require('../frontend/auth');
const cookies = require('../auth/yauth-cookies');
const alarms = require('../../utils/alarms');
const logger = require('../../utils/logger').create('zen');
const settings = require('../settings/settings');

const LOADER_URL = 'layout/zen/loader.js';
const RELOAD_TIMEOUT = 60; // in minutes
const DESTROY_TIMEOUT = 5000; // in ms
const RELOAD_ALARM_NAME = 'zen.reload';
const COOKIE_CHANGE_DEBOUNCE_TIME = 500; // @see https://st.yandex-team.ru/VB-6031
const ua = parse(navigator.userAgent);

let loaderScriptTag = null;
const cookieWatcher = debounce(reloadZen, COOKIE_CHANGE_DEBOUNCE_TIME);

exports.init = function () {
    settings.onChanged.addListener(({key, newValue}) => {
        if (key !== 'showZen') {
            return;
        }
        if (newValue) {
            enable();
        } else {
            disable();
        }
    });
    if (settings.get('showZen')) {
        enable();
    }
};

exports.getConfig = function () {
    return {
        clid: 450,
        lang: i18n.locale || 'ru',
        isTeethEnabled: true,
        teethType: 'white-image',
        theme: backgrounds.getCurrent().color === '000000' ? 'white' : 'black',
        notifiersPosition: 'top',
        staticHost: '/layout/zen',
        libType: 'vb',
        version: '',
        uatraits: {
            isMobile: false,
            isBrowser: true,
            BrowserEngine: ua.engine.name,
            BrowserEngineVersion: ua.engine.version,
            BrowserBase: browser.isChrome() ? 'Chromium' : undefined,
            BrowserBaseVersion: browser.isChrome() ? ua.browser.version : undefined,
            BrowserName: ua.browser.name,
            BrowserVersion: ua.browser.version,
            OSFamily: ua.os.name,
            OSVersion: ua.os.version
        },
        useLegacy: false,
        exps: [],
        isVideoDisabled: true,
        initialDisplayedRowsNumber: 3,
        cache: {
            api: {
                userId: getUserId(),
                storeId: 'yandex-vb'
            }
        },
        userInfo: {
            login: auth.getYandexLogin() || undefined,
            yandexuid: auth.getYandexUid() || undefined,
        }
    };
};

function disable() {
    logger.info('Disabling zen');
    alarms.clear(RELOAD_ALARM_NAME);
    destroyZenLib();
    cookies.onChanged.removeListener(cookieWatcher);
}

function enable() {
    logger.info('Enabling zen');
    Promise.all(cookies.cookiesResolves).then(() => {
        alarms.clear(RELOAD_ALARM_NAME);
        reloadZen();
        cookies.onChanged.addListener(cookieWatcher);
    });
}

function reloadZen() {
    logger.info('Reloading zen lib');
    destroyZenLib();
    addloaderScriptTag(createLib);
}

function destroyZenLib() {
    deleteLoaderScriptTag();
    deleteLibResources();
    document.querySelector('html').removeAttribute('class'); // https://st.yandex-team.ru/VB-6183#1521198947000
    delete window.YandexZen;
}

function deleteLoaderScriptTag() {
    if (loaderScriptTag) {
        document.body.removeChild(loaderScriptTag);
        loaderScriptTag = null;
    }
}

function deleteLibResources() {
    [...document.querySelectorAll('link, script')]
        .filter(tag => {
            return tag && (tag.href || tag.src || '').indexOf('vb-lib/vb-lib') >= 0
        })
        .forEach(tag => tag.parentNode.removeChild(tag));
}

function addloaderScriptTag(onLoadCallback) {
    loaderScriptTag = document.createElement('script');
    loaderScriptTag.src = LOADER_URL;
    loaderScriptTag.onload = onLoadCallback;
    document.body.appendChild(loaderScriptTag);
}

function createLib() {
    const config = exports.getConfig();
    YandexZen.loader.create(config)
        .then(scheduleReload)
        .catch(scheduleReload);
}

function scheduleReload() {
    alarms.clear(RELOAD_ALARM_NAME);
    alarms.register({
        name: RELOAD_ALARM_NAME,
        delayInMinutes: RELOAD_TIMEOUT
    }, reloadZen);
    setTimeout(destroyZenLib, DESTROY_TIMEOUT);
}

function getUserId() {
    const yandexUid = auth.getYandexUid();
    const yandexLogin = auth.getYandexLogin();
    if (yandexUid || yandexLogin) {
        return [yandexUid, yandexLogin].filter(Boolean).join('_');
    }
    return 'zen-user';
}



