/**
 *       .
 * {@link https://wiki.yandex-team.ru/stardust/tech/winsearch/voicestroka/Obrabotka-otveta-SpeechKit-a/}
 */
'use strict';

const Consumer = require('./consumer');
const SerpNavigator = require('./serp-navigator');
const vbFetch = require('../../../utils/fetch');
const navigations = require('../../navigations');
const intersection = require('lodash/array/intersection');
const PromisedTimer = require('../../../utils/promised-timer');

class SiteNavigator extends Consumer {
    /**
     * @constructor
     * @param {number} tabId
     * @param {number} delay   
     * @param {string} target     .
     *      .
     */
    constructor (tabId, delay = 0, target = navigations.TARGET.TAB) {
        super();
        this.delay = delay;
        this.target = target;
        this.tabId = tabId;
        this.status = STATUS.READY;
    }

    /**
     *        
     *
     * @param text
     * @returns {Promise.<boolean>}
     */
    check (text) {
        const url = URL_TO_HANDLER + encodeURIComponent(text);
        this.status = STATUS.CHECKING;

        return vbFetch(url, {
            responseType: 'json'
        })
            .then(result => this._processResult(result))
            .catch(() => false);
    }

    /**
     * @returns {Promise}
     */
    run () {
        this.status = STATUS.NAVIGATING;

        //   checkResult   text,     
        if (this.checkResult.hasOwnProperty('text')) {
            this.serpNavigator = new SerpNavigator(this.tabId, this.delay, this.target);
            return this.serpNavigator.run(this.checkResult);
        }

        //      this.checkResult.url
        this.timeout = new PromisedTimer(this.delay);

        //        .
        return this.timeout
            .then(() => this._goToSite(this.checkResult.url))
            .catch(() => Promise.resolve());
    }

    /**
     *    .
     *
     * @param result
     * @returns {{text:string, url:string}}
     * @private
     */
    _processResult (result) {
        //     
        if (this.status === STATUS.STOPPED) {
            return false;
        }

        for (let {tags, url, weight, text} of result.candidates) {
            //     .
            if (weight === 0) {
                return false;
            }

            //         
            //     .
            if (intersection(tags, BAD_TAGS).length) {
                continue;
            }

            if (!url) {
                //    url,      "   ".
                //             .
                if (text && tags.indexOf(SEARCH_TAG) !== -1) {
                    this.checkResult = {text};
                    return true;
                }

                continue;
            }

            this.checkResult = {url};
            return true;
        }
        return false;
    }

    /**
     *    
     */
    stop () {
        if (this.timeout) {
            this.timeout.stop();
        } else if (this.serpNavigator) {
            this.serpNavigator.stop();
        }

        this.status = STATUS.STOPPED;
    }

    /**
     *  
     *
     * @param {string} url
     * @private
     */
    _goToSite (url) {
        navigations.go({
            url,
            target: this.target,
            tabId: this.tabId,
        });

        return true;
    }
}

const URL_TO_HANDLER = 'https://yastroka.yandex.net/speech?user_input=';

const BAD_TAGS = [
    'action',
    'file',
    'folder',
    'local',
    'off',
    'soft',
    'local search',
];

const SEARCH_TAG = 'search';

const STATUS = {
    //    
    READY: 0,
    //   
    CHECKING: 1,
    //   
    NAVIGATING: 2,
    //   
    STOPPED: 3,
};

module.exports = SiteNavigator;



