/**
 *     ,  ,  
 */
'use strict';

var branding = require('../branding');
var curly = require('../../utils/curly');
var xhr = require('../../utils/xhr');
var urlUtils = require('../../utils/url');
var weights = require('./weights');
var isRelease = require('../debug/build-data').git.release;

var OUTPUT_COUNT = 10;

/**
 *         
 * (   ,     )
 */
module.exports = function (query, callback) {
    xhr({
        url: getUrl(query),
        logError: true,
        parse: 'json',
    }, function (err, json) {
        if (err) {
            callback(err);
        } else {
            var items = parseResponse(json).map(convertToItem);
            items = weights.calc(query, items, OUTPUT_COUNT, false);
            callback(null, items);
            printInDebug(query, items);
        }
    });
};

function getUrl(query) {
    return curly.insertUrlVars(branding.thumbs.suggestUrl, {
        searchTerms: query
    });
}

function parseResponse(arr) {
    var urls = arr[1];
    var titles = arr[2];
    return urls.map(function (url, i) {
        return {
            url: urlUtils.getNormalUrl(url),
            title: titles[i],
        };
    });
}

function convertToItem(webItem, i) {
    return weights.getItem(
        webItem.url,
        webItem.title,
        Math.max(0.9 - i / 10, 0.1).toFixed(1)
    );
}

function printInDebug(query, items) {
    if (!isRelease) {
        weights.printItems('Search web: ' + query, items);
    }
}



