/**
 *     html 
 */
'use strict';

var escape = require('lodash/string/escape');
var platformFs = require('../../platform/file-storage');

exports.openAsFile = function (title, text, tabId) {
    if (typeof text !== 'string') {
        try {
            text = JSON.stringify(text, false, 2);
        } catch (e) {}
    }
    var fileName = 'temp.html';
    var html = decorate(title, text);
    var data = {data: html, type: 'text/html', append: false};
    platformFs.write(fileName, data, function () {
        platformFs.getFileUrl(fileName, function (err, url) {
            if (tabId) {
                chrome.tabs.update(tabId, {url: url});
            } else {
                chrome.tabs.create({url: url});
            }
        });
    });
};

function decorate(title, text) {
    return '<head><title>' + title + '</title><meta charset="utf-8"></head></head><body>' +
        '<h2>' + title + '</h2>' +
            // '<div><a href="" onclick="this.href=window.location.href" download="vb.html"' +
            // 'style="font-weight: bold">Save as</a></div>' +
        '<pre style="word-wrap: break-word; white-space: pre-wrap;">' + escape(text) + '</pre>' +
        '</body>';
}



