'use strict';

var messaging = require('./messaging');
var clicker = require('../statistics/clicker');
var events = require('./events');
var tabsWatcher = require('../../utils/tabs-watcher');

exports.init = function () {
    messaging.on(events.frontend.SEND_FRONTEND_STAT, function (req, {tab}) {
        tabsWatcher.waitForComplete(tab.id, function () {
            clicker.send(req.options);
        });
    });
};



