/**
 *    
 *
 *       debug,        debug.getters,
 *          .
 *
 * :
 *
 * debug.show.log();
 *    :
 * debug.show.log
 *
 */
'use strict';

var settings = require('../settings/settings');
var files = require('./files');
var log = require('./log');
var advertisement = require('./advertisement');
var _ = require('../../utils/lodash');
var dateUtils = require('../../utils/date');
var buildData = require('./build-data');
var backup = require('../backup');
var platformAbout = require('../../platform/about');
var tabOpener = require('./opener');
var state = require('./state');
var database = require('../database/database');
var platformBranding = require('../../platform/branding');
var branding = require('../branding');
var remoteBrandingStorage = require('../branding/remote/storage');
var experiments = require('../experiments');
var backgroundSelector = require('../backgrounds/selector');

var help = [
    ['npm.*', '  npm- ,  debug.npm.URI'],
    ['modules.*', '  ,  debug.modules.settings.settings.get(...)'],
    ['utils.*', '  '],
    ['platform.*', '  '],
];

var debug = module.exports = {};
var getters = debug.getters = _.extend({}, require('./file-structure'));

addCommand('help', '',
    function () {
        // eslint-disable-next-line no-console
        console.log('----- DEBUG COMMANDS -----');
        help.sort(function (a, b) {
            return a[0] > b[0] ? 1 : -1;
        });
        var maxLength = 0;
        help.forEach(function (h) {
            maxLength = h[0].length > maxLength ? h[0].length : maxLength;
        });
        maxLength = maxLength + 4;
        help.forEach(function (h) {
            // eslint-disable-next-line no-console
            console.log('debug.' + h[0], _.repeat(' ', maxLength - h[0].length), '- ' + h[1]);
        });
    }
);

addCommand('setInstallDate', '  ,  setInstallDate(, , [])',
    function (d, m, y) {
        if (!d || d > 31) {
            return 'Invalid day, example debug.setInstallDate(15, 5, 1984)';
        }
        if (!m || m > 12) {
            return 'Invalid month, example debug.setInstallDate(15, 5, 1984)';
        } else {
            m -= 1;
        }
        y = y || new Date().getFullYear();
        var date = new Date(y, m, d);
        settings.set('appInstallTime', Math.round(date.getTime() / 1000));
        return 'New install date: ' + dateUtils.format(date, '%D.%M.%Y');
    }
);

addCommand('reset', ' ,     ',
    function () {
        return Promise.all([
            backup.clear(),
            database.deleteDatabase(),
            files.removeAll(),
        ]).then(resetSettings);
    }
);

addCommand('reload', '  ',
    function () {
        //   location.reload(), ..     
        window.location.href = window.location.href + '?' + Math.random();
    }
);

addCommand('enableLog', '   ',
    function () {
        // todo
    }
);

addCommand('log', '    ',
    function () {
        debug.show.log();
    }
);

addCommand('adv.showBlock', '   (, adv.showBlock("newbackground"))',
    function (blockId) {
        advertisement.show(blockId);
    }
);

addCommand('show.log', '    ',
    function () {
        log.openInTab();
    }
);

addCommand('show.thumbs', '     ',
    function () {
        tabOpener.openAsFile('Thumbs', state.getThumbs());
    }
);

addCommand('show.settings', '    ',
    function () {
        tabOpener.openAsFile('Settings', state.getSettings());
    }
);

addCommand('show.pickupcache', '     ',
    function () {
        state.getPickupcache(function (err, items) {
            tabOpener.openAsFile('Pickup cache', items);
        });
    }
);

addCommand('show.blacklistUser', '     ',
    function () {
        state.getBlacklistUser(function (err, items) {
            tabOpener.openAsFile('Blacklist (user)', items);
        });
    }
);

addCommand('show.blacklistServer', '     ',
    function () {
        tabOpener.openAsFile('Blacklist (server)', state.getBlacklistServer());
    }
);

addCommand('show.bg', '     ',
    function () {
        tabOpener.openAsFile('Backgrounds', state.getBg());
    }
);

addCommand('show.adv', '     ',
    function () {
        tabOpener.openAsFile('Advertisement', advertisement.getState());
    }
);

addCommand('show.branding', '     ',
    function () {
        var regionalData = platformBranding.getData();
        var data = Object.keys(regionalData).reduce(function (res, key) {
            res[key] = branding.getBlock(key);
            return res;
        }, {});
        tabOpener.openAsFile('Branding', data);
    }
);

addCommand('show.custombranding', '      ',
    function () {
        tabOpener.openAsFile('Custom branding', remoteBrandingStorage.getData());
    }
);

addCommand('show.files', '      ',
    function () {
        if (platformAbout.isChr) {
            files.openChromeFS();
            return;
        }

        if (platformAbout.isFx) {
            files.openFirefoxFS();
            return;
        }

        files.getFileList(function (err, items) {
            tabOpener.openAsFile('Files', items);
        });
    }
);

addCommand('show.buildInfo', '      ',
    function () {
        tabOpener.openAsFile('Build info', buildData);
    }
);

addCommand('show.experiments', '   ',
    function () {
        tabOpener.openAsFile('Experiments', {
            selected: experiments.getExperimentsList(),
            possible: require('../../experiment-configs'),
        });
    }
);

addCommand('clear.db', '  ',
    function () {
        database.deleteDatabase();
    }
);

addCommand('clear.backup', ' ',
    function () {
        backup.clear();
    }
);

addCommand('clear.settings', ' ',
    function () {
        return resetSettings();
    }
);

/**
 *
 * @param {String} cmd
 * @param {String} desc
 * @param {Function} fn
 */
function addCommand(cmd, desc, fn) {
    var parts = cmd.split('.');
    var obj;
    var objGetters;
    var cmdName;
    if (parts.length > 1) {
        obj = debug[parts[0]] = debug[parts[0]] || {};
        objGetters = getters[parts[0]] = getters[parts[0]] || {};
        cmdName = parts[1];
    } else {
        obj = debug;
        objGetters = getters;
        cmdName = parts[0];
    }
    obj[cmdName] = fn;
    /*
     * !
     *        ,    .
     *       !
     */
    Object.defineProperty(objGetters, cmdName, {get: fn});
    help.push([cmd, desc]);
}

/**
 *       .
 * ,  .
 * @returns {Promise}
 */
function resetSettings() {
    settings.resetDefaults();
    return new Promise (function (resolve) {
        backgroundSelector.selectDefaultBackground(resolve);
    });
}



