/**
 * ,      
 */

'use strict';

var asyncLib = require('async');
var storage = require('./storage');
var brandingBackgrounds = require('./branding-backgrounds');
var cloudBackgrounds = require('./cloud-backgrounds');
var userBackground = require('./user-background');
var bgSettings = require('../settings/backgrounds');
var logger = require('../../utils/logger').create('BackgroundsRequestAll');
var _ = require('../../utils/lodash');

/**
 * Creates list of backgrounds based on cloud and branding lists
 * todo: replace async.waterfall with sync calls
 * @param {Function} callback
 */
module.exports = function (callback) {
    asyncLib.waterfall([
        createDataObject,
        getLocalFiles,
        searchForUserBackground,
        addBrandingBackgrounds,
        addCloudBackgrounds,
        addMissingCloudBackground,
        addMissingLocalBackgrounds,
        createFinalList
    ], callback);
};

/**
 * Creates data object for async flow
 * @param {Function} callback
 */
function createDataObject(callback) {
    callback(null, {
        // storage for all backgrounds (from cloud, branding, and local lists)
        backgroundsMap: {},
        // data about local files in fromat {fileName: localFileUrl}
        localFilesMap: {}
    });
}

/**
 * Retrieves local files and adds it to data.localFilesMap
 * @param {Object} data
 * @param {Object} data.backgroundsMap
 * @param {Object} data.localFilesMap
 * @param {Function} callback
 */
function getLocalFiles(data, callback) {
    storage.getAll(function (error, localFilesMap = {}) {
        data.localFilesMap = localFilesMap;
        callback(null, data);
    });
}

/**
 * Retrieves user background from local files and adds it to backgroundsMap
 * @param {Object} data
 * @param {Object} data.backgroundsMap
 * @param {Object} data.localFilesMap
 * @param {Function} callback
 */
function searchForUserBackground(data, callback) {
    var background = userBackground.getFromMap(data.localFilesMap);
    if (background) {
        data.backgroundsMap[background.id] = background;
    }
    callback(null, data);
}

/**
 * Retrieves branding backgrounds and adds it to backgroundsMap
 * @param {Object} data
 * @param {Object} data.backgroundsMap
 * @param {Object} data.localFilesMap
 * @param {Function} callback
 */
function addBrandingBackgrounds(data, callback) {
    var backgrounds = brandingBackgrounds.getBackgrounds();
    _.defaults(data.backgroundsMap, backgrounds);
    callback(null, data);
}

/**
 * Retrieves cloud backgrounds and adds it to backgroundsMap
 * @param {Object} data
 * @param {Object} data.backgroundsMap
 * @param {Object} data.localFilesMap
 * @param {Function} callback
 */
function addCloudBackgrounds(data, callback) {
    var backgrounds = cloudBackgrounds.getBackgrounds();
    _.defaults(data.backgroundsMap, backgrounds);
    callback(null, data);
}

/**
 * Retrieves missing cloud background and adds it to backgroundsMap
 * @param {Object} data
 * @param {Object} data.backgroundsMap
 * @param {Object} data.localFilesMap
 * @param {Function} callback
 */
function addMissingCloudBackground(data, callback) {
    var background = constructMissingCloudBackground(data.backgroundsMap, data.localFilesMap);
    if (background) {
        data.backgroundsMap[background.id] = background;
    }
    callback(null, data);
}

/**
 * Creates missing cloud background object, if it's exist
 * @param {Object} backgroundsMap
 * @param {Object} localFilesMap
 * @returns {?Object}
 */
function constructMissingCloudBackground(backgroundsMap, localFilesMap) {
    var backgroundData = bgSettings.getMissingCloudBackground();
    if (backgroundData && isBackgroundMissing(backgroundData.id, backgroundsMap, localFilesMap)) {
        logger.info('Missing cloud background has been found:', backgroundData.id);
        return constructBackgroundFromLocalData(backgroundData.id, localFilesMap, backgroundData.index);
    }
    return null;
}

/**
 * Retrieves missing local backgrounds and adds it to backgroundsMap
 * @param {Object} data
 * @param {Object} data.backgroundsMap
 * @param {Object} data.localFilesMap
 * @param {Function} callback
 */
function addMissingLocalBackgrounds(data, callback) {
    Object.keys(data.localFilesMap).forEach(function (id) {
        if (!(id in data.backgroundsMap)) {
            logger.info('Missing local background has been found:', id);
            data.backgroundsMap[id] = constructBackgroundFromLocalData(id, data.localFilesMap);
        }
    });
    callback(null, data);
}

/**
 * Checks status of background by id
 * Background is missing if it don't exist in backgroundsMap
 * but exist in localFilesMap
 * @param {String} missingId
 * @param {Object} backgroundsMap
 * @param {Object} localFilesMap
 * @returns {Boolean}
 */
function isBackgroundMissing(missingId, backgroundsMap, localFilesMap) {
    var isConstructed = missingId in backgroundsMap;
    var isInLocalData = missingId in localFilesMap;
    return !isConstructed && isInLocalData;
}

/**
 * Creates background obect from local data
 * @param {String} id
 * @param {Object} localFilesMap
 * @param {?Number} index
 * @returns {Object}
 */
function constructBackgroundFromLocalData(id, localFilesMap, index) {
    return {
        id: id,
        image: localFilesMap[id],
        preview: localFilesMap[id],
        index: index || 0
    };
}

/**
 * Updates backgrounds in currentMap with url from local data
 * @param {Object} localMap
 * @param {Object} currentMap
 */
function updateUrlsFromLocalData(localMap, currentMap) {
    Object.keys(localMap).forEach(function (fileName) {
        if (currentMap[fileName]) {
            currentMap[fileName].image = localMap[fileName];
        }
    });
}

/**
 * Creates final list of backgrounds
 * @param {Object} data
 * @param {Object} data.backgroundsMap
 * @param {Object} data.localFilesMap
 * @param {Function} callback
 */
function createFinalList(data, callback) {
    updateUrlsFromLocalData(data.localFilesMap, data.backgroundsMap);
    var list = Object.keys(data.backgroundsMap)
        .map(function (backgroundId) {
            return data.backgroundsMap[backgroundId];
        })
        .map(function (data) {
            data.branded = Boolean(data.branded);
            data.isUser = Boolean(data.isUser);
            return data;
        })
        .sort(sortFinalList);
    callback(null, list);
}

/**
 * Branded backgrounds come first,
 * then user's background(s),
 * then backgrounds from cloud.
 * @param {Object} a
 * @param {Object} b
 * @returns {Number}
 */
function sortFinalList(a, b) {
    return Number(b.branded) - Number(a.branded)
        || Number(b.isUser) - Number(a.isUser)
        || a.index - b.index;
}



