'use strict';

var User = require('./user');
var find = require('lodash/collection/find');
var channel = require('../../utils/channel');

module.exports = UsersList;

/**
 * A class to work with a set of accounts, a person ever logged in
 * @param {?string} uid
 * @constructor
 */
function UsersList(uid) {
    /**
     * @type {?string}
     */
    this.defaultUid = uid;

    /**
     * @type {User[]}
     */
    this.users = [];

    this.onChanged = channel.create('UsersList.onChanged');
    // TODO: this.onRemoved
}

/**
 * @param {Object} json
 * @returns {UsersList}
 */
UsersList.fromJSON = function (json) {
    var defaultUid;
    var users = (json || [])
        .map(function (userJson) {
            if (userJson.isDefault) {
                defaultUid = userJson.uid;
            }
            return User.fromJSON(userJson);
        }, ul);
    var ul = new UsersList(defaultUid);
    users.forEach(ul.appendUser, ul);
    return ul;
};

UsersList.prototype = {
    /**
     * @returns {boolean}
     */
    get hasAuth() {
        return this.users.some(function (user) {
            return user.hasAuth;
        });
    },

    /**
     * @returns {?User}
     */
    get defaultUser() {
        return this.getUserByUid(this.defaultUid);
    }
};

/**
 * @param {string} uid
 * @returns {?User}
 */
UsersList.prototype.getUserByUid = function (uid) {
    return find(this.users, 'uid', uid);
};

/**
 * Logs out all users
 *
 * @returns {UsersList}
 */
UsersList.prototype.logoutAll = function () {
    this.users.forEach(function (user) {
        user.logout();
    });
    return this;
};

UsersList.prototype.getUsersExcept = function (uids) {
    var uidMap = uids.reduce(function (memo, uid) {
        memo[uid] = true;
        return memo;
    }, {});
    return this.users.filter(function (user) {
        return !uidMap[user.uid];
    });
};

/**
 * Appends new User to collection.
 *
 * @param {User} user
 * @returns {UsersList}
 */
UsersList.prototype.appendUser = function (user) {
    this.users.push(user);
    this.onChanged.dispatch('add', user);
    user.onLogin.addListener(this.onChanged.dispatch.bind(this.onChanged, 'login'));
    user.onLogout.addListener(this.onChanged.dispatch.bind(this.onChanged, 'logout'));
    return this;
};

/**
 * Updates user tf there is user with the same uid in collection,
 * or appends new User account to collection if no uid found
 *
 * @param {Object} account
 * @returns {UsersList}
 */
UsersList.prototype.appendAccount = function (account) {
    var user = this.getUserByUid(account.uid);
    if (user) {
        user.authorize(account);
    } else {
        this.appendUser(new User(account));
    }
    return this;
};

/**
 * @param {User} user
 * @returns {boolean}
 */
UsersList.prototype.isDefault = function (user) {
    return user.uid === this.defaultUid;
};

/**
 * @returns {Object[]}
 */
UsersList.prototype.toJSON = function () {
    return this.users
        .map(function (user) {
            var userJson = user.toJSON();
            if (this.isDefault(user)) {
                userJson.isDefault = true;
            }
            return userJson;
        }, this);
};



