/**
 *     gpauto
 * @see https://st.yandex-team.ru/VB-5208
 */
'use strict';

var logger = require('../../utils/logger').create('cookies gpauto');
var settings = require('../settings/settings');
var geoApi = require('../../platform/geolocation');
var dateUtils = require('../../utils/date');

var LocationWatcher = require('yandex-elements-core/location/watcher');
var LocationTimer = require('yandex-elements-core/location/timer');
var LocationParser = require('yandex-elements-core/location/parser');
var Gpauto = require('yandex-elements-core/cookies/gpauto');
var YpCookie = require('yandex-elements-core/cookies/yp-cookie');
var {logUpdateResult} = require('./utils');
var {logUpdateResult} = require('./utils');

var GPAUTO_PREF = 'geolocation';
var UPDATE_TEMOUT = dateUtils.MINUTE_MS * 10;

var DOMAINS = [
    'yandex.ru',
    'yandex.kz',
    'yandex.ua',
    'yandex.by',
    'yandex.net',
    'yandex.com',
    'yandex.com.tr',
    'ya.ru',
];

var locationTimer;

exports.init = function () {
    logger.info('Initializing');
    locationTimer = initLocationTimer();
    settings.onChanged.addListener(handleSettingsChange);
    if (settings.get(GPAUTO_PREF)) {
        logger.info('Start geolocation monitoring');
        locationTimer.start();
    }
};

/**
 * @returns {LocationTimer}
 */
function initLocationTimer() {
    var watcher = new LocationWatcher(geoApi, {});
    var timer = new LocationTimer(watcher, UPDATE_TEMOUT);
    timer.onError.add(handleTimerError);
    timer.onChange.add(handleLocationChange);
    return timer;
}

/**
 * @param {Object} data
 */
function handleSettingsChange(data) {
    if (data.key === GPAUTO_PREF) {
        updateTimerState(data.newValue);
    }
}

/**
 * @param {Boolean} isMonitoringAllowed
 */
function updateTimerState(isMonitoringAllowed) {
    logger.info('Geolocation settings changed: %s', isMonitoringAllowed);
    if (isMonitoringAllowed) {
        locationTimer.start();
    } else {
        locationTimer.stop();
    }
}

/**
 * @param {Object} position
 */
function handleLocationChange(position) {
    if (position) {
        updateGpauto(position);
    }
}

/**
 * @param {Object} position
 */
function updateGpauto(position) {
    var parser = new LocationParser();
    var gpauto = new Gpauto({locationParser: parser});
    var updatedDomains = [];
    var notUpdatedDomains = [];

    logger.info('Trying to update position: %j', position);
    Promise.all(DOMAINS.map(function (domain) {
        var cookie = new YpCookie(domain);
        return gpauto.updatePosition(position, cookie)
            .then(result => {
                if (result) {
                    updatedDomains.push(domain);
                }
            })
            .catch(() => {
                notUpdatedDomains.push(domain);
            });
    }))
    .then(() => logUpdateResult(logger, updatedDomains, notUpdatedDomains))
    .catch(() => logUpdateResult(logger, updatedDomains, notUpdatedDomains));
}

function handleTimerError(error) {
    logger.warn('Timer error %j', error);
}



