/**
 *      
 */
'use strict';

var asyncLib = require('async');
var platformFs = require('../../platform/file-storage');

var FILE_DIRS = {
    'root': '/',
    'cloud-logo': require('../cloud-logo/saver').CLOUD_LOGO_DIR,
    'cloud-logo-sub': require('../cloud-logo/saver').CLOUD_LOGO_SUB_DIR,
    favicons: require('../favicon').FAVICON_DIR,
    backgrounds: require('../backgrounds/storage').directory,
    screenshots: require('../screenshots/screenshots').SCREENSHOT_DIR,
    tableau: require('../site-info/tableau').TABLEAU_DIR,
};

exports.openChromeFS = function () {
    chrome.tabs.create({url: 'filesystem:chrome-extension://' + chrome.runtime.id + '/persistent/'});
};

exports.openFirefoxFS = function () {
    platformFs.getFileUrl(FILE_DIRS.root, function (err, url) {
        chrome.tabs.create({url: url});
    });
};

exports.getFileList = function (callback) {
    var tasks = Object.keys(FILE_DIRS).reduce(function (res, dirName) {
        res[dirName] = function (callback) {
            readDir(FILE_DIRS[dirName], callback);
        };
        return res;
    }, {});
    asyncLib.parallel(tasks, callback);
};

/**
 *    
 * @returns {Promise}
 */
exports.removeAll = function () {
    var dirs = Object.keys(FILE_DIRS)
        .filter(function (name) {
            return name !== 'root'
        });

    return removeFiles(dirs);
};

function readDir(dir, callback) {
    asyncLib.waterfall([
        function (callback) {
            platformFs.list(dir, callback);
        },
        function (files, callback) {
            asyncLib.series(files.map(function (fileName) {
                return platformFs.getFileUrl.bind(platformFs, fileName);
            }), callback);
        }
    ], callback);
}

/**
 *    url/
 * @param   {Array<String>}   namesOrUrls
 * @returns {Promise}
 */
function removeFiles(namesOrUrls) {
    return new Promise(function (resolve) {
        asyncLib.parallel(
            namesOrUrls.map(function (nameOrUrl) {
                return platformFs.remove.bind(platformFs, nameOrUrl);
            }),
            resolve
        );
    });
}



