/**
 * Replace vars in string templates
 */
'use strict';

var CURLY_RX = /\{([^}]+)\}/g;
var _ = require('./lodash');

/**
 * Finds all entries like {foo.bar} and replaces them with string provided by object
 *
 * @example
 * curly.insertVars('{ns.greet} {ns.who}!', {
 *   ns: {
 *     who: 'world',
 *     greet: 'Hello'
 *   }
 * });
 *
 * @param {string} tmpl
 * @param {Object} vars
 * @param {Object} [options]
 * @param {Function} [options.encode]
 * @param {Regexp} [options.regex]
 * @param {Function} [options.getter] value getter that receives obj and path
 * @returns {string}
 */
exports.insertVars = function (tmpl, vars, options) {
    if (typeof tmpl !== 'string') {
        return tmpl;
    }

    if (!vars || typeof vars !== 'object') {
        return tmpl;
    }

    options = _.defaults({}, options, {
        regex: CURLY_RX,
        getter: _.get,
        encode: null,
    });

    return tmpl.replace(options.regex, function (match, varName) {
        varName = varName.trim();
        var replacement = options.getter(vars, varName);
        if (replacement === undefined) {
            return match;
        } else {
            return options.encode ? options.encode(replacement) : replacement;
        }
    });
};

/**
 * Insert vars for url with encodeURIComponent
 *
 * @param {string} tmpl
 * @param {Object} vars
 * @param {Object} [options]
 * @param {Function} [options.encode]
 * @param {Regexp} [options.regex]
 * @returns {string}
 */
exports.insertUrlVars = function (tmpl, vars, options) {
    options = options || {};
    options.encode = options.encode || encodeURIComponent;
    return exports.insertVars(tmpl, vars, options);
};



