/**
 * @description
 *   tabs-watcher-.        
 *   Signal
 */

'use strict';
const Channel = require('./channel');
const TabsWatcher = require('yandex-elements-core/tabs-watcher');

/**
 *   
 * @type {TabsWatcher}
 */
exports.watcher = new TabsWatcher();

/**
 *   signal => channel
 */
exports.events = {
    public: {}
};

[
    'onCreated',
    'onUpdated',
    'onActivated',
    'onRemoved',
    'onAttached',
    'onReplaced',
    'onBeforeNavigate',
    'onCommitted',
    'onCompleted',
    'onErrorOccurred',
    'onDOMContentLoaded',
    'onHistoryStateUpdated',
    'onReferenceFragmentUpdated',
].forEach(event => {
    exports.events[event] = eventProxyFactory(exports.watcher[event]);
    exports.events.public[event] = eventProxyFactory(exports.watcher.public[event]);
});

/**
 *   
 * @param   {Signal} signal
 * @returns {Channel}
 */
function eventProxyFactory(signal) {
    const channel = new Channel();
    signal.add((...args) => {
        channel.dispatch(...args);
    });

    return channel;
}



