/**
 *   / 
 */
'use strict';

var platformBranding = require('./branding');
var fileDownloader = require('./file-downloader');
var softExport = require('../modules/statistics/soft-export');
var barNavig = require('../modules/statistics/bar-navig');
var about = require('./about');

var logger = require('../utils/logger').create('Platform/configure');

/**
 * @see https://github.yandex-team.ru/desktop/docs/blob/master/visbookmarks-backend/platform.md
 */
module.exports = function (params) {
    params = params || {};

    if (params.branding) {
        platformBranding.init();
    }

    //  file-downloader:   download  platform/file-storage
    if (params.fileDownloader) {
        fileDownloader.init();
    }

    if (params.softExport === 'init') {
        softExport.init();
    } else if (params.softExport === 'send') {
        softExport.send();
    } else if (params.softExport === 'setUninstallURL') {
        var url = softExport.getGoodbyePageUrl();
        setUninstallUrl(url);
    }

    if (params.barNavig === 'init') {
        barNavig.init();
    }

    if (params.browserAction) {
        setBrowserAction();
    }
};

function setUninstallUrl(url) {
    try {
        chrome.runtime.setUninstallURL(url);
        logger.info('Set uninstallUrl: %s', url);
    } catch (e) {
        logger.error('Can not set uninstallUrl: %j', e);
    }
}

// https://st.yandex-team.ru/VB-5616
function setBrowserAction() {
    chrome.action.onClicked.addListener(function () {
        chrome.tabs.create({url: about.visbookmarksUrl, active: true});
    });
}



