/**
 *      ( )
 * TODO:     
 *
 *   :
 * [
 * {
 *  "id":"ru.yandex",
 *  "nodes":["yandex.ru","m.yandex.ru","yandex.ua","yandex.by","yandex.kz"],
 *  "bgcolor":"#ffffff",
 *  "resources":{
 *      "logo_main":"https://storage.ape.yandex.net/get/dashboard3/043_a0eb4e320436fddf835a44b665e27eef.png",
 *      "logo_sub":"https://storage.ape.yandex.net/get/dashboard3/043_a0eb4e320436fddf835a44b665e27eef.png"
 *  }}]
 */

'use strict';

var utils = require('../../utils/utils');
var branding = require('../branding');
var curly = require('../../utils/curly');
var i18n = require('../i18n');
var logger = require('../../utils/logger').create('Cloud-logo/loader');

var URL_TPL = 'https://api.browser.yandex.ru/dashboard3/get/?nodes={nodes}&brandID={brandId}&lang={lang}&scale=0.43';

module.exports = {
    loadFromServer: function (domain, callback) {
        var url = curly.insertUrlVars(URL_TPL, {
            nodes: domain,
            brandId: branding.vars.regionId,
            lang: i18n.locale
        });

        logger.info('Loading logo for domain: %s', domain);

        utils.xhr({
            url: url,
            logError: true,
            parse: 'json',
        }, function (err, json) {
            callback(err, err ? null : parseResponse(json, url));
        });
    }
};

/**
 *   
 * @param {*} json
 * @param {string} url
 */
function parseResponse(json, url) {
    if (!json || !Array.isArray(json)) {
        logger.warn('JSON for url %s is not array %j', url, json);
        return null;
    }

    var item = json[0];

    if (!item) {
        return null;
    }

    var res = typeof item === 'object' ? item.resources : null;

    var hasRequiredFields =
        res && typeof res === 'object'
        && res.logo_main && typeof res.logo_main === 'string'
        && item.bgcolor && typeof item.bgcolor === 'string';

    if (!hasRequiredFields) {
        logger.warn('JSON for url %s does not contain required fields %j', url, json);
        return null;
    }

    return {
        logo: res.logo_main,
        logoSub: res.logo_sub !== res.logo_main
            ? res.logo_sub
            : null,
        color: item.bgcolor.replace(/^#/, '')
    };
}



