/**
 *  soft-export  (INSTALL, DAYUSE  UNINSTALL)
 *       ,     , ..    
 */

'use strict';

var utils = require('../../utils/utils');
var urlUtils = require('../../utils/url');
var platform = require('../../platform');
var platformAbout = require('../../platform/about');
var platformBuildInfo = require('../../platform/build-info');
var i18n = require('../i18n');
var platformClid = require('../../platform/clid');
var branding = require('../branding');
var DayusePoller = require('./dayuse-poller');
var xhr = require('../../utils/xhr');
var logger = require('../../utils/logger').create('Statistics/soft-export');
var settings = require('../settings/settings');
var bannerId = require('../cookies/bannerid');

var INSTALL_SETTING = 'appInstallTime';
var STAT_URL = 'https://soft.export.yandex.ru/status.xml';
var LAST_SENDING_TIME_SETTING = 'lastDayUseSendTime';
var MAX_GOODBYE_URL_LENGTH = 255; // @see https://developer.chrome.com/extensions/runtime#method-setUninstallURL

var poller;

exports.init = function () {
    poller = new DayusePoller(LAST_SENDING_TIME_SETTING);
    poller.onTrigger.addListener(exports.send);
};

/**
 *  install || dayuse.
 *  ,    ,     install  .
 */
exports.send = function () {
    var lastSendTime = poller.getLastSendTime();
    var stat = lastSendTime ? 'dayuse' : 'install';
    var params = exports.getParams(stat);
    var url = urlUtils.createURI(STAT_URL).setSearch(params).toString();
    logger.info('Sending %s to %s', stat, url);
    xhr({url: url}, function (error) {
        if (error) {
            logger.error('Error while sending: %s', error.message);
        } else {
            poller.updateLastSendTime();
        }
    });
};

/**
 *   
 */
exports.getParams = function (stat) {
    var installTime = settings.get(INSTALL_SETTING);
    var output = {
        stat: stat,
        ui: platform.guid,
        ver: platformBuildInfo.getVersion(),
        lang: i18n.locale,
        bv: utils.browserInfo.version,
        os: utils.browserInfo.os === 'win' ? 'winnt' : (utils.browserInfo.os === 'mac' ? 'darwin' : ''),
        yasoft: platformAbout.yasoft,
        gchid: platformAbout.extensionId, // https://st.yandex-team.ru/VB-6179
        brandID: branding.vars.regionId,
        fd: utils.formatDate(new Date(installTime * 1000), '%Y.%M.%D'),
    };

    var lastSendTime = poller.getLastSendTime();
    if (lastSendTime) {
        output.tl = lastSendTime;
    }

    //  Chrome    ,  .
    //  Firefox   .
    // VB-5016
    var clid = utils.browserInfo.isFirefox() ? platformClid.getByNumber(1) : platformClid.getPortalClidByNumber(1);
    if (clid) {
        output.clid = clid;
    }

    var bannerIdValue = bannerId.getBannerId();
    if (bannerIdValue) {
        output.bnrd = bannerIdValue;
    }

    return output;
};

/**
 *  goodbyePage url
 */
exports.getGoodbyePageUrl = function () {
    var url = branding.settings.goodbyeUrl;
    var uri = urlUtils.createURI(url);
    if (!uri) {
        return '';
    }
    uri.setSearch(exports.getParams('uninstall'));
    //   ,     255 
    // @see https://st.yandex-team.ru/VB-6179#1520247731000
    var paramsToTrim = ['os', 'brandID', 'bnrd', 'gchid', 'ver', 'bv', 'lang', 'fd', 'clid'];
    var index = 0;
    while (uri.toString().length > MAX_GOODBYE_URL_LENGTH && index < paramsToTrim.length) {
        uri.removeSearch(paramsToTrim[index++]);
    }
    return uri.toString();
};



