/**
 *         
 */

'use strict';

var backgrounds = require('../backgrounds');
var remoteConfig = require('../remote-config');
var advertisement = require('../advertisement');
var experimentActualConfigs = require('../experiments/actual-configs');
var alarms = require('../../utils/alarms');

/**
 * preInit
 * @param {Function} callback
 */
exports.preInit = function (callback) {
    // always try force testid (for testers convenience)
    experimentActualConfigs.tryForceTestId(callback);
};

/**
 * Run
 */
exports.run = function () {
    //     
    backgrounds.sync();
    //    
    remoteConfig.check();
    //   
    advertisement.checkGroups();
    //  
    alarms.trigger('informers');
};



