/**
 *      modules, utils, platform .  
 */
'use strict';

var setNested = require('lodash/object/set');

module.exports = {};

// require.context works only in webpack environment
try {
    module.exports.modules = getAllStuff(require.context('..', true, /\.js$/));
    module.exports.utils = getAllStuff(require.context('../../utils', true, /\.js$/));
    module.exports.platform = getPlatform();
} catch (e) {}

function getAllStuff(context) {
    return context.keys().reduce(function (memo, name) {
        var filteredName = name
            .replace(/\.js$/, '')
            .replace(/\.\//, '')
            .replace(/\//g, '.')
            .replace(/[^.\w]/g, '_');
        setNested(memo, filteredName, context(name));
        return memo;
    }, Object.create(null));
}

function getPlatform() {
    // require.context ,         
    return {
        about: require('../../platform/about'),
        branding: require('../../platform/branding'),
        build_info: require('../../platform/build-info'),
        clid: require('../../platform/clid'),
        file_storage: require('../../platform/file-storage'),
        locale: require('../../platform/locale'),
        log: require('../../platform/log'),
    };
}



