/**
 *       /    .
 */
'use strict';

var watcher = require('./watcher');
var clients = require('./clients');

exports.init = function () {
    watcher.init();
    clients.init();
};

/**
 *    .
 * @returns {Promise}
 */
exports.tryRestoreFromCookies = function () {
    return clients.cookies.tryRestore();
};

/**
 *  .
 * @returns {Promise}
 */
exports.clear = function () {
    return Promise.all([
        clients.cookies.clear(),
        clients.files.clear(),
    ]);
};



