/**
 *     
 */
'use strict';

var messaging = require('./messaging');
var urlUtils = require('../../utils/url');
var platformAbout = require('../../platform/about');
var events = require('./events');

var XB_URL_REG_EXP = /^xb:\/\//;

var sources = {
    [events.frontend.SEARCH_HISTORY]: {
        id: 0,
        fn: require('../url-suggest/search-history'),
    },
    [events.frontend.SEARCH_BOOKMARKS]: {
        id: 1,
        fn: require('../url-suggest/search-bookmarks'),
    },
    [events.frontend.SEARCH_OPENED_TABS]: {
        id: 2,
        fn: require('../url-suggest/search-opentabs'),
    },
    [events.frontend.SEARCH_WEB]: {
        id: 3,
        fn: require('../url-suggest/search-web'),
    }
};

module.exports = {
    init: function () {
        Object.keys(sources).forEach(createOnMessageHandler);
    }
};

function createOnMessageHandler(msgName) {
    messaging.on(msgName, function (req, sender, sendResponse) {
        var query = req.word || req.query;
        var source = sources[msgName];
        source.fn(query, function (err, items) {
            sendResponse({
                source: source.id,
                query: req.query,
                data: err ? [] : convertItems(items),
            });
        });
        return true;
    });
}

/**
 * @param {Array} [items]
 * @returns {Array}
 */
function convertItems(items) {
    if (!Array.isArray(items)) {
        return [];
    }
    return items.reduce(function (result, item) {
        if (!isBadUrl(item.url)) {
            item.url = urlUtils.getReadableUrl(item.url);
            result.push(item);
        }
        return result;
    }, []);
}

/**
 * @param {String} url
 * @returns {Boolean}
 */
function isBadUrl(url) {
    return urlUtils.isEqualUrls(platformAbout.visbookmarksUrl, url) ||
        urlUtils.isEqualUrls(platformAbout.visbookmarksFakeTabUrl, url) ||
        XB_URL_REG_EXP.test(url);
}



