'use strict';

// see: https://wiki.yandex-team.ru/users/kirand/projects/elements/chrome/searchext
var yandexSearchIDs = [
    // 5.   ()
    'cpimfcfpokkicebcgjimeeemfjkhfplk',
    'laddjijkcfpakbbnnedbhnnciecidncp',
    'ijbepmfgphlcbokeedcnmbedhckonlin',

    // 6.     - 
    'nehapofakghljopfegjogpgpeljkhjjn',
    'ebbpgceilfeopdjlpkcdpefgkjhapoie',
    'cbagalopghhleggojlledjihpdbogmcn',

    // 7.   
    'fdgobpeoflmgannbplllbnhkoniogeka',

    // 8.  " "
    'pmloecjpjlgmhmfjpimadenoekkoknnh',

    // 19.  " "
    'jihlbmjiphbhpbggdjmcigebceidammg',
    'peagnjphcbmgmpoacoganhkchgljlban',
    'bfmefpmchiiiklaoabpmaobmjapabigk',
    'dgpbaklblfccdobmjkjjfcibnbaafofh',
    'ppfbfddocfkbegapcgkipgjnggpaecfe',
];

var yandexStartpageIDs = [
    // 2.     (ya.ru)
   'cdlgdffkkjdoglbioephbahjkkmmpdhi',

    // 3.    ()
    'lalfiodohdgaejjccfgfmmngggpplmhp',
    'lpedlkoodagolnaladgccadeahpjgblg',
    'dkipfjephaclinnhbbpmjigimebkpmbk',

    // 4.    (  )
    'ecalfoflieekeegboemfckkhlgfmgldo',
    'keoelcjgdedmhlojlhlhiogfomadpfkh',
    'bfacnjhhlonddplapoikenaalhekaabg',

    // 6.    - 
    'nehapofakghljopfegjogpgpeljkhjjn',
    'ebbpgceilfeopdjlpkcdpefgkjhapoie',
    'cbagalopghhleggojlledjihpdbogmcn',

    // 9. " "
    'aecdfooahhnabdgjifldpdjoehldcpoj',

    // 19. " "
    'jihlbmjiphbhpbggdjmcigebceidammg',
    'peagnjphcbmgmpoacoganhkchgljlban',
    'njomkkghlicmkndlldaeeboemmimpboe',
    'ljdehichgphpibomgiolefjhjcmgkdga',
    'gokakabmbonignaoohkcipkfakkahnmn',
];

var yandexStartpageConfigs = [
    {
        startpageReg: /^https?\:\/\/(www\.)?yandex\.ru/i,
        landing: 'https://home.yandex.ru',
        extensionIds: []
    },
    {
        startpageReg: /^https?\:\/\/(www\.)?yandex\.ua/i,
        landing: 'https://home.yandex.ua',
        extensionIds: []
    },
    {
        startpageReg: /^https?\:\/\/(www\.)?yandex\.com\.tr/i,
        landing: 'https://home.yandex.com.tr',
        extensionIds: []
    },
];

var yandexSearchConfigs = [
    {
        searchUrlReg: /^https?\:\/\/(www\.)?yandex\.ru/i,
        landing: 'https://set.yandex.ru',
        extensionIds: []
    },
    {
        searchUrlReg: /^https?\:\/\/(www\.)?yandex\.ua/i,
        landing: 'https://set.yandex.ua',
        extensionIds: []
    },
    {
        searchUrlReg: /^https?\:\/\/(www\.)?yandex\.com\.tr/i,
        landing: 'https://set.yandex.com.tr',
        extensionIds: []
    },
];

module.exports = {
    /**
     *     
     *     ,     
     * @param {Function} callback ({Object|null} err, {Array<String>} urls)
     */
    getStartpages: function (callback) {
        getEnabledExtensions(function (items) {
            var hasYandex = items.some(function (item) {
                return yandexStartpageIDs.indexOf(item.id) >= 0;
            });
            //  yandex.ru   ,       ,
            //    
            callback(null, hasYandex ? ['https://yandex.ru'] : []);
        });
    },

    /**
     *    
     *          ,   
     *    ,   
     */
    setStartpages: function (startpages) {
        var startpage = Array.isArray(startpages) ? startpages[0] : null;
        if (typeof startpage !== 'string') {
            return;
        }
        var matchedConfig = yandexStartpageConfigs.filter(function (config) {
            return config.startpageReg.test(startpage);
        })[0];
        if (matchedConfig) {
            chrome.tabs.create({url: matchedConfig.landing});
        }
    },

    /**
     *    
     * @param {Function} callback
     * @param {Object|null} callback.err error
     * @param {Object} callback.providersInfo providers info, e.g. {defaultId = '', providers = [ <config> ] }
     */
    getSearchProviders: function (callback) {
        getEnabledExtensions(function (items) {
            var hasYandex = items.some(function (item) {
                return yandexSearchIDs.indexOf(item.id) >= 0;
            });

            var providersInfo;
            if (hasYandex) {
                //  yandex.ru   ,       ,
                //    
                providersInfo = {
                    defaultId: 'yandex',
                    providers: [{
                        id: 'yandex',
                        url: 'https://yandex.ru'
                    }]
                };
            } else {
                providersInfo = {
                    defaultId: '',
                    providers: []
                };
            }

            callback(null, providersInfo);
        });
    },

    /**
     *    
     *          ,   
     *
     * @param {Object} provider
     * @param {string} provider.id - id 
     * @param {string} provider.url    
     * @param {string} provider.displayName 
     *
     * @param {boolean} [provider.showTopResult]
     * @param {string} [provider.topResultUrl]
     * @param {string} [provider.topResultUrlFallback]
     *
     * @param {boolean} provider.showSearchSuggestions
     * @param {string} [provider.suggestionsUrlJson]
     * @param {string} provider.suggestionsUrl
     * @param {string} [provider.suggestionsUrlFallback]
     *
     * @param {string} provider.faviconUrl
     * @param {string} [provider.faviconUrlFallback]
     * @param {string} [provider.faviconPath]
     *
     * @param {int} [provider.codePage]
     * @param {int} [provider.sortIndex]
     */
    addDefaultSearchProvider: function (provider) {
        if (!provider || !provider.url) {
            return;
        }
        var matchedConfig = yandexSearchConfigs.filter(function (config) {
            return config.searchUrlReg.test(provider.url);
        })[0];
        if (matchedConfig) {
            chrome.tabs.create({url: matchedConfig.landing});
        }
    },

    /**
     *  
     *    , ..    
     */
    removeSearchProvider: function () { },

    /**
     *   
     */
    openBookmarksWindow: function () {
        chrome.tabs.create({url: 'chrome://bookmarks'});
    },

    /**
     *   
     */
    openHistoryWindow: function () {
        chrome.tabs.create({url: 'chrome://history'});
    },

    /**
     *   
     */
    openDownloadsWindow: function () {
        chrome.tabs.create({url: 'chrome://downloads'});
    },
};

function getEnabledExtensions(callback) {
    callback([]);
}



