/**
 *    
 */

'use strict';

var platformAbout = require('../../platform/about');
var platformBuildInfo = require('../../platform/build-info');
var buildData = require('../debug/build-data');
var isChromeDebug = platformAbout.id === 'chr' && (platformBuildInfo.isDebug() || !buildData.git.release);

var logger = require('../../utils/logger').create('Core');

module.exports = {
    attach: function (win) {
        win.onerror = function (msg, url, line, symbol, exception) {
            var msgError = msg + ' in ' + url + ' (line: ' + line + ', symbol: ' + symbol + ')';
            if (exception && exception.stack) {
                msgError += ' \nSTACK: ' + exception.stack;
            }

            logger.error(msgError);

            // todo: https://wiki.yandex-team.ru/stardust/Sboroshibokpriraboterasshirenijj
            /*
            Metrika.param(msg, line + ':' + symbol, {
                module: 'Core',
                trace: (exception && exception.trace)
            });
            */

            //      ,    (  )
            if (isChromeDebug) {
                window.alert(msgError);
            }
        };
    },
    /**
     * ,        window.onerror
     * https://code.google.com/p/chromium/issues/detail?id=357568
     *
     *       chrome.* api :    setTimeout 0.

     :

     chrome.tabs.get(123, wrap(function () {
         throw '123';
     }));

     *   :
     * -    
     * -    
     * -    chrome.runtime.lastError
     * -      chrome.runtime.onMessage,
     * ..   ,  setTimeout    
     *
     *  ,      ,
     *           .
     *
     * todo: ,      /     
     *
     * @param {Function} fn
     * @returns {*}
     */
    wrap: function (fn) {
        //        (    )  
        //      
        if (platformAbout.id !== 'chr' || isChromeDebug) {
            return fn;
        } else {
            return function () {
                var args = arguments;
                if (chrome.runtime.lastError) {
                    // todo: log about ('Error');
                }
                setTimeout(function () {
                    fn.apply(null, args);
                }, 0);
            };
        }
    }
};



