/**
 *  bar-navig 
 * https://beta.wiki.yandex-team.ru/visualbookmarks/tz/statistics/#parametrybar-navig
 *
 *       ,     , ..    
 *
 */

'use strict';

var utils = require('../../utils/utils');
var platform = require('../../platform');
var platformAbout = require('../../platform/about');
var platformBuildInfo = require('../../platform/build-info');
var platformClid = require('../../platform/clid');
var branding = require('../branding');
var settings = require('../settings/settings');
var tabsWatcher = require('../../utils/tabs-watcher-2');
var xhr = require('../../utils/xhr');
var xml = require('../../utils/xml');
var barNavigThumbs = require('./bar-navig-thumbs');
var logger = require('../../utils/logger').create('Statistics/bar-navig');

//         -,     
//     
var R1_SETTING = 'r1String';

exports.init = function () {
    tabsWatcher.events.onCompleted.addListener(onTabLoaded);
    barNavigThumbs.init();
};

/**
 *   bar-navig
 */
function onTabLoaded(tabId, tab) {
    var url = tab.data && tab.data.url;
    if (settings.get('sendUsageStat') && utils.url.isRemote(url)) {
        collectData(tabId, tab.data, url, trySendWithBackup);
    }
}

/**
 *      ,   
 */
function trySendWithBackup(params) {
    trySend(branding.statistics.barnavigPrimaryUrl, params, function (err) {
        if (err) {
            trySend(branding.statistics.barnavigBackupUrl, params, logSendWithBackupErrors);
        }
    });
}

/**
 *     
 */
function trySend(url, params, callback) {
    callback = callback || utils.noop;
    if (url) {
        send(url, params, callback);
    } else {
        callback(new Error('Url is empty'));
    }
}

/**
 * @param {Error} [err]
 */
function logSendWithBackupErrors(err) {
    if (err) {
        logger.error('Problem in trySendWithBackup method: %o', err);
    }
}

/**
 *   
 */
function send(url, params, callback) {
    logger.info('Sending bar-navig to %s: %j', url, params);
    xhr({
        url: url,
        withCredentials: true,
        parse: 'xml',
        body: params,
    }, function (error, xmlDoc) {
        if (error) {
            logger.error('Error while sending to %s: %s', url, error.message);
            callback(error);
        } else {
            updateR1(xmlDoc);
            callback();
        }
    });
}

/**
 *   
 */
function collectData(tabId, tabData, url, callback) {
    var params = getBaseParams();
    params.url = url;
    params.title = tabData.title || '';
    params.target = tabData.navInNewWindow
                        ? 'w'
                        : (tabData.navInExistingTab ? 'c' : 't');
    // todo: params['real-referer'] = tabData.realRef || undefined;
    // todo: params['tab-id'] = tabData.uuid ? tabData.uuid.replace(/\-/g, '') : undefined;

    //  ,    
    var vtbNum = barNavigThumbs.getPosition(tabId, tabData.url);
    if (typeof vtbNum === 'number') {
        params.vtbNum = vtbNum;
    }

    //     
    var options = {
        tabId: tabId,
        isYandexHost: utils.url.isYandexHost(url)
    };

    platform.getBarnavigParams(options, function (platformParams) {
        if (platformParams) {
            params.referer = platformParams.referer || undefined;
            params.yamm = platformParams.yamm || undefined;
        }
        callback(params);
    });
}

function getBaseParams() {
    return {
        ver: platformBuildInfo.getVersion(),
        clid: platformClid.getByNumber(4) || platformClid.getByNumber(1),
        yasoft: platformAbout.yasoft,
        brandID: branding.getRegionBrandId(),
        ui: platform.guid,
        show: 1,
        urlinfo: 0,
        r1: settings.get(R1_SETTING) || undefined,
    };
}

function updateR1(xmlDoc) {
    if (xmlDoc) {
        var r1 = xml.getText(xmlDoc, 'urlinfo > r1');
        if (r1) {
            settings.set(R1_SETTING, r1);
        }
    }
}



