/**
 *       Blob|File
 */

'use strict';

const blobUtils = {
    /**
     *      JSON    
     *
     * @param {File|Blob} file
     * @returns {Promise.<Object>}
     */
    readAsJson: function (file) {
        return blobUtils.readAsText(file)
            .then(JSON.parse);
    },

    /**
     * @see FileReader.readAsText
     *
     * @param {File|Blob} file
     * @returns {Promise.<Object>}
     */
    readAsText: readAsFactory('readAsText'),

    /**
     * @see FileReader.readAsArrayBuffer
     *
     * @param {File|Blob} file
     * @returns {Promise.<Object>}
     */
    readAsArrayBuffer: readAsFactory('readAsArrayBuffer'),

    /**
     * @see FileReader.readAsBinaryString
     *
     * @param {File|Blob} file
     * @returns {Promise.<Object>}
     */
    readAsBinaryString: readAsFactory('readAsBinaryString'),

    /**
     * @see FileReader.readAsDataUrl
     *
     * @param {File|Blob} file
     * @returns {Promise.<Object>}
     */
    readAsDataURL: readAsFactory('readAsDataURL'),
};

/**
 * @param {string} as    :
 * 'readAsText', 'readAsArrayBuffer', 'readAsBinaryString', 'readAsDataURL'
 *
 * @returns {function}
 */
function readAsFactory(as) {
    /**
     * @param {Blob|File} file
     * @return {Promise}
     */
    return function readAs(file) {
        return new Promise((resolve, reject) => {
            var reader = new FileReader();

            reader.addEventListener('load', () => resolve(reader.result));
            reader.addEventListener('error', reject);
            reader.addEventListener('abort', reject);

            reader[as](file);
        });
    };
}

module.exports = blobUtils;




