/**
 * Sends clicker statistics every time NewTab is opened
 * @see https://st.yandex-team.ru/VB-5601
 */
'use strict';

var clicker = require('./clicker');
var tabs = require('../../utils/tabs');
var tabsWatcher = require('../../utils/tabs-watcher');
var urlUtils = require('../../utils/url');
var platfromAbout = require('../../platform/about');

exports.init = function () {
    tabsWatcher.init(platfromAbout);
    tabsWatcher.onComplete.addListener(handleCompletedEvent);
    forEachVBTab(sendStat);
};

/**
 * @param {String} tabId
 * @param {Object} tabData
 */
function handleCompletedEvent(tabId, tabData) {
    if (tabData && urlUtils.isUrlInList(platfromAbout.visbookmarksUrl, tabData.urls)) {
        sendStat();
    }
}

/**
 * @param {Function} callback
 */
function forEachVBTab(callback) {
    tabs.getOpen(function (err, items) {
        if (Array.isArray(items)) {
            items.filter(function (tab) {
                return urlUtils.isEqualUrls(tab.url, platfromAbout.visbookmarksUrl) && tab.status === 'complete';
            }).forEach(callback);
        }
    });
}

function sendStat() {
    clicker.send({path: 'system.show'});
}



