/**
 * Base informer class
 * - make request to url
 * - save data to storage
 */

'use strict';

const xhr = require('../../utils/xhr');
const channel = require('../../utils/channel');
const settings = require('../settings/settings');
const ticker = require('./ticker');

/**
 * @param {Object} options
 * @param {String} options.url
 * @param {String} options.setting
 * @param {Function} options.parser
 * @param {Object} options.logger
 */
const Informer = module.exports = function (options) {
    this.options = options;
    this.logger = options.logger;
    this.onUpdated = channel.create(options.setting);
    ticker.onTick.addListener(() => this.updateData());
};

/**
 * Returns current data
 * @returns {Object}
 */
Informer.prototype.getData = function () {
    return settings.get(this.options.setting) || null;
};

/**
 * Updates data
 */
Informer.prototype.updateData = function () {
    this._loadData()
        .then(responseXml => this._extractData(responseXml))
        .then(data => this._saveData(data))
        .catch(e => this._processError(e));
};

Informer.prototype._loadData = function () {
    this.logger.info('Loading data from: %s', this.options.url);
    // todo: make utility xhr returning promise
    // todo: move to window fetch
    return new Promise((resolve, reject) => {
        xhr({
            url: this.options.url,
            parse: 'xml',
        }, (error, xmlDoc) => {
            if (error) {
                this.logger.error('Error while loading data %s', error.message);
                reject();
            } else {
                resolve(xmlDoc);
            }
        });
    });
};

Informer.prototype._extractData = function (responseXml) {
    return this.options.parser.call(this, responseXml);
};

Informer.prototype._saveData = function (data) {
    this.logger.info('Saving data: %j', data);
    settings.set(this.options.setting, data);
    this.onUpdated.dispatch();
};

Informer.prototype._processError = function (e) {
    if (e) {
        this.logger.error(e);
    }
    this._saveData(null);
};

/**
 * Static method checking that all props are not NaN or empty string
 * @param {Object|null} obj
 * @returns {Boolean}
 */
Informer.hasEmptyProps = function (obj) {
    if (!obj) {
        return true;
    } else {
        return Object.keys(obj).some(key => {
            // use Number.isNaN() instead of isNaN() as isNaN() returns true for strings
            return (typeof obj[key] === 'string' && obj[key] === '') || Number.isNaN(obj[key]);
        });
    }
};



