/**
 * Implementation of url utils for 'url' type
 * It's default implementation for all urlUtils methods
 */

'use strict';

var URI = require('urijs');
var createURI = require('./create-uri');
var buildInfo = require('../../platform/build-info');
var about = require('../../platform/about');

var YANDEX_DOMAINS = require('./yandex-domains');

var PROTOCOL_NORMALIZATION_MAP = {
    https: 'http',
    ftps: 'ftp'
};

var REMOTE_PROTOCOLS = {
    http: true,
    https: true,
    ftp: true,
    ftps: true,
};

var ALLOWED_FOR_DOWNLOAD_PROTOCOLS = {
    http: true,
    https: true,
    file: true,
    ftp: true,
    ftps: true,
};

/**
 *  ,       ,
 *    
 */
var TECHNICAL_PARAM = 'nugt';

/**
 *   URI   :
 * -   ,   ,  URI
 * -   ,   DEFAULT_PROTOCOL   URI
 * @param {String} [urlOrDomain]
 * @returns {URI|null}
 */
exports.createURI = createURI;

/**
 * @param {String} url
 * @returns {URI|null}
 */
function createReadableURI(url) {
    try {
        var readableUrl = createURI(url).readable();
        return createURI(readableUrl);
    } catch (e) {
        return null;
    }
}

/**
 *   (https -> http, ftps -> ftp) /  www  url.
 *    url ,     http
 *
 * @param {String} url
 * @param {Object} [params]
 * @param {Boolean} [params.protocol=true]  true   .
 * @param {Boolean} [params.www=true]  true,   www.
 * @returns {String}
 */
exports.getNormalUrl = function (url, params) {
    params = params || {};
    var shouldNormalizeProtocol = params.protocol === true ? true : false;
    var shouldNormalizeWWW = params.www === true ? true : false;

    try {
        var urlObject = createURI(url).normalize();

        if (shouldNormalizeProtocol) {
            var protocol = urlObject.protocol();
            urlObject.protocol(PROTOCOL_NORMALIZATION_MAP[protocol] || protocol);
        }

        if (shouldNormalizeWWW) {
            urlObject.hostname(normalizeHostname(urlObject));
        }

        return urlObject.toString();
    } catch (e) {
        return url;
    }
};

/**
 * @param {String} [url]
 * @returns {String}
 */
exports.getReadableUrl = function (url) {
    var uri = createReadableURI(url);
    if (uri) {
        return uri.toString();
    }
    return url;
};

/**
 *     www
 * @param {String} [url]
 * @returns {String}
 */
exports.getNormalDomain = function (url) {
    try {
        return normalizeHostname(createURI(url).normalize());
    } catch (e) {
        return '';
    }
};

/**
 * @param {String} [url]
 * @returns {String}
 */
exports.getReadableDomain = function (url) {
    try {
        return normalizeHostname(createReadableURI(url));
    } catch (e) {
        return '';
    }
};

/**
 *     www
 * @param   {URI} uri
 * @returns {String}
 */
function normalizeHostname (uri) {
    var subdomain = uri.subdomain();

    if (subdomain) {
        subdomain = subdomain.replace(/^www(\.|$)/, '');
        return (subdomain ? subdomain + '.' : '') + uri.domain();
    }

    return uri.hostname();
}

var LAST_SLASH_REG_EXP = /\/$/;

/**
 * Normalizes url and returns url path
 * @param {String} url
 * @returns {String}
 */
exports.getNormalPath = function (url) {
    try {
        return createURI(url).normalize().path().replace(LAST_SLASH_REG_EXP, '');
    } catch (e) {
        return '';
    }
};

/**
 * Returns index part of provided url.
 * Example: 'https://www.yandex.ru/path?search=hello'->'https://www.yandex.ru/'
 * @param {String} url
 * @returns {String}
 */
exports.getIndexPart = function (url) {
    try {
        var uri = createURI(url).normalize();
        return uri.protocol() + '://' + uri.hostname() + '/';
    } catch (error) {
        return '';
    }
};

/**
 * Returns query object for passed url
 * @param {String} url
 * @returns {Object}
 */
exports.getQueryObject = function (url) {
    try {
        return createURI(url).search(true);
    } catch (e) {
        return {};
    }
};

/**
 * Adds 'rnd' param with random value to passed url.
 * If passed param isn't valid url or scheme is "data:", this method just returns it back.
 * @param {String} url
 * @returns {String}
 */
exports.addRandomParam = function (url) {
    if (!/^data:/.test(url)) {
        try {
            // TODO make sure that url with random params has only '/' slashes
            // TODO use exports.setQuery
            var randomValue = (Date.now() + Math.random()).toString(36);
            return createURI(url).setQuery('rnd', randomValue).toString();
        } catch (error) {}
    }

    return url;
};

/**
 * Is passed url is top level (without path)
 * @param {String} url
 * @returns {Boolean}
 */
exports.isIndexPage = function (url) {
    try {
        var uri = createURI(url).normalize();
        return exports.isYandexHost(url)
            ? isYandexIndexPage(uri)
            : isCommonIndexPage(uri);
    } catch (e) {
        return false;
    }
};

/**
 * @param {URI} uri
 * @returns {Boolean}
 * @throws {Error}
 */
function isYandexIndexPage(uri) {
    return uri.path() === '/' && isYandexIndexQuery(uri);
}

var YANDEX_INDEX_QUERIES = ['clid', 'from'];

/**
 * @param {URI} uri
 * @returns {Boolean}
 * @throws {Error}
 */
function isYandexIndexQuery(uri) {
    var queryObject = uri.search(true);
    return Object.keys(queryObject).every(function (name) {
        return YANDEX_INDEX_QUERIES.indexOf(name) !== -1;
    });
}

/**
 * @param {URI} uri
 * @returns {Boolean}
 * @throws {Error}
 */
function isCommonIndexPage(uri) {
    return uri.path() === '/' && uri.query() === '';
}

/**
 * Is passed url remote (has http/ftp protocols)
 * @param {String} url
 * @returns {Boolean}
 */
exports.isRemote = function (url) {
    try {
        return createURI(url).normalize().protocol() in REMOTE_PROTOCOLS;
    } catch (error) {
        return false;
    }
};

/**
 * Is url downloadable (has http/ftp/file protocols)
 * @param {String} url
 * @returns {Boolean}
 */
exports.isAllowedForDownload = function (url) {
    try {
        return createURI(url).normalize().protocol() in ALLOWED_FOR_DOWNLOAD_PROTOCOLS;
    } catch (error) {
        return false;
    }
};

/**
 *   
 * @param {String} url1
 * @param {String} url2
 * @returns {Boolean}
 */
exports.isEqualUrls = function (url1, url2) {
    //   ,    true
    // see https://github.com/medialize/URI.js/issues/265
    if (isEqualStrings(url1, url2)) {
        return true;
    }
    var uri1 = createURI(url1);
    var uri2 = createURI(url2);
    return isEqualUris(uri1, uri2);
};

/**
 * @param {String} url
 * @param {Array<String>} urlList
 * @returns {Boolean}
 */
exports.isUrlInList = function (url, urlList) {
    if (!Array.isArray(urlList)) {
        return false;
    }
    if (!url) {
        return false;
    }
    return urlList.some(function (listUrl) {
        return exports.isEqualUrls(listUrl, url);
    });
};

/**
 * @param {String} url1
 * @param {String} url2
 * @returns {Boolean}
 */
function isEqualStrings(url1, url2) {
    var url1Lower = typeof url1 === 'string' ? url1.toLowerCase() : url1;
    var url2Lower = typeof url2 === 'string' ? url2.toLowerCase() : url2;
    return url1Lower === url2Lower;
}

/**
 * @param {URI} uri1
 * @param {URI} uri2
 * @returns {Boolean}
 */
function isEqualUris(uri1, uri2) {
    try {
        return uri1.equals(uri2);
    } catch (e) {
        return false;
    }
}

/**
 * @param {String} url
 * @returns {Boolean}
 */
exports.isYandexHost = function (url) {
    try {
        return createURI(url).normalize().domain() in YANDEX_DOMAINS;
    } catch (error) {
        return false;
    }
};

/**
 * @returns {Boolean}
 */
exports.isDataUri = function () {
    return false;
};

/**
 * Is url similar to "foo:bar"
 * @returns {Boolean}
 */
exports.isSpecialUrl = function () {
    return false;
};

/**
 * Adds nugt param to url, but only if passed url has yandex host
 * @param {String} url
 * @returns {String}
 */
exports.addTechnicalParam = function (url) {
    if (!exports.isYandexHost(url)) {
        return url;
    }
    try {
        return createURI(url).setQuery(TECHNICAL_PARAM, createTechnicalParam()).toString();
    } catch (error) {
        return url;
    }
};

/**
 * Creates value for nugt param
 * @returns {String}
 */
function createTechnicalParam() {
    return about.yasoft + '-' + buildInfo.getVersion();
}

/**
 * Removes nugt params from urls with yandex hosts
 * @param {String} url
 * @returns {Boolean}
 */
exports.deleteTechnicalParam = function (url) {
    if (!exports.isYandexHost(url)) {
        return url;
    }
    try {
        return createURI(url).removeQuery(TECHNICAL_PARAM).toString();
    } catch (error) {
        return url;
    }
};

/**
 * Creates absolute url from passed relativeUrl
 * @param {?String} siteUrl
 * @param {?String} baseUrl
 * @param {?String} relativeUrl
 * @returns {null|String}
 */
exports.resolve = function (siteUrl, baseUrl, relativeUrl) {
    try {
        var siteURI = new URI(siteUrl);
        var relativeURI = new URI(relativeUrl);
        if (!siteURI.is('url') || !siteURI.is('absolute')
            || !siteURI.protocol() || !relativeURI.is('url')) {
            return null;
        }
        var baseURI = baseUrl ? new URI(baseUrl).absoluteTo(siteURI) : siteURI;
        return relativeURI.absoluteTo(baseURI).toString();
    } catch (e) {
        return null;
    }
};

/**
 * Adds query to url.
 * If passed param isn't valid url, this method just returns it back.
 * @param {String} url
 * @param {Object} params key-value data
 * @returns {String}
 */
exports.setQuery = function (url, params) {
    try {
        return createURI(url).setQuery(params).toString();
    } catch (error) {
        return url;
    }
};



