'use strict';

const navigations = require('../../navigations');
const branding = require('../../branding');
const curly = require('../../../utils/curly');
const Consumer = require('./consumer');
const PromisedTimer = require('../../../utils/promised-timer');

/**
 *        .
 *
 * @class
 */
class SerpNavigator extends Consumer {
    /**
     * @constructor
     * @param {tabId} tabId
     * @param {number} delay    
     * @param {string} target     .
     *      .
     */
    constructor (tabId, delay = 0, target = navigations.TARGET.TAB) {
        super();
        this.delay = delay;
        this.target = target;
        this.tabId = tabId;
    }

    /**
     * @param text
     * @returns {Promise.<boolean>}
     */
    check (text) {
        this.checkResult = text;
        return Promise.resolve(true);
    }

    /**
     * @param {string} text        
     * @returns {boolean}
     */
    run ({text} = {}) {
        text = text || this.checkResult;
        this.timeout = new PromisedTimer(this.delay);

        //        .
        return this.timeout
            .then(() => this._goToSerp(text))
            .catch(() => Promise.resolve());
    }

    /**
     *    
     */
    stop () {
        if (this.timeout) {
            this.timeout.stop();
        }
    }

    /**
     *  
     *
     * @param {string} query
     * @private
     */
    _goToSerp (query) {
        const url = curly.insertUrlVars(branding.search.url, {searchTerms: query});
        navigations.go({
            url,
            target: this.target,
            tabId: this.tabId,
        });
    }
}

module.exports = SerpNavigator;



