/**
 * Experiments module
 */
'use strict';

var settings = require('../settings/settings');
var Experimentator = require('yandex-elements-core/experiments/experimentator');
var Experiment = require('yandex-elements-core/experiments/experiment');
var platformConfigure = require('../../platform/configure');
var alarms = require('../../utils/alarms');
var runstate = require('../startup/runstate');
var actualConfigs = require('./actual-configs');
var logger = require('../../utils/logger').create('Experiments');
var _ = require('lodash');

var EXPERIMENTS_PREF = 'experiments';

//      
var ALARM = {
    name: 'experiments',
    periodInMinutes: 60 * 24
};

/**
 * Init
 */
exports.init = function () {
    alarms.register(ALARM, removeOutdatedExperiments);
    if (runstate.isInstall()) {
        install();
    }
    if (runstate.isUpgrade()) {
        upgrade();
    }
    logExistingExperiments();
};

/**
 * Returns actual experiments
 * @returns {Array<Experiment>}
 */
exports.getExperimentsList = function () {
    var experiments = settings.get(EXPERIMENTS_PREF);
    if (Array.isArray(experiments)) {
        return experiments.map(function (data) {
            return new Experiment(data);
        });
    } else {
        return [];
    }
};

/**
 * Search actual experiment with given name and returns its data
 * @param {String} experimentName
 * @returns {Object|null}
 */
exports.getExperimentData = function (experimentName) {
    var experiment = _.find(this.getExperimentsList(), {name: experimentName});
    return experiment ? experiment.data : null;
};

/**
 * Read configs and create experiments from it
 */
function install() {
    var configs = actualConfigs.get();
    var existing = [];
    var experiments = new Experimentator().createExperiments(configs, existing, {isInstall: true});
    store(experiments);
}

/**
 * Read configs
 * Read current experiments and remove non-actual
 * Merge configs with experiments and save new experiments
 */
function upgrade() {
    var configs = actualConfigs.get();
    var existing = exports.getExperimentsList();
    var experiments = new Experimentator().createExperiments(configs, existing, {isInstall: false});
    store(experiments);
}

/**
 * Store actual experiments
 * @param {Array<Experiment>} experiments
 */
function store(experiments) {
    settings.set(EXPERIMENTS_PREF, experiments);
    platformConfigure({experiments: experiments});
}

/**
 * Remove outdated experiments
 */
function removeOutdatedExperiments() {
    var experiments = exports.getExperimentsList();
    var filtered = new Experimentator().getActualExistingExperiments(experiments, actualConfigs.get());
    store(filtered);
}

/**
 * Log existing experiments
 */
function logExistingExperiments() {
    logger.info('Selected experiments: %j', exports.getExperimentsList());
    logger.info('Possible experiments: %j', actualConfigs.get());
}



