/**
 * Fetches ad blocks from Atom
 * @see https://old.wiki.yandex-team.ru/visualbookmarks/tz/atom
 * Example of getData response:
 {
    "banner": {
        "banner_id": "7159705",
        "button_yes": "",
        "host": "vb_popup",
        "image": "http://download.cdn.yandex.net/...",
        "link": "https://browser.yandex.ru/...",
        "region_genitive": "",
        "show_id": "000009145693274032416339940000",
        "title": "  .",
        "counters": {
            "cid": "198",
            "pid": "198",
            "click": {
                "path": "tech.portal-ads.vb_popup",
                "vars": "-product=browser,-eventtype=click"
            },
            "close": {...},
            "install": {...},
            "show": {...}
        }
    },
    "stripe": {
        "banner_id": "1393695",
        "button_yes": "",
        "host": "vb_stripe",
        "link": "https://browser.yandex.ru/desktop/main/...",
        "region_genitive": "",
        "show_id": "010009145693274032416339940000",
        "title": "   ",
        "counters": {...}
    }
 }
 */
'use strict';

var logger = require('../../utils/logger').create('advertisement/atom');
var branding = require('../branding');
var i18n = require('../i18n');
var authCookies = require('../auth/yauth-cookies');
var Atom = require('yandex-elements-core/atom');
var merge = require('lodash/object/merge');

var ATOM_URL = 'http://mobile.yandex.net/search/atomsearch/vb_extension';

var AD_TYPES = ['banner', 'stripe'];

var atom = new Atom(ATOM_URL);

/**
 * Fetches ad blocks from Atom
 * @returns {Promise<Object>}
 */
exports.getData = function () {
    return getParams()
        .then(atom.getContent.bind(atom))
        .then(formatResponse)
        .catch(logger.error.bind(logger));
};

/**
 * @returns {Promise<Object>}
 */
function getParams() {
    return getYandexUID().then(function (uid) {
        return merge({yandexuid: uid}, getCommonParams());
    });
}

/**
 * @returns {Promise<Object>}
 */
function getYandexUID() {
    return new Promise(function (resolve) {
        authCookies.getYandexuidCookie(resolve);
    });
}

/**
 * @returns {Object}
 */
function getCommonParams() {
    return {
        tld: branding.vars.regionTld,
        lang: i18n.locale,
        'user-agent': navigator.userAgent
    };
}

/**
 * @param {AtomResponse} atomResponse
 * @returns {Object}
 */
function formatResponse(atomResponse) {
    return AD_TYPES.reduce(function (result, type) {
        result[type] = atomResponse.getConfigSource(type);
        return result;
    }, {});
}



