'use strict';

var urlUtils = require('../../utils/url');
var FAVICON_SERVICE_URL = 'https://favicon.yandex.net/favicon/';

exports.getUrl = function (siteUrl) {
    return FAVICON_SERVICE_URL + urlUtils.getNormalDomain(siteUrl);
};



