/**
 * Informers: weather, traffic, currency, mail
 * https://st.yandex-team.ru/VB-5689
 */

'use strict';

const ticker = require('./ticker');
const reginfo = require('./reginfo');
const quotes = require('./quotes');
const branding = require('../branding');
const settings = require('../settings/settings');
const channel = require('../../utils/channel');
const _ = require('../../utils/lodash');

const ON_UPDATED_DELAY_MS = 500;
const ENABLED_SETTING_NAME = 'showInformers';

// informers
const informers = {};

/**
 * Init
 */
exports.init = function () {
    if (exports.isSupported()) {
        ticker.init();
        createInformers();
        listenUpdates();
        settings.onChanged.addListener(onSettingChanged);
    }
};

/**
 * Is informers supported for that build (currently only for yandex branding + experiment)
 * @returns {Boolean}
 */
exports.isSupported = function () {
    return branding.getRegionBrandId() === branding.REGION_BRANDINGS.YANDEX;
};

/**
 * Is informers enabled in settings
 * @returns {Boolean}
 */
exports.isEnabled = function () {
    return Boolean(settings.get(ENABLED_SETTING_NAME));
};

/**
 * Returns data for all informers
 * @returns {Object}
 */
exports.getData = function () {
    return Promise.all([
        informers.reginfo.getData(),
        informers.quotes.getData()
    ]).then((results) => {
        const [reginfoData, quotesData] = results;
        return {
            weather: reginfoData && reginfoData.weather,
            traffic: reginfoData && reginfoData.traffic,
            // keep both usd and eur for future
            currency: {
                usd: quotesData && quotesData.code === 'usd' ? quotesData : null,
                eur: quotesData && quotesData.code === 'eur' ? quotesData : null,
            }
        };
    });
};

/**
 * On updated channel. Debounced trigger after informers updated.
 */
exports.onUpdated = channel.create();

/**
 * On enabled changed
 */
exports.onEnabledChanged = channel.create();

function createInformers() {
    informers.reginfo = reginfo.create();
    informers.quotes = quotes.create();
}

function listenUpdates() {
    const dispatchDebounced = _.debounce(() => exports.onUpdated.dispatch(), ON_UPDATED_DELAY_MS);
    Object.keys(informers).forEach(key => informers[key].onUpdated.addListener(dispatchDebounced));
}

function onSettingChanged(info) {
    if (info.key === ENABLED_SETTING_NAME) {
        exports.onEnabledChanged.dispatch();
    }
}



