/**
 *    ( bootstrap)
 */

'use strict';

var utils = require('../../utils/utils');
var always = require('./always');
var runstate = require('./runstate');
var install = require('./install');
var upgrade = require('./upgrade');
var normalStart = require('./normal-start');
var bootstrap = require('./bootstrap');
var logger = require('../../utils/logger').create('Startup');

//        
var runCallback;
//    
var startExecuteTime;

/**
 *  
 */
exports.run = function (callback) {
    startExecuteTime = Date.now();
    runCallback = callback || utils.noop;
    always.prepare();

    always.initCore(processRunstate);
};

/**
 *    runstate: install | upgrade | normal start
 */
function processRunstate() {
    runstate.detect(function () {
        var moduleToRun = getModuleToRun();
        moduleToRun.preInit(function () {
            always.initNormal();
            moduleToRun.run(finalize);
        });
    });
}

function getModuleToRun() {
    return runstate.isInstall()
        ? install
        : runstate.isUpgrade()
            ? upgrade
            : normalStart;
}

function finalize() {
    always.finalize(function () {
        logTimes();
        runCallback();
    });
}

function logTimes() {
    var runstateText = runstate.isInstall() ? 'install' : (runstate.isUpgrade() ? 'upgrade' : 'normal run');
    var now = Date.now();
    logger.info('Bundle times (%s): load (%i ms), execute (%i ms), total (%i ms)',
        runstateText,
        startExecuteTime - bootstrap.startTime,
        now - startExecuteTime,
        now - bootstrap.startTime
    );
}



