'use strict';

/**
 * Checks type of passed value
 * @param {*} instance
 * @param {*} Class
 * @param {*} message
 * @throws {Error}
 */
exports.type = function (instance, Class, message) {
    if (!(instance instanceof Class)) {
        throw new Error(message);
    }
};

/**
 * Checks that passed value isn't empty.
 * Uses simple "!value" check.
 * @param {*} value
 * @param {*} message
 * @throws {Error}
 */
exports.notEmpty = function (value, message) {
    if (!value) {
        throw new Error(message);
    }
};



