/**
 *     
 */

'use strict';

var FIRST_SLASH_REG = /^\//;
var SLASH_BACKSLASH_REG = /\\+|\/+/g;

var fileUtils = {
    /**
     * Extracts file name from passed file path
     * @param {String} filePath
     * @returns {String}
     */
    extractName: function (filePath) {
        var slashIndex = filePath.lastIndexOf('/');
        return filePath.slice(slashIndex + 1);
    },

    /**
     *   
     * @param {String} filePath
     * @returns {String}
     */
    extractExtension: function (filePath) {
        var dotIndex = (filePath || '').lastIndexOf('.');
        return dotIndex >= 0 ? filePath.slice(dotIndex + 1) : '';
    },

    /**
     * Extracts dir name from passed filePath
     * @param {String} filePath
     * @returns {String|null}
     */
    extractDirectoryName: function (filePath) {
        var slashIndex = filePath.lastIndexOf('/');
        if (slashIndex > 0) {
            return filePath.slice(0, slashIndex);
        }
        return null;
    },

    /**
     * Wraps path with "/" separator
     * Example:
     * "path/to" --> "/path/to/"
     *
     * @param {String} path
     */
    wrapSeparator: function (path) {
        if (typeof path !== 'string') {
            throw new Error('Invalid path');
        }
        if (path === '') {
            return '/';
        }
        return '/' + path.replace(/^\/+|\/+$/g, '') + '/';
    },

    /**
     * Replaces duplicated slashes/backslashes with singular slash.
     * Also removes leading slash, if it exists.
     * @param {?String} path
     * @returns {String}
     */
    normalizePath: function (path) {
        if (path) {
            return path
                .replace(SLASH_BACKSLASH_REG, '/')
                .replace(FIRST_SLASH_REG, '');
        } else {
            return '';
        }
    }
};

module.exports = fileUtils;




