/**
 * Backgrounds rotation
 * https://st.yandex-team.ru/VB-5700
 */

'use strict';

const alarms = require('../../utils/alarms');
const settings = require('../settings/settings');
const cloudBackgrounds = require('./cloud-backgrounds');
const selector = require('./selector');
const logger = require('../../utils/logger').create('Backgrounds/rotation');

const ALARM = {
    name: 'backgroundsRotation',
    periodInMinutes: 24 * 60,
};

// is rotation enabled
const SETTING_NAME = 'backgroundRotation';

// index of current bg in cloud items
const INDEX_SETTING_NAME = 'backgroundRotationIndex';

/**
 * Init
 */
exports.init = function () {
    alarms.register(ALARM, onAlarm);
};

/**
 * Rotate to next bg
 */
exports.rotate = function () {
    let index = settings.get(INDEX_SETTING_NAME);
    const ids = getCloudIds();
    if (ids.length) {
        index = index < ids.length - 1 ? index + 1 : 0;
        settings.set(INDEX_SETTING_NAME, index);
        logger.info('Rotate to next bg #%i of %i: %s', index, ids.length, ids[index]);
        selector.selectBackground(ids[index]);
    }
};

/**
 * Is rotation selected
 * @returns {Boolean}
 */
exports.isSelected = function () {
    return settings.get(SETTING_NAME);
};

/**
 * Set rotation selected
 * @param {Boolean} value
 */
exports.setSelected = function (value) {
    logger.info('Set selected: %s', value);
    settings.set(SETTING_NAME, Boolean(value));
};

/**
 * Get current background in rotation
 * Required when user clicks to some bg and then click back to rotation preview
 * @returns {String}
 */
exports.getCurrentBackgroundId = function () {
    const index = settings.get(INDEX_SETTING_NAME);
    const ids = getCloudIds();
    if (ids.length && index < ids.length) {
        return ids[index];
    } else {
        return '';
    }
};

function getCloudIds() {
    const items = cloudBackgrounds.getBackgrounds();
    const ids = Object.keys(items);
    ids.sort((a, b) => {
        return items[a].index - items[b].index;
    });
    return ids;
}

function onAlarm() {
    // if checkbox enabled, rotate to next background
    if (exports.isSelected()) {
        exports.rotate();
    }
}



