/**
 * Logic for creating closed tab/window objects.
 */
'use strict';

var urlUtils = require('../../utils/url');
var browserInfo = require('../../utils/browser-info');
var faviconYandex = require('../favicon/yandex');

// All closed items which id starts with "win-" are actually closed windows...
// It's because frontend sends us only ids when it wants to restore some item,
// but we must detect item type, to launch correct logic.
// TODO change frontend API and get rid of this.
var CLOSED_WINDOWS_PREFIX = 'win-';

/**
 * @param {String} id
 * @returns {Boolean}
 */
exports.isWindowId = function (id) {
    return id && id.indexOf(CLOSED_WINDOWS_PREFIX) > -1;
};

/**
 * @param {String} url
 * @param {Object} tabData
 * @param {String} tabData.uuid
 * @param {Number} tabData.windowId
 * @param {String} [tabData.favIconUrl]
 * @param {String} [tabData.title]
 * @param {Object} removeInfo
 * @param {Boolean} removeInfo.isWindowClosing
 * @returns {Object} closedTab
 */
exports.createClosedTab = function (url, tabData, removeInfo) {
    return {
        id: String(tabData.id),
        url: url,
        isWindow: false,
        title: tabData.title || url,
        favicon: getFaviconUrl(tabData, url),
        windowId: tabData.windowId,
        closedTime: Date.now(),
        closedWithWindow: removeInfo.isWindowClosing
    };
};

/**
 * @param {Object} tabData
 * @param {String} url
 */
function getFaviconUrl(tabData, url) {
    var favUrl = tabData.favIconUrl;
    if (!favUrl && urlUtils.isRemote(url)) {
        favUrl = faviconYandex.getUrl(url);
    }
    return favUrl;
}

/**
 * @param {Array<String>} urls
 * @param {Number} windowId
 * @returns {Object} closedWindow
 */
exports.createClosedWindow = function (urls, windowId) {
    //  Firefox      .
    // @see https://st.yandex-team.ru/VB-6125
    if (browserInfo.isFirefox()) {
        urls.reverse();
    }

    return {
        id: CLOSED_WINDOWS_PREFIX + windowId,
        isWindow: true,
        windowId: windowId,
        urls: urls,
        domains: getReadableParts(urls),
        closedTime: Date.now()
    };
};

/**
 * @param {Array<String>} urls
 * @returns {Array<String>}
 */
function getReadableParts(urls) {
    return urls.map(function (url) {
        if (!urlUtils.isSpecialUrl(url)) {
            return urlUtils.getReadableDomain(url)
                || urlUtils.getReadableUrl(url)
                || url;
        }
        return url;
    });
}



