/**
 * 
 */

'use strict';

var utils = require('../../utils/utils');
var _ = require('../../utils/lodash');
var apply = require('./apply');
var candidates = require('./candidates');
var forceThumbs = require('./force-thumbs');
var logger = require('../../utils/logger').create('Pickup');
var alarms = require('../../utils/alarms');
var collection = require('../thumbs/collection');
var clicker = require('../statistics/clicker');
var platformAbout = require('../../platform/about');

var ALARM = {
    name: 'pickup',
    periodInMinutes: 24 * 60
};

module.exports = {
    /**
     * 
     */
    init: function () {
        alarms.register(ALARM, this.run.bind(this));
    },
    /**
     *  
     * 1. ()      
     * 2.   
     * 3.     
     *
     * @param {Object} [options]
     * @param {Boolean} [options.clear]       (   )
     * @param {Boolean} [options.dryRun]      (  )
     * @param {Boolean} [options.fast]   (  )
     * @param {Function} [callback]
     */
    run: function (options, callback) {
        callback = callback || utils.noop;
        options = getRunOptions(options);

        logger.info('Pickup started with options: %j', options);

        // send clicker statistics https://st.yandex-team.ru/VB-5731#1463753525000
        if (!options.dryRun) {
            clicker.send({path: 'system.autopick'});
        }

        if (options.clear && collection.length) {
            logger.info('Collection cleared by pickup');
            collection.clear();
        }

        //    -       
        if (options.clear) {
            setForceThumbs();
        }

        candidates.get(options, function (items) {
            logger.info('Candidates ready, count: %i', items.length);
            if (options.dryRun) {
                logger.info('Pickup ended (dryRun).');
            } else {
                applyCandidates(items, options);
            }
            callback();
        });
    }
};

/**
 *     
 * @param {Object} options
 */
function getRunOptions(options) {
    var result = _.defaults({}, options, {
        clear: !collection.length,
        dryRun: false,
        fast: false,
    });
    //       -, VB-5735
    result.useTopSites = result.clear && platformAbout.isChr;
    return result;
}

/**
 *     
 * @param {Array} items
 * @param {Object} options
 * @param {Function} callback
 */
function applyCandidates(items, options, callback) {
    apply.run(items, options, function () {
        logger.info('Pickup ended.');
        callback();
    });
}

function setForceThumbs() {
    forceThumbs.apply();
}



