'use strict';

var clicker = require('./clicker');
var brandingLoader = require('../branding/remote/loader');

exports.init = function () {
    brandingLoader.onLoadError.addListener(function (bannerId) {
        clicker.send({path: 'system.custombridloadfail.' + bannerId});
    });
};



