/**
 *    
 */
'use strict';

// <link rel="yandex-tableau-widget" href="/manifest.json" />
/*
{
    "version": "2",
    "api_version": 3,
    "layout": {
        "logo": "https://www.linux.org.ru/tango/img/ya-widget.png",
        "color": "#2e3436",
        "show_title": false
    },
}
*/

// <meta name="yandex-tableau-widget" content="logo=http://sitename.tld/images/logo.png, color=#ffffff, feed=feed.json" />

var siteLoader = require('./site-loader');
var urlUtils = require('../../utils/url');
var colorUtil = require('../../utils/colors');
var xhr = require('../../utils/xhr');
var logger = require('../../utils/logger').create('site-info/tableau');
var asyncLib = require('async');
var find = require('lodash/collection/find');
var get = require('lodash/object/get');
var colorUtils = require('../../utils/colors');
var FileStorage = require('../../utils/file-storage');
var ResourceLoader = require('../../utils/resource-loader');

var fileStorage;
var resourceLoader;

var MANIFEST_LINK_REL = 'yandex-tableau-widget';
var VALID_KEYS = ['logo', 'color', 'feed'];

module.exports = {
    TABLEAU_DIR: 'tableau',

    init: function () {
        fileStorage = new FileStorage(module.exports.TABLEAU_DIR);
        resourceLoader = new ResourceLoader();
    },

    getManifest: function (siteUrl, callback) {
        siteUrl = urlUtils.getIndexPart(siteUrl);
        resourceLoader.load({
            id: siteUrl,
            loadMethod: loadMethod.bind(null, siteUrl),
            callback: callback
        });
    },

    clearExpired: function () {
        resourceLoader.clearExpired();
    }
};

function loadMethod(siteUrl, callback) {
    siteLoader.load(siteUrl, function (err, rootNode) {
        if (err) {
            callback(err);
        } else {
            parseSite(siteUrl, rootNode, callback);
        }
    });
}

function parseSite(siteUrl, rootNode, callback) {
    asyncLib.waterfall([
            function (next) {
                next(null, {
                    doWarn: false,
                    siteUrl: siteUrl,
                    domain: urlUtils.getNormalDomain(siteUrl),

                    fileUrl: null,
                    remoteUrl: null,
                    color: null,

                    rootNode: rootNode,

                    manifestUrl: null,
                    text: null,
                    remoteColor: null,
                });
            },

            // test DOM
            function (data, next) {
                // temp keys, used in steps
                var err = !data.rootNode
                    ? new Error('Root node is missing')
                    : null;
                next(err, data);
            },

            // get manifest.json url
            function (data, next) {
                data.manifestUrl = getUrlToManifestJson(data.rootNode, data.siteUrl);
                data.metaContent = getMetaContent(data.rootNode);
                var err = !data.manifestUrl && !data.metaContent
                    ? new Error('No tableau manifest')
                    : null;
                next(err, data);
            },

            // load manifest.json or parse meta tag content attribute to `manifest.json like` object
            function (data, next) {
                if (data.manifestUrl) {
                    xhr({
                        url: data.manifestUrl,
                        ignoreParseError: true,
                        parse: 'json',
                    }, function (err, responseJson) {
                        if (err) {
                            data.doWarn = true;
                        } else {
                            data.json = responseJson;
                        }
                        next(err, data);
                    });
                } else {
                    data.json = parseMetaContent(data.metaContent);
                    next(null, data);
                }
            },

            // parse manifest.json
            function (data, next) {
                var layout = data.json && data.json.layout || {};
                if (!layout.logo) {
                    next(new Error('No logo in tableau manifest'), data);
                } else {
                    var uri = urlUtils.createURI(layout.logo);
                    if (!uri) {
                        next(new Error('Invalid url ' + layout.logo), data);
                    } else {
                        data.remoteUrl = uri.absoluteTo(data.siteUrl);
                        data.remoteColor = layout.color;
                        next(null, data);
                    }
                }
            },

            // download logo image
            function (data, next) {
                fileStorage.download(data.remoteUrl, data.domain, function (err, storageData) {
                    data.doWarn = Boolean(err) || !storageData || !storageData.url;
                    if (storageData) {
                        data.fileUrl = storageData.url;
                        data.filePath = storageData.path;
                    }
                    next(err, data);
                });
            },

            // normalize color
            function (data, next) {
                if (data.remoteColor) {
                    data.color = colorUtil.normalize(data.remoteColor);
                    next(null, data);
                } else {
                    colorUtils.calculateDominantColorByUrl(data.fileUrl, null, function (color) {
                        data.color = color;
                        next(null, data);
                    });
                }
            },
        ],

        function (err, data) {
            if (err) {
                if (data.doWarn) {
                    logger.warn('Error with tableau manifest domain: %s; url: %s %s',
                        data.domain,
                        data.siteUrl,
                        err.message
                    );
                }
                callback(err);
            } else {
                callback(null, {
                    url: data.fileUrl,
                    remoteUrl: data.remoteUrl,
                    color: data.color,
                    filePath: data.filePath
                });
            }
        }
    );
}

function getUrlToManifestJson(data, siteUrl) {
    var manifestHref = extractManifestHref(data);
    var baseUrl = get(data, 'base[0].attributes.href');
    return urlUtils.resolve(siteUrl, baseUrl, manifestHref);
}

function extractManifestHref(data) {
    data = data || {};
    var link = find(data.link, function (linkData) {
        return get(linkData, 'attributes.rel') === MANIFEST_LINK_REL;
    });
    return get(link, 'attributes.href', null);
}

/**
 * @param {Object} data
 * @returns {String}
 */
function getMetaContent(data) {
    data = data || {};
    const meta = find(data.meta, metaData => get(metaData, 'attributes.name') === MANIFEST_LINK_REL);
    return get(meta, 'attributes.content', null);
}

/**
 * @param {String} content
 * @returns {Object}
 */
function parseMetaContent(content) {
    const parts = content.split(',');
    const params = Object.create(null);
    parts.forEach(part => {
        const [key, ...value] = part.trim().split('=');
        if (key && VALID_KEYS.indexOf(key) >= 0) {
            params[key] = value.join('=');
        }
    });
    return {
        api_version: 4,
        layout: {
            logo: params.logo || undefined,
            color: params.color || undefined
        },
        feed: params.feed || undefined
    };
}



