/**
 * Storage for closed tabs/windows
 */
'use strict';

var settings = require('../settings/settings');
var debounce = require('lodash/function/debounce');

var CLOSED_TABS_PREF = 'closedTabs';
var STORE_DELAY_TIME = 1000;

var cache = null;

var storeCacheDelayed = debounce(function () {
    settings.set(CLOSED_TABS_PREF, cache);
}, STORE_DELAY_TIME);

exports.get = function () {
    if (!cache) {
        cache = settings.get(CLOSED_TABS_PREF) || [];
    }
    return cache;
};

exports.set = function (tabs) {
    cache = tabs;
    storeCacheDelayed();
};



