/**
 *  
 * https://beta.wiki.yandex-team.ru/visualbookmarks/tz/homesearchadvert/
 */

'use strict';

var Block = require('../block');
var platformBrowser = require('../../../platform/browser');
var platformAbout = require('../../../platform/about');
var branding = require('../../branding');

var ID = 'vbsetsearch';
var CONFIG = {
    maxShownCount: 3,
    maxRefuseCount: 3,
    duration: 0,
    interval: 30,
    silent: 1,
    afterInstall: platformAbout.id === 'fx' ? 0 : 3, // VB-5282
};

var logger = require('../../../utils/logger').create('Advertisement/' + ID);

var YANDEX_SEARCH_REG = /^(https?:\/\/)?([a-z0-9]+\.)*(ya|yandex)(\.com)?\.[a-z]{2,3}([\/\?#&]|$)/i;

module.exports = function () {
    var block = new Block(ID, CONFIG);
    block.setChecker(checker.bind(null, block));
    block.setAcceptor(acceptor);
    return block;
};

/**
 *    
 */
function checker(block) {
    if (branding.quicksearch) {
        getDefaultSearchProvider(function (defaultSearchProvider) {
            var isYandex = isYandexProvider(defaultSearchProvider);
            block.updateNeedShowing(!isYandex);
        });
    } else {
        logger.warn('Branding package does not contain quicksearch');
        block.updateNeedShowing(false);
    }
}

/**
 *  
 */
function acceptor() {
    if (branding.quicksearch) {
        getDefaultSearchProvider(function (defaultSearchProvider, allSearchProviders) {
            if (!isYandexProvider(defaultSearchProvider)) {
                removeYandexProviders(allSearchProviders);
                setDefaultYandexProvider();
            }
        });
    } else {
        logger.warn('Branding package does not contain quicksearch');
    }
}

/**
 *       
 * @param {Function} callback
 * @param {Object} callback.defaultSearchProvider
 * @param {Array} callback.allSearchProviders
 */
function getDefaultSearchProvider(callback) {
    platformBrowser.getSearchProviders(function (err, info) {
        if (err) {
            logger.error('Can not get SearchProvidersInfo, err %j', err);
        } else {
            logger.info('Got SearchProvidersInfo %j', info);
        }
        info = info || {};

        var defaultSearchProvider = findDefaultSearchProvider(info);
        if (defaultSearchProvider) {
            logger.info('Default search provider %s %j', defaultSearchProvider.displayName, defaultSearchProvider);
        } else {
            logger.info('Default search provider is empty');
        }
        callback(defaultSearchProvider, info.providers || []);
    });
}

function findDefaultSearchProvider(info) {
    if (info && info.defaultId && Array.isArray(info.providers)) {
        return info.providers.filter(function (provider) {
            return provider.id === info.defaultId;
        })[0];
    } else {
        return null;
    }
}

function isYandexProvider(provider) {
    return provider && YANDEX_SEARCH_REG.test(provider.url);
}

/**
 *     
 */
function removeYandexProviders(providers) {
    providers.forEach(function (provider) {
        if (isYandexProvider(provider)) {
            platformBrowser.removeSearchProvider(provider.id);
        }
    });
}

/**
 *    
 */
function setDefaultYandexProvider() {
    var qs = branding.quicksearch;
    var config = {
        url: qs.searchUrl,
        displayName: qs.shortName,
        showSearchSuggestions: true,
        suggestionsUrl: qs.suggestUrl,
        faviconUrl: qs.iconUrl,
    };
    logger.info('Setting Yandex as default search provider %j', config);
    platformBrowser.addDefaultSearchProvider(config);
}



