/*
 * Process background selection
 */

'use strict';

var utils = require('../../utils/utils');
var logger = require('../../utils/logger').create('Backgrounds/selector');
var bgSettings = require('../settings/backgrounds');
var storage = require('./storage');
var fontColors = require('./font-colors');
var brandingBackgrounds = require('./branding-backgrounds');
var cloudBackgrounds = require('./cloud-backgrounds');
var channel = require('../../utils/channel');
var urlUtils = require('../../utils/url');

var selectQueue = {};

/**
 * Select background
 * @param {String} backgroundId
 * @param {Function} callback
 */
exports.selectBackground = function (backgroundId, callback) {
    callback = callback || utils.noop;
    // if user clicks many times on the same background
    if (selectQueue[backgroundId]) {
        logger.info('Selecting background %s is in progress', backgroundId);
        selectQueue[backgroundId].push(callback);
    } else {
        selectQueue[backgroundId] = [callback];
        logger.info('Selecting background: %s', backgroundId);
        select(backgroundId, function (err) {
            selectQueue[backgroundId].forEach(function (callback) {
                callback(err);
            });
            delete selectQueue[backgroundId];
        });
    }
};

/**
 * Selects default background (on install or error)
 */
exports.selectDefaultBackground = function (callback) {
    callback = callback || utils.noop;
    var data = brandingBackgrounds.getDefaultBackground();
    if (data && data.id) {
        exports.selectBackground(data.id, callback);
    } else {
        logger.error('Can not set default background %j', data);
        callback(new Error());
    }
};

exports.onSelectSuccess = channel.create();

function select(backgroundId, callback) {
    var backgroundObject = brandingBackgrounds.getBackground(backgroundId)
        || cloudBackgrounds.getBackground(backgroundId);
    if (backgroundObject) {
        selectBrandedOrCloud(backgroundObject, callback);
    } else {
        selectFromFS(backgroundId, callback);
    }
}

/**
 * Selects branded or cloud background
 * @param {Object} backgroundObject
 * @param {String} backgroundObject.id
 * @param {String} backgroundObject.image
 * @param {String} backgroundObject.color
 * @param {Function} callback
 */
function selectBrandedOrCloud(backgroundObject, callback) {
    var params = {
        id: backgroundObject.id,
        url: backgroundObject.image,
        color: backgroundObject.color,
    };
    storage.getLocalUrl(backgroundObject.id, function (err, data) {
        var localUrl = data && data.url;
        if (localUrl) {
            params.url = localUrl;
            logger.info('Background %s found in local fs: %s', backgroundObject.id, localUrl);
            save(params, callback);
        } else {
            // try download image to local fs
            download(backgroundObject, function (error, localUrl) {
                if (localUrl) {
                    params.url = localUrl;
                    save(params, callback);
                } else {
                    callback(error || new Error('Empty localUrl'));
                }
            });
        }
    });
}

function download(backgroundObject, callback) {
    storage.download(backgroundObject.image, backgroundObject.id, function (error, data) {
        if (error) {
            logger.error(error.message);
            callback(error);
            return;
        }
        var localUrl = data && data.url;
        if (localUrl) {
            logger.info('Downloaded background %s to %s', backgroundObject.id, localUrl);
        } else {
            logger.error('Can not download background %s from %s', backgroundObject.id, backgroundObject.image);
        }
        callback(null, localUrl);
    });
}

function selectFromFS(backgroundId, callback) {
    // try get user bg or previous from fs (here it does not matter)
    storage.getAll(function (err, urls) {
        var localUrl = urls && urls.hasOwnProperty(backgroundId) && urls[backgroundId];
        if (localUrl) {
            // TODO add to url timestamp of image instead of random param.
            localUrl = urlUtils.addRandomParam(localUrl);
            save({
                id: backgroundId,
                url: localUrl,
                color: ''
            }, callback);
        } else {
            logger.error('Background %s not found in local fs', backgroundId);
            callback(new Error());
        }
    });
}

/**
 * Save background data in settings and calc font color
 * @param {Object} params
 * @param {String} params.id
 * @param {String} params.url
 * @param {String} params.color
 * @param {Function} callback
 */
function save(params, callback) {
    bgSettings.setBackgroundImage(params.id);
    bgSettings.setBackgroundImagePath(params.url);
    logger.info('Background %s saved in settings', params.id);
    saveFontColor(params, function () {
        exports.onSelectSuccess.dispatch(params);
        callback();
    });
}

/**
 * Save / calc font color
 * @param {Object} params
 * @param {String} params.id
 * @param {String} params.url
 * @param {String} params.color
 * @param {Function} callback
 */
function saveFontColor(params, callback) {
    if (!params.color) {
        fontColors.get(params.id, params.url, function (color) {
            params.color = color;
            callback();
        });
    } else {
        if (!fontColors.getCached(params.id)) {
            fontColors.setCached(params.id, params.color);
        }
        callback();
    }
}



