'use strict';

var collection = require('./collection');
var logger = require('../../utils/logger').create('ThumbPinner');

exports.pin = function (index) {
    doPinAction(true, index);
};

exports.unpin = function (index) {
    doPinAction(false, index);
};

function doPinAction(pinFlag, index) {
    var thumb = collection.getItem(index);
    if (thumb && Boolean(thumb.pinned) !== pinFlag) {
        logger.info(
            'Thumb %i %s; set pinned state: %s',
            index, thumb.url, pinFlag
        );
        thumb.pinned = pinFlag;
    } else {
        logWarning(thumb);
    }
}

function logWarning(thumb) {
    if (thumb) {
        logger.warn(
            'Incorrect pin attempt, %s %s',
            'thumb already in this pinned state:',
            thumb.pinned
        );
    } else {
        logger.warn(
            'Incorrect pin attempt, %s',
            'thumb does not exist'
        );
    }
}



