/**
 *   
 */
'use strict';

var advertisement = require('../advertisement');
var settings = require('../settings/settings');
var dateUtils = require('../../utils/date');

exports.getState = function () {
    var result = {NOW: formatDate(new Date())};
    result.blocks = advertisement.getAllBlocks().map(function (block) {
        return {
            block: block.getId().toUpperCase(),
            status: getTextStatus(block),
            state: getState(block),
        };
    });
    return result;
};

exports.show = function (blockId) {
    var block = advertisement.getBlockById(blockId);
    var blocks = advertisement.getAllBlocks();
    if (!block) {
        var blocksStr = blocks.map(function (block) {
            return block.getId();
        }).join(', ');
        return 'Block not found! Available blocks: ' + blocksStr;
    }
    block.startShowing();
    //    ,    silent     
    blocks.forEach(function (block) {
        if (block.getId() !== blockId) {
            block._state.lastShown = 0;
            block._state.lastRefuse = 0;
        }
    });
};

function getTextStatus(block) {
    if (block.isShowing()) {
        return block._config.duration
            ? 'showing ' + block._config.duration + ' day(s) until: ' +
               formatDate(block._state.lastShown + block._config.duration * dateUtils.DAY_MS)
            : 'showing infinitly';
    }

    if (block.isSilent()) {
        return 'silent ' + block._config.silent + ' day(s) until: ' +
            formatDate(block._state.lastRefuse + block._config.silent * dateUtils.DAY_MS);
    }

    if (!block.isSilent() && block.isInterval()) {
        return 'interval ' + block._config.interval + ' day(s) until: ' +
            formatDate(block._state.lastRefuse + block._config.interval * dateUtils.DAY_MS);
    }

    if (block._isAfterInstallReached()
        && !block.isShowing()
        && !block.isSilent()
        && !block.isInterval()) {
        return 'normal';
    }

    if (!block._isAfterInstallReached()
        && block._state.shownCount === 0) {
        return 'waiting ' + block._config.afterInstall + ' day(s) after install, until: ' +
            formatDate(settings.get('appInstallTime') * 1000 + block._config.afterInstall * dateUtils.DAY_MS);
    }

    return 'unknown';
}

function getState(block) {
    var dateFields = ['lastRefuse', 'lastShown'];
    return Object.keys(block._state).reduce(function (res, key) {
        if (key === 'data') {
            return res;
        }
        var value = block._state[key];
        if (dateFields.indexOf(key) >= 0 && value > 0) {
            res[key] = formatDate(value);
        } else {
            res[key] = value;
        }
        return res;
    }, {});
}

function formatDate(date) {
    return dateUtils.format(date, '%D.%M.%Y %H:%N');
}



