'use strict';

var deepEqual = require('deep-equal');

function Differ(collection) {
    if (!Array.isArray(collection)) {
        throw new Error('Differ can be initialized with arrays only');
    }

    this.collection = collection.slice();
}

Differ.prototype.update = function (collection) {
    if (!Array.isArray(collection)) {
        throw new Error('Differ can be updated with arrays only');
    }

    var result = {
        changed: [],
        added: [],
        removed: []
    };

    var length = Math.max(this.collection.length, collection.length);

    for (var i = 0; i < length; i++) {
        var currentItem = this.collection[i];
        var newItem = collection[i];

        if (currentItem && newItem && !deepEqual(currentItem, newItem)) {
            result.changed.push(i);
        }

        if (!currentItem && newItem) {
            result.added.push(i);
        }

        if (currentItem && !newItem) {
            result.removed.push(i);
        }
    }

    this.collection = collection.slice();

    return result;
};

module.exports = Differ;



