/**
 * Getting reginfo data (weather + traffic)
 * https://old.wiki.yandex-team.ru/StarDust/Vidzhet.Pogoda#jeksportpogody
 * https://old.wiki.yandex-team.ru/StarDust/VidzhetProbki#jeksportiispolzovanieapikart
 */

'use strict';

const logger = require('../../utils/logger').create('Informers/reginfo');
const xml = require('../../utils/xml');
const urlUtils = require('../../utils/url');
const Informer = require('./informer');
const platformAbout = require('../../platform/about');
const branding = require('../branding');

const URL_TPL = 'https://export.yandex.ru/bar/reginfo.xml?lang={locale}&brid={regionId}';

/**
 * Create
 * @returns {Informer}
 */
exports.create = function () {
    return new Informer({
        url: branding.expandUrl(URL_TPL),
        setting: 'informerReginfo',
        parser: extractData,
        logger: logger,
    });
};

// Parser method inside of an informer is being handled within a Promise,
// therefore a Promise can be returned without a change in functionality.
function extractData(responseXml) {
    return Promise.all([
        extractWeather(responseXml),
        extractTraffic(responseXml)
    ]).then((results) => {
        const [weather, traffic] = results;
        return {weather, traffic};
    }).catch((error) => {
        logger.error('Error during xml data extratingx, %i', error.message);
    });
}

function extractWeather(responseXml) {
    return Promise.all([
        xml.getText(responseXml, 'info > weather > day > title'),
        xml.getText(responseXml, 'info > weather > day > day_part > temperature'),
        xml.getText(responseXml, 'info > weather > day > day_part > image'),
        xml.getText(responseXml, 'info > weather > url')
    ]).then((results) => {
        const [title, temperature, icon, url] = results;
        const result = {title, level: parseInt(temperature, 10), icon, url};
        if (Informer.hasEmptyProps(result)) {
            logger.error('Weather contains empty data, %j %j', result, responseXml);
            return null;
        }
        // add ?from={vbID}
        result.url = urlUtils.setQuery(result.url, {from: platformAbout.type});
        return result;
    }).catch((error) => {
        logger.error('Error on weather xml parsing, %i', error.message);
    });
}

function extractTraffic(responseXml) {
    return Promise.all([
        xml.getText(responseXml, 'info > traffic > title'),
        xml.getText(responseXml, 'info > traffic > region > level'),
        xml.getText(responseXml, 'info > traffic > region > icon'),
        xml.getText(responseXml, 'info > traffic > region > url')
    ]).then((results) => {
        const [title, level, icon, url] = results;
        const result = {title, level: parseInt(level, 10), icon, url};
        if (Informer.hasEmptyProps(result)) {
            logger.error('Traffic contains empty data, %j %j', result, responseXml);
            return null;
        }
        // we have images only for level 0..10
        if (result.level < 0 || result.level > 10) {
            logger.error('Traffic contains incorrect level, %i', result.level);
            return null;
        }
        return result;
    }).catch((error) => {
        logger.error('Error on traffic xml parsing, %i', error.message);
    });
}



