/**
 *     
 */
'use strict';

const getCurrentData = require('../data-serializer');
const backupRestore = require('../restore');
const readAsJson = require('../../../utils/blob-utils').readAsJson;
const fetch = require('../../../utils/fetch');
const Channel = require('../../../utils/channel');

module.exports = {

    /**
     *  data url  
     *
     * ,     data url,   object url  Blob:
     *
     * 1)  -    FF      download,
     *       .
     * {@link https://st.yandex-team.ru/VB-5803}
     * 2)   data url          ..
     * 3) Data url        ,     .
     *
     * @return {String|null}
     */
    make() {
        try {
            const data = JSON.stringify(getCurrentData(), null, 2);
            this.onMade.dispatch();

            return `data:application/x-backup,${encodeURIComponent(data)}`;
        } catch (e) {
            return null;
        }
    },

    /**
     *  
     *
     * @param {String} url
     * @returns {Promise}
     */
    load(url) {
        return this._loadData(url)
            .then(readAsJson)
            .then(backupRestore.restore)
            .then(() => this.onLoaded.dispatch());
    },

    /**
     *    ObjectUrl
     *
     * @param {String} url
     * @returns {Promise}
     * @private
     */
    _loadData: function (url) {
        return fetch(url, {
            logError: true,
            responseType: 'blob'
        });
    },
    onLoaded: new Channel('onLoad'),
    onMade: new Channel('onMade'),
};



