/**
 * Migration 2.28.0
 */

'use strict';

exports.VERSION = '2.28.0';

const logger = require('../../utils/logger').create('Migration ' + exports.VERSION);
const cloudBackgrounds = require('../backgrounds/cloud-backgrounds');
const fontColors = require('../backgrounds/font-colors');

/**
 * @param {Object} options
 * @param {Function} callback
 */
exports.run = function (options, callback) {
    logger.info('Starting...');
    updateBgFontColors();
    callback();
};

/**
 *  2.28       ,       .
 *   .  2.28  ,         
 * (backgroundImagesFontColors)   -     .
 *          
 *
 * https://st.yandex-team.ru/VB-5664
 * https://st.yandex-team.ru/VB-5733
 */
function updateBgFontColors() {
    const fontColorsMap = fontColors.getAll();
    const cloud = cloudBackgrounds.getBackgrounds();
    let hasChanges = false;
    Object.keys(fontColorsMap).forEach(bgId => {
        if (cloud[bgId] && cloud[bgId].color !== fontColorsMap[bgId]) {
            logger.info('Update cached font color for bg: %s %s --> %s', bgId, fontColorsMap[bgId], cloud[bgId].color);
            fontColorsMap[bgId] = cloud[bgId].color;
            hasChanges = true;
        }
    });
    if (hasChanges) {
        fontColors.setAll(fontColorsMap);
    }
}



