/**
 *    ,      
 */

'use strict';

var utils = require('../../utils/utils');
var platformAbout = require('../../platform/about');
var brandedSites = require('./branded-sites');
var collection = require('../thumbs/collection');
var getTopHistory = require('./top-history');
var topSites = require('./top-sites');
var _ = require('../../utils/lodash');
var blacklist = require('../blacklist/blacklist');
var candidatesStorage = require('./candidates-storage');
var safebrowsing = require('../safebrowsing/safebrowsing');
var sorter = require('./sorter');
var logger = require('../../utils/logger').create('Pickup/candidates');

var TOP_HISTORY_PARAMS = {
    minVisits: 3,
    maxResults: 100,
    days: 20
};

var INTERNAL_PROTOCOLS = ['moz-extension', 'about'];
var INTERNAL_URLS = [platformAbout.visbookmarksUrl];

/**
 *      
 * @param {Object} options
 * @param {Boolean} [options.fast]     
 * @param {Boolean} [options.useTopSites]  -
 * @param {Function} callback
 */
exports.get = function (options, callback) {
    getAllCandidates({useTopSites: options.useTopSites}, function (items) {
        filterCandidates({fast: options.fast}, items, function (filteredItems) {
            filteredItems.sort(sorter);
            if (options.useTopSites) {
                // -   ,     ,
                //      
                filteredItems = topSites.setVisits(filteredItems);
            }
            candidatesStorage.save(filteredItems);
            callback(filteredItems);
        });
    });
};

/**
 *       ( )
 * @param {Function} callback
 */
exports.getFromStorage = function (callback) {
    candidatesStorage.get(callback);
};

/**
 *      
 * @param {Object} options
 * @param {Boolean} options.useTopSites    
 * @param {Function} callback
 */
function getAllCandidates(options, callback) {
    options = options || {};
    var branded = brandedSites.get();
    logger.info('Branded sites: %i', branded.length);
    var unpinned = getCurrent({pinned: false});
    logger.info('Current unpinned thumbs: %i', unpinned.length);
    getTopHistory(TOP_HISTORY_PARAMS, function (historyItems) {
        logger.info('Top history count: %i', historyItems.length);
        var items = [].concat(historyItems, branded, unpinned);
        if (options.useTopSites) {
            topSites.get(function (err, topSiteItems) {
                if (Array.isArray(topSiteItems)) {
                    logger.info('Top sites count: %i', topSiteItems.length);
                    items = items.concat(topSiteItems);
                }
                callback(items);
            });
        } else {
            callback(items);
        }
    });
}

/**
 *   
 * @param {Object} options
 * @param {Array} items
 * @param {Function} callback
 */
function filterCandidates(options, items, callback) {
    var filtered = applyFilters(items, [
        setDomain,
        filterEmptyURI,
        filterInternalURI,
        removeSameDomains,
        removePinnedDomains,
    ]);
    removeBlacklistDomains(filtered, function (filtered) {
        removeUnsafeDomains(options, filtered, callback);
    });
}

/**
 *     
 * @returns {Array}
 */
function getCurrent(options) {
    options = _.defaults({}, options, {pinned: false});
    return collection.toArray()
        .filter(function (item) {
            return Boolean(item.thumb.pinned) === options.pinned;
        })
        .map(function (item) {
            return {
                url: item.thumb.url,
                visits: item.thumb.visits,
                position: item.position
            };
        });
}

/**
 *  
 * @param {Array} items
 * @param {Array} filterFns
 */
function applyFilters(items, filterFns) {
    return filterFns.reduce(function (res, fn) {
        return fn(res);
    }, items);
}

/**
 *  URI  
 * @param {array} items
 * @returns {*}
 */
function setDomain(items) {
    return items.map(function (item) {
        item.uri = utils.url.createURI(item.url);
        item.domain = utils.url.getNormalDomain(item.url);
        return item;
    });
}

/**
 *     ,      URI  .
 * @param {array} items
 * @returns {*}
 */
function filterEmptyURI(items) {
    return items.filter(function (item) {
        return Boolean(item.uri) && Boolean(item.domain);
    });
}

/**
 *       ,  
 *    "  ".
 *        .
 * @param {array} items
 * @returns {*}
 */
function filterInternalURI(items) {
    return items.filter(function (item) {
        return INTERNAL_PROTOCOLS.indexOf(item.uri.protocol()) === -1
            && INTERNAL_URLS.indexOf(item.url) === -1;
    });
}

/**
 *          -    .
 *       ,       position  index,
 *     
 * @param {Array} items
 * @returns {Array}
 */
function removeSameDomains(items) {
    var domainGroups = _.groupBy(items, 'domain');
    return _.map(domainGroups, function (group, domain) {
        //    -,   -       
        var mainItemForDomain = _.filter(group, {topSite: true})[0];
        if (!mainItemForDomain) {
            // todo:   isEqualDomains (maps.yandex.ru == harita.yandex.com.tr)   domaingroups.xml
            mainItemForDomain = _.max(group, 'visits');
        }
        if (group.length > 1) {
            var reason = mainItemForDomain.topSite ? 'topsite' : 'maxVisits=' + mainItemForDomain.visits;
            logger.info('Found %i sites for domain %s. Use as main site (%s): %s',
                group.length,
                domain,
                reason,
                mainItemForDomain.url
            );
        }
        group
            .filter(function (item) {
                //  ,        
                return utils.url.isEqualUrls(item.uri, mainItemForDomain.uri);
            })
            .forEach(function (item) {
                /**
                 *         position,
                 *   position       
                 * (      )
                 */
                if (!_.isNumber(mainItemForDomain.position) && _.isNumber(item.position)) {
                    mainItemForDomain.position = item.position;
                }
                /**
                 *         index,
                 *   index       
                 * (      )
                 */
                if (!_.isNumber(mainItemForDomain.index) && _.isNumber(item.index)) {
                    mainItemForDomain.index = item.index;
                }
            });
        return mainItemForDomain;
    });
}

/**
 *  ,     
 */
function removeBlacklistDomains(items, callback) {
    blacklist.createChecker(function (blacklistChecker) {
        items = items.filter(function (item) {
            var inBlacklist = blacklistChecker.contains(item.url);
            if (inBlacklist) {
                logger.info('Blacklisted: %s', item.url);
            }
            return !inBlacklist;
        });
        callback(items);
    });
}

/**
 *  ,        
 */
function removePinnedDomains(items) {
    var pinned = getCurrent({pinned: true});
    pinned = setDomain(pinned);
    var pinnedDomains = _.groupBy(pinned, 'domain');
    return items.filter(function (item) {
        return !(item.domain in pinnedDomains);
    });
}

/**
 *  
 * @param {Object} options
 * @param {Boolean} options.fast
 * @param {Array} items
 * @param {Function} callback
 */
function removeUnsafeDomains(options, items, callback) {
    var domains = items
        //       ,     (  position)
        .filter(function (item) {
            return !(item.branded && _.isNumber(item.position));
        })
        .map(function (item) {
            return item.domain;
        });
    safebrowsing.checkDomains(options, domains, function (err, unsafeDomains) {
        if (err) {
            logger.error(err.message);
            callback(items);
            return;
        }
        if (unsafeDomains && Object.keys(unsafeDomains).length) {
            items = items.filter(function (item) {
                var isUnsafe = item.domain in unsafeDomains;
                if (isUnsafe) {
                    logger.info('Removed as unsafe: %s', item.url);
                }
                return !isUnsafe;
            });
        }
        callback(items);
    });
}



