/**
 *   
 */

'use strict';

var utils = require('../../utils/utils');
var Thumb = require('./thumb');
var storage = require('./storage');
var channel = require('../../utils/channel');
var assert = require('../../utils/assert');
var settings = require('../settings/settings');
var logger = require('../../utils/logger').create('Thumbs collection');

var MAX_THUMBS_COUNT = 25; //   55
var MAX_THUMBS_COUNT_INCREASED = 49; //  77,     1.. . maxAvailableIncreased

//   
var items = [];

var collection = module.exports = {
    init: function () {
        // todo:   ,     thumbs/index.js,
        //        
        // https://github.yandex-team.ru/desktop/visbookmarks-backend/pull/79#discussion_r332215
        storage.init(this);
        return this;
    },

    //       
    onChanged: channel.create(),

    //     
    onLoaded: channel.create(),

    // occurs after failed attempt to save new thumb to collection
    // passes thumb's position as dispatch data
    onUpdateError: channel.create(),

    /**
     *    
     */
    loadFromDB: function (callback) {
        storage.load(function (err, thumbs) {
            if (err) {
                logger.error('Can not load from db %j', err);
                callback(err);
            } else {
                logger.info('Loaded thumbs from db: %i', thumbs.length);
                //       onChanged, .    storage,
                //        
                collection.onChanged.mute();
                var logData = thumbs.map(function (thumb, position) {
                    collection.setItem(position, thumb);
                    return `${position} ${thumb.url}${thumb.pinned ? ' (pinned)' : ''}`;
                }).join('; ');

                logger.info(logData);

                collection.onChanged.unmute();
                collection.onLoaded.dispatch(collection);
                callback();
            }
        }.bind(this));
    },

    // [deprecated] use collection.items instead
    toArray: function () {
        return items.map(function (thumb, position) {
            return {position: position, thumb: thumb};
        });
    },

    get items() {
        return items;
    },

    get length() {
        return items.length;
    },

    get maxLength() {
        return settings.get('maxAvailableIncreased')
            ? MAX_THUMBS_COUNT_INCREASED
            : MAX_THUMBS_COUNT;
    },

    clear: function () {
        logger.info('Cleared');
        items.length = 0;
        return this;
    },

    getItem: function (position) {
        return items[position];
    },

    /**
     * Sets thumb to passed position
     * Replaces current thumb on this position
     * @param {Number} position
     * @param {Thumb} thumb
     */
    setItem: function (position, thumb) {
        return this._appendItem(position, thumb, function () {
            items[position] = thumb;
        });
    },

    /**
     * Inserts thumb to passed position
     * Does not replace current thumb on this position,
     * just moves all thumbs after position to right
     * @param {Number} position
     * @param {Thumb} thumb
     */
    insertItem: function (position, thumb) {
        return this._appendItem(position, thumb, function () {
            items.splice(position, 0, thumb);
        });
    },

    /**
     *  
     */
    removeItem: function (position) {
        if (position >= 0 && position < this.length) {
            var removedThumbs = items.splice(position, 1);
            if (removedThumbs.length) {
                removeListenersFromThumb(removedThumbs[0]);
            }
            this.onChanged.dispatch(this);
        } else {
            logger.warn('Try to remove nonexistent position %i', position);
        }
        return this;
    },

    /**
     *     
     * @param {Object} options
     * @param {Object} options.force    
     * @param {Array} options.positions   
     */
    loadMissingData: function (options) {
        options = options || {};
        logger.info('Loading missing data with options %j', options);
        var positions = Array.isArray(options.positions) ? options.positions : Object.keys(this.items);
        positions.forEach(function (position) {
            var thumb = this.getItem(position);
            if (thumb) {
                thumb.update(Boolean(options.force));
            }
        }, this);
    },

    /**
     *       
     * @param {String} normalDomain
     */
    hasDomain: function (normalDomain) {
        return items.some(function (thumb) {
            return utils.url.getNormalDomain(thumb.url) === normalDomain;
        });
    },

    /**
     *       
     * @param {String} url
     */
    hasUrl: function (url) {
        return items.some(function (thumb) {
            return utils.url.isEqualUrls(thumb.url, url);
        });
    },

    _appendItem: function (position, thumb, appendAction) {
        assert.type(thumb, Thumb, 'Thumb param should be instance of Thumb!');
        if (position >= 0) {
            appendAction();
            addListenersToThumb(thumb);
            this.onChanged.dispatch(this);
        } else {
            // only waring for bad position, because it can happen in runtime
            // assert on type - it's for developers happiness
            logger.warn('Try to append item to incorrect position %i', position);
        }
        return this;
    },
};

/**
 * Adds onChanged listener to thumb, if there is no one
 * {Thumb} thumb
 */
function addListenersToThumb(thumb) {
    if (!thumb.onChanged.hasListener(onItemChanged)) {
        thumb.onChanged.addListener(onItemChanged);
    }
}

/**
 * Removes onChanged listener from thumb
 */
function removeListenersFromThumb(thumb) {
    thumb.onChanged.removeListener(onItemChanged);
}

/**
 *   
 */
function onItemChanged() {
    collection.onChanged.dispatch(collection);
}



