/**
 * ,         UI
 */
'use strict';

var messaging = require('./messaging');
var backgrounds = require('../backgrounds');
var bgSelector = require('../backgrounds/selector');
var userBackground = require('../backgrounds/user-background');
var bgRotation = require('../backgrounds/rotation');
var events = require('./events');

exports.init = function () {
    messaging.on(events.frontend.UPLOAD_USER_BACKGROUND, uploadUserBackground);
    bgSelector.onSelectSuccess.addListener(backgroundChanged);
};

exports.getCurrent = function () {
    var data = backgrounds.requestCurrent() || {};

    //    data ,             ,
    //     ,      

    return {
        id: data.id || '',
        image: data.image || '',
        color: data.color || ''
    };
};

exports.getAll = function (callback) {
    backgrounds.requestAll(function (err, items) {
        if (err || !Array.isArray(items)) {
            callback(null, []);
        } else {
            // todo: format items for frontend here
            callback(null, items);
        }
    });
};

exports.setBackgroundImage = function (req, sender) {
    var backgroundId = req.value;

    if (!backgroundId) {
        return;
    }

    bgSelector.selectBackground(backgroundId, function (err) {
        var tabId = sender && sender.tab.id;
        if (err && tabId) {
            messaging.send(events.backend.BACKGROUND_CHANGED, {error: true}, tabId);
        } else {
            // disable background rotation if user selects background manually, VB-5858
            if (bgRotation.isSelected()) {
                bgRotation.setSelected(false);
            }
        }
    });
};

function uploadUserBackground(req, sender, sendResponse) {
    // FF and Chrome send req.url param with dataObjectUrl.
    // IE sends req.path param with path to file in user's file system.
    if (req.url) {
        userBackground.download(req.url, sendResponse);
    } else {
        userBackground.copy(req.path, sendResponse);
    }
    return true;
}

function backgroundChanged(data) {
    if (data) {
        messaging.send(events.backend.BACKGROUND_CHANGED, {
            id: data.id,
            image: data.url,
            color: data.color
        });
    }
}



