/**
 * Clears file storage by deleting files which no longer used in any of modules
 */
'use strict';

var parallel = require('async').parallel;
var series = require('async').series;
var flatten = require('lodash/array/flatten');

var logger = require('../../utils/logger').create('Maintenance');
var alarms = require('../../utils/alarms');
var fs = require('../../platform/file-storage');
var fileUtils = require('../../utils/file-utils');

var collection = require('../thumbs/collection');
var screenshots = require('../screenshots/screenshots');
var cloudLogo = require('../cloud-logo');
var titleLoader = require('../site-info/title');
var faviconLoader = require('../favicon');
var tableauLoader = require('../site-info/tableau');
var customLogo = require('../custom-logo');

var ALARM = {
    name: 'maintenance',
    periodInMinutes: 2 * 24 * 60,
};

var DIRS = [
    require('../screenshots/screenshots').SCREENSHOT_DIR,
    require('../cloud-logo/saver').CLOUD_LOGO_DIR,
    require('../cloud-logo/saver').CLOUD_LOGO_SUB_DIR,
    tableauLoader.TABLEAU_DIR,
    faviconLoader.FAVICON_DIR,
    require('../custom-logo/logo-saver').CUSTOM_LOGO_DIR,
];

exports.init = function () {
    alarms.register(ALARM, exports.run);
};

exports.run = function (callback) {
    /**
     *      `async.series`  `async.parallel`,
     *       :
     *   maintenance "after each" hook:
     *   Uncaught TypeError: Cannot read property 'ls' of undefined
     * ,    -    ,   -     .
     *    fs.list,     filer.ls.
     *  fs.list   ,    .
     */
    parallel(DIRS.map(function (dir) {
        return getFileList.bind(null, dir);
    }), function (_error, allFiles) {
        clearCaches();
        deleteUnusedFiles(flatten(allFiles), callback);
    });
};

function clearCaches() {
    logger.info('Clear caches');
    screenshots.clearCache();
    cloudLogo.clearExpired();
    titleLoader.clearExpired();
    faviconLoader.clearExpired();
    tableauLoader.clearExpired();
}

function getFileList(folder, callback) {
    fs.list(fileUtils.wrapSeparator(folder), function (error, files) {
        if (error) {
            logger.error('Error while listing files in folder: %s, error: %j', folder, error);
        }
        callback(null, files || []);
    });
}

function deleteUnusedFiles(allFiles, callback) {
    var filesInUse = getFilesInUse();
    logger.info('Going to delete unused files');
    logger.info('All files: %j', allFiles);
    logger.info('Files in use: %j', filesInUse);
    var deleteTasks = createDeleteTasks(filesInUse, allFiles);
    series(deleteTasks, callback);
}

function createDeleteTasks(filesInUse, allFiles) {
    return allFiles
        .reduce(function (deleteTasks, path) {
            var normalizedPath = fileUtils.normalizePath(path);
            if (filesInUse.indexOf(normalizedPath) === -1) {
                deleteTasks.push(deleteFile.bind(null, path));
            }
            return deleteTasks;
        }, []);
}

function getFilesInUse() {
    return getThumbsFilesInUse()
        .concat(getCustomLogoFilesInUse())
        .filter(Boolean)
        .map(fileUtils.normalizePath);
}

function getCustomLogoFilesInUse() {
    return customLogo.getAll()
        .reduce(function (filesInUse, customLogo) {
            var tillTimestamp = new Date(customLogo.till).setHours(23, 59, 59, 999);
            if (tillTimestamp >= Date.now()) {
                return filesInUse.concat([
                    customLogo.localPath
                ]);
            }
            return filesInUse;
        }, []);
}

function getThumbsFilesInUse() {
    return collection.items
        .reduce(function (filesInUse, thumb) {
            return filesInUse.concat([
                thumb.cloudImageFsPath,
                thumb.cloudImageSubFsPath,
                thumb.tableauFsPath,
                thumb.faviconFsPath,
                thumb.screenshotFsPath
            ]);
        }, []);
}

function deleteFile(path, callback) {
    logger.info('Delete file: %s', path);
    fs.remove(path, function (error) {
        if (error) {
            logger.error('Error while deleting file: %s, error: %j', path, error);
        }
        callback();
    });
}



