/**
 *     
 */

'use strict';

var branding = require('./');
var pickup = require('../pickup/pickup');
var initSender = require('../frontend/init-sender');
var logger = require('../../utils/logger').create('Branding applier');
var runstate = require('../startup/runstate');
var bgSelector = require('../backgrounds/selector');
var asyncLib = require('async');
var noargs = require('../../utils/utils').noargs;

/**
 * Init
 */
exports.init = function () {
    branding.onRemoteLoaded.addListener(onRemoteLoaded);
};

function onRemoteLoaded() {
    //     ,     (        )
    if (runstate.isReady()) {
        update();
    }
}

function update() {
    logger.info('Applying new branding data...');
    asyncLib.parallel([
        //  
        function (callback) {
            bgSelector.selectDefaultBackground(noargs(callback));
        },
        //  
        function (callback) {
            pickup.run({clear: true}, noargs(callback));
        }
    ], function () {
        logger.info('Update all frontends');
        initSender.sendAll();
    });
}



