/**
 *     banner-id
 *
 *   bannerid:
 * 1.   yandex.ru
 * 2.    : javascript:document.cookie='ys=bnrd.12345;domain=yandex.ru';
 */
'use strict';

var logger = require('../../utils/logger').create('cookies bannerid');
var settings = require('../settings/settings');
var branding = require('../branding');
var BannerID = require('yandex-elements-core/cookies/banner-id');
var YsCookie = require('yandex-elements-core/cookies/ys-cookie');

var BANNER_ID_PREF = 'bannerid';

/**
 * @returns {Promise}
 */
exports.processCookie = function () {
    var bannerId = exports.getBannerId();
    return bannerId
        ? Promise.resolve(bannerId)
        : createBannerId().run().then(saveBannerId);
};

/**
 * @returns {String}
 */
exports.getBannerId = function () {
    return settings.get(BANNER_ID_PREF);
};

/**
 * @returns {BannerID}
 */
function createBannerId() {
    var domains = branding.bannerid.domains.map(function (domain) {
        return branding.expandUrl(domain);
    });
    logger.info('Search bannerid in ys cookie on domains: %s', domains.join(', '));
    return new BannerID({domainsQueue: domains}, YsCookie);
}

/**
 * @param {String} [bannerId]
 */
function saveBannerId(bannerId) {
    logger.info('Banner id value from cookie: %s', bannerId);
    if (bannerId) {
        settings.set(BANNER_ID_PREF, bannerId);
    }
    return bannerId;
}



