'use strict';

var logger = require('../../utils/logger').create('Clicker');
var branding = require('../branding');
var settings = require('../settings/settings');
var platformAbout = require('../../platform/about');
var buildInfo = require('../../platform/build-info');
var merge = require('lodash/object/merge');
var xhr = require('../../utils/xhr');
var curly = require('../../utils/curly');
var URL_TMPL = 'https://yandex.{regionTld}/clck/click/dtype=stred/pid={pid}/cid={cid}/path={path}{vars}/*';

var PID = 12;
var PATH_DELIMITER = '.';

var XHR_OPTIONS = {
    withCredentials: true,
    bypassCache: true
};

/**
 *         .
 *
 * @param {Object} options
 * @param {String} options.path
 * @param {String} [options.vars]
 * @param {String} [options.pid]
 * @param {String} [options.cid]
 * @param {Boolean} [options.force]          .
 * @param {String} [options.transport]   (xhr|img)  ,     host permissions (VB-6220)
 *
 * @link https://wiki.yandex-team.ru/stardust/tablo/normal/#parametryvclicker
 */
exports.send = function (options) {
    if (!(options && typeof options === 'object')) {
        logger.warn('Wrong options argument: %s', options);
        return;
    }

    if (options.force || isStatisticsAllowed()) {
        forceSend(options);
    }
};

exports.getPlatformVersion = function () {
    var rawVersion = buildInfo.getVersion();
    // clicker wants version with '_' instand of '.'
    return rawVersion.replace(/\./g, '_');
};

function forceSend(options) {
    var params = createStatParams(options);
    if (params) {
        logger.info('Sending path: %s vars: %s pid: %s cid: %s',
                    params.path, params.vars, params.pid, params.cid);
        /**
         *  insertVars,   insertUrlVars ..       vars
         * https://st.yandex-team.ru/VB-5681
         */
        var url = curly.insertVars(URL_TMPL, params);
        if (options.transport === 'img') {
            new Image().src = url;
        } else {
            xhr(merge({url: url}, XHR_OPTIONS));
        }
    }
}

function isStatisticsAllowed() {
    //     Firefox      .
    // @see {@link https://st.yandex-team.ru/VB-5060}
    return platformAbout.id !== 'fx' || settings.get('sendUsageStat');
}

function createStatParams(options) {
    var path = options.path;

    if (!path) {
        logger.warn('Empty path');
        return null;
    }

    var pid = options.pid || PID;
    var cid = options.cid || platformAbout.cid;
    var vars = options.vars;

    path = Array.isArray(path) ? path.join(PATH_DELIMITER) : path;

    //  CID ,  path    ,
    //   path   
    if (cid === platformAbout.cid) {
        path = [exports.getPlatformVersion(), path].join(PATH_DELIMITER);
    }

    return {
        regionTld: branding.vars.regionTld,
        pid: pid,
        cid: cid,
        path: path,
        vars: vars ? '/vars=' + vars : ''
    };
}



