/**
 * Sends user hosts permissions statistics
 * @see https://st.yandex-team.ru/VB-6220
 */
'use strict';

var clicker = require('./clicker');
var hostsPermissions = require('../host-permissions');

exports.init = function () {
  hostsPermissions.onRestrictedByUser.addListener(sendStat);
};

function sendStat() {
  clicker.send({ path: 'system.hostPermissions.restrictedByUser', transport: 'img' });
}



