/**
 * Messages exchange with frontend
 * Messages going from background to frontend has structure: {type: 'someCommand', data: 'someData'}
 * Messages going from frontend to background has structure: {action: 'someAction', param1: 'value1', ...}
 */

'use strict';

var utils = require('../../utils/utils');
var logger = require('../../utils/logger').create('Frontend/messaging');
var formatMessage = require('../../utils/format-message');

// flag: are messages going through
var isRunning = false;

var listeners = {};

/**
 * Run messaging
 */
exports.run = function () {
    isRunning = true;
    chrome.runtime.onMessage.addListener(onMessage);
    logger.info('Start messaging');
};

/**
 * Send message to frontend
 *
 * @param {String} command
 * @param {*} data
 * @param {Number} [tabId]
 */
exports.send = function (command, data, tabId) {
    if (!isRunning) {
        // logger.error('Can not send message %s as module is not running', command);
        return;
    }

    var msgBody = {
        type: command,
        data: data
    };

    if (tabId) {
        logger.info('Message sent to frontend in tab %i: %s %j', tabId, command, data);
        chrome.tabs.sendMessage(parseInt(tabId, 10), msgBody);
    } else {
        logger.info('Message sent to all frontends: %s %j', command, data);
        // Pass noop to stop message passing to background page self (chrome-only)
        chrome.runtime.sendMessage(msgBody, utils.noop);
    }
};

/**
 * Listen message from frontend
 *
 * @param {String} action
 * @param {Function} fn
 */
exports.on = function (action, fn) {
    if (typeof fn !== 'function') {
        throw new Error(formatMessage('Function required for listener: %s', action));
    }

    // Only ONE listener per message (because listener return value defines sync/async sendResponse call)
    // see: https://developer.chrome.com/extensions/runtime#event-onMessage
    if (listeners[action]) {
        throw new Error(formatMessage('Dublicate action listener: %s', action));
    } else {
        listeners[action] = fn;
    }
};

function onMessage(req, sender, sendResponse) {
    if (!isRunning) {
        logger.error('Can not process frontend action %j as module is not running', req);
        return;
    }

    var action = req && req.action;
    if (!action) {
        //     action, ..      
        // logger.error('Empty action: %j ', req);
        return;
    }

    if (listeners[action]) {
        var sendResponseSafe = wrapSendResponse(action, sendResponse);
        return listeners[action](req, sender, sendResponseSafe);
    } else {
        logger.error('Listener not found for action: %s', action);
    }
}

/**
 *  sendResponse  try..catch
 *     Attempting to use a disconnected port object,
 *       (,      , VB-3562)
 * @param {String} action
 * @param {Function} sendResponse
 */
function wrapSendResponse(action, sendResponse) {
    return function () {
        try {
            // We can't use apply directly on sendResponse in IE
            Function.prototype.apply.call(sendResponse, null, arguments);
        } catch (e) {
            logger.warn('sendResponse failed for action %s: %s', action, e.message);
        }
    };
}



