/**
 *      dayuse .
 *   dayusePoller ,       3  ,
 *      ,  .
 *        
 */
'use strict';

const alarms = require('../../utils/alarms');
const clicker = require('./clicker');
const bgRotation = require('../backgrounds/rotation');
const informers = require('../informers');

var ALARM = {
    name: 'dayuseSimple',
    periodInMinutes: 24 * 60,
    delayInMinutes: 1,
};

/**
 * Init
 */
exports.init = function () {
    alarms.register(ALARM, onAlarm);
};

function onAlarm() {
    sendRotationStat();
    sendInformersStat();
}

function sendRotationStat() {
    if (bgRotation.isSelected()) {
        clicker.send({path: 'system.dailyback.dayuse'});
    }
}

function sendInformersStat() {
    if (informers.isSupported() && informers.isEnabled()) {
        clicker.send({path: 'system.informers.dayuse'});
    }
}



