/**
 *      
 */

'use strict';

var versions = require('./versions');
var collection = require('../thumbs/collection');
var runstate = require('./runstate');
var migration = require('../migration');
var pickup = require('../pickup/pickup');
var installUpgrade = require('./install-upgrade');
var logger = require('../../utils/logger').create('Startup/upgrade');

/**
 * preInit
 * @param {Function} callback
 */
exports.preInit = function (callback) {
    installUpgrade.preInit(callback);
};

/**
 * Run
 * @param {Function} callback
 */
exports.run = function (callback) {
    logger.info('Upgrading from %s to %s', versions.getPrevious(), versions.getCurrent());

    migration.run({
        previousVersion: versions.getPrevious(),
        currentVersion: versions.getCurrent(),
        migrationData: runstate.getMigrationDataCommonBackend(),
    }, function (err) {
        if (err) {
            logger.error('Error during migration %j', err);
        } else {
            versions.addToHistory(versions.getCurrent());
        }
        collection.loadFromDB(function () {
            //     dryRun -     ,   , VB-5132
            pickup.run({dryRun: true});
            //    
            collection.loadMissingData({force: true});
            installUpgrade.run();
            callback();
        });
    });
};



