/**
 * Getting currency data
 * https://old.wiki.yandex-team.ru/visualbookmarks/tz/informers
 */

'use strict';

const logger = require('../../utils/logger').create('Informers/quotes');
const xml = require('../../utils/xml');
const curly = require('../../utils/curly');
const Informer = require('./informer');

// server url for currency depends on id only
const URL_TPL = 'https://ya.ru/search/?lr=174993&text={text}&wiz=finance'

/**
 * Create currency informer (exact quote id is auto-detected by server)
 * @returns {Informer}
 */
exports.create = function () {
    return new Informer({
        url: 'https://export.yandex.ru/bar/quotes.xml',
        setting: 'informerQuotes',
        parser: extractData,
        logger: logger,
    });
};

function extractData(responseXml) {
    return Promise.all([
        xml.getAttr(responseXml, 'quotes > quote', 'name'),
        xml.getAttr(responseXml, 'quotes > quote', 'id'),
        xml.getText(responseXml, 'quotes > quote > value'),
        xml.getText(responseXml, 'quotes > quote > change')
    ]).then((results) => {
        const [name, _id, value, change] = results;
        const code = getCurrencyCode(name);
        if (!code) {
            logger.warn('Currency name does not contain USD|EUR: %s', name);
            return null;
        }
        const result = {
            value: parseFloat(value),
            change: parseFloat(change),
            code: code,
            url: curly.insertUrlVars(URL_TPL, {text: name}),
        };
        if (Informer.hasEmptyProps(result)) {
            logger.error('Quote contains empty data, %j %j', result, responseXml);
            return null;
        } else {
            return result;
        }
    }).catch((error) => {
        logger.error('Excract xml data error, %j', error.messsage);
    });
}

function getCurrencyCode(name) {
    if (/usd/i.test(name)) {
        return 'usd';
    }
    if (/eur/i.test(name)) {
        return 'eur';
    }
    return '';
}



