'use strict';

var logger = require('../utils/logger').create('Platform/clid');
var buildInfo = require('./build-info');
var browserInfo = require('../utils/browser-info');
var isPlainObject = require('lodash/lang/isPlainObject');

class ClidStorage {
    constructor() {
        this.storage = new Map();
    }

    setItem(key, value) {
        this.storage.set(key, String(value));
    }

    getItem(key) {
        return this.storage.has(key) ? this.storage.get(key) : null;
    }

    removeItem(key) {
        this.storage.delete(key);
    }

    clear() {
        this.storage.clear();
    }

    key(index) {
        const keys = Array.from(this.storage.keys());
        return keys[index] || null;
    }

    get length() {
        return this.storage.size;
    }
}

const clidStorage = new ClidStorage();

const CLID_STORAGE_KEY_CHROME_PREFIX = 'clid';
const CLID_STORAGE_KEY_PREFIX = 'yandex.statistics.clid.';

exports.init = function (callback) {
    if (browserInfo.isFirefox()) {
        importChromeStorageClids(callback, window.localStorage);
    } else {
        importChromeStorageClids(callback, clidStorage);
    }
};

exports.getByName = function (name) {
    var number = getClidNumberByName(name);
    return exports.getByNumber(number);
};

exports.getByNumber = function (number) {
    return getClidFromStorage(number) || getClidFromBuild(number);
};

/**
 *   ( )   .
 *     xml,  -  localStorage
 *   :      ,    , ..  ,
 *    .
 * @param {Integer} number
 */
exports.getPortalClidByNumber = function (number) {
    var clidFromStorage = getClidFromStorage(number);
    return clidFromStorage ? '' : getClidFromBuild(number);
};

/**
 *      
 * @param {Integer} number
 * @returns {String}
 */
function getClidFromBuild(number) {
    var clids = buildInfo.getClids() || {};
    var name = getClidNameByNumber(number);
    return clids[name] || '';
}

/**
 *    localStorage (   )
 * @param {Integer} number
 * @returns {String}
 */
function getClidFromStorage(number) {
    var keyName = CLID_STORAGE_KEY_PREFIX + number;
    var clid = browserInfo.isChrome() ? clidStorage.getItem(keyName) :
        window.localStorage.getItem(keyName);
    return filterClidValue(clid, keyName);
}

function filterClidValue(clid, keyName) {
    clid = String(clid || '').trim().replace(/["']/g, '');

    if (clid && !/^[a-z0-9-]+$/.test(clid)) {
        logger.error('Invalid clid %s from storage: %s', keyName, clid);
        clid = '';
    }

    return clid;
}

function getClidNameByNumber(number) {
    return 'clid' + number;
}

function getClidNumberByName(name) {
    return String(name).replace('clid', '');
}

/**
 *   $profile/browser-extension-data/vb@yandex.ru/storage.js
 *     `chrome.storage.local`.  
 *   `window.localStorage`.     ,  
 *    `window.localStorage`.
 * @see https://st.yandex-team.ru/VB-6144
 * @param {Function} callback
 * @param {Object} storage
 */
function importChromeStorageClids(callback, storage) {
    chrome.storage.local.get(null, values => {
        if (!isPlainObject(values)) {
            logger.error('values is not an object');
            values = Object.create(null);
        }

        //         .
        //       try/catch.
        try {
            Object.keys(values)
                .filter(key => (
                    key.indexOf(CLID_STORAGE_KEY_PREFIX) === 0 ||
                    storage instanceof ClidStorage &&
                    key.indexOf(CLID_STORAGE_KEY_CHROME_PREFIX) === 0
                ))
                .map(key => {
                    var clid = filterClidValue(values[key], key);
                    if (clid && storage.getItem(key) === null) {
                        storage.setItem(key, clid);
                    }
                });
        } catch (err) {
            logger.error('Error importing clids from chrome storage %s', err);
        }

        callback();
    });
}



