/**
 * Holds parsers for xhr module
 */
'use strict';

var xml = require('./xml');

module.exports = {
    text: function () {
        return function (evt) {
            return evt.target.responseText;
        };
    },

    // Don't use responseType = json, because it throws on .responseText:
    // The value is only accessible if the object's 'responseType' is '' or 'text' (was 'json').
    json: function () {
        return function (evt) {
            return JSON.parse(evt.target.responseText);
        };
    },

    xml: function () {
        return function (evt) {
            return evt.target.responseXML
                || xml.fromString(evt.target.responseText);
        };
    },

    blob: function (xhr) {
        xhr.responseType = 'blob';
    }
};



