/**
 *     
 */
'use strict';

var traverse = require('traverse');
var _ = require('../../utils/lodash');
var i18n = require('../i18n');
var curly = require('../../utils/curly');
var platformBuildInfo = require('../../platform/build-info');
var platformBranding = require('../../platform/branding');
var remoteBrandingStorage = require('./remote/storage');

var I18N_REGEXP = /\{@([^}]+)\}/g;
var ASSETS_PLACEHOLDER = '__assets__';
var MERGE_INFO_KEY = 'mergeInfo';

/**
 *        .
 *        merge:
 * -  
 * -  ,   
 * -  ,    ( bannerid)
 *
 * @param {String} name
 * @param {Object} [options]
 * @param {Object} [options.i18n]
 * @param {Function} [options.expandUrl]
 */
exports.getMerged = function (name, options) {
    options = options || {};
    var regionBlock = extractBlock(getRegionData(), name, Boolean(options.i18n));
    var customBlock = extractBlock(getCustomData(), name, Boolean(options.i18n));
    var remoteBlock = extractBlock(remoteBrandingStorage.getData(), name);
    var block = _.assign({}, regionBlock);
    mergeBlocks(block, customBlock);
    mergeBlocks(block, remoteBlock);
    if (options.i18n || options.expandUrl) {
        replacePlaceholders(block, options);
    }
    return block;
};

/**
 *     
 */
exports.getNames = function () {
    return _.uniq([]
        .concat(Object.keys(getRegionData()))
        .concat(Object.keys(getCustomData()))
        .concat(Object.keys(remoteBrandingStorage.getData()))
    );
};

function extractBlock(data, name, hasAssets) {
    var block = data[name];
    if (hasAssets) {
        resolveAssets(block, data);
    }
    return block;
}

function getRegionData() {
    return platformBranding.getData() || {};
}

function getCustomData() {
    var brandId = platformBuildInfo.getCustomBrandId();
    return brandId
        ? platformBranding.getData(brandId) || {}
        : {};
}

function replacePlaceholders(block, options) {
    traverse(block).forEach(function (item) {
        if (typeof item === 'string') {
            //   {@key} --> value
            var newItem = replaceI18n(item, options.i18n);
            //      
            if (options.expandUrl && /^https?:\/\//.test(newItem)) {
                newItem = options.expandUrl(newItem);
            }
            if (newItem !== item) {
                this.update(newItem);
            }
        }
    });
}

function replaceI18n(str, i18nData) {
    var localeData = i18nData && i18nData[i18n.locale];
    return curly.insertVars(str, localeData, {regex: I18N_REGEXP});
}

/**
 *   __assets__       
 */
function resolveAssets(block, brandingData) {
    if (brandingData.vars && brandingData.vars.brandId) {
        var assetsPath = brandingData.vars.brandId + '/assets';
        traverse(block).forEach(function (item) {
            if (typeof item === 'string' && item.indexOf(ASSETS_PLACEHOLDER) === 0) {
                var itemPath = item.replace(ASSETS_PLACEHOLDER, assetsPath);
                var itemUrl = platformBranding.getFileUrl(itemPath);
                this.update(itemUrl);
            }
        });
    }
}

/**
 *      ,    arrayMergeInfo.
 * arrayMergeInfo   :
 * {
 *   items1: 'merge',
 *   items2: 'insertFirst',
 * }
 *
 * @param {Object} to
 * @param {Object} from
 */
function mergeBlocks(to, from) {
    from = from || {};
    var mergeInfo = from[MERGE_INFO_KEY] || {};
    Object.keys(from).forEach(function (key) {
        if (key === MERGE_INFO_KEY) {
            return;
        }
        var fromValue = from[key];
        if (fromValue === undefined) {
            return;
        }
        if (fromValue && typeof fromValue === 'object') {
            if (Array.isArray(fromValue)) {
                var mergeType = mergeInfo[key] || 'merge';
                to[key] = combineArrays(to[key] || [], fromValue, mergeType);
            } else {
                //     
                // (     i18n)
                to[key] = _.merge(to[key] || {}, fromValue);
            }
        } else {
            to[key] = fromValue;
        }
    });
}

/**
 *      mergeType:
 * - merge (default)
 * - insertFirst
 * - insertLast
 * - overwrite
 *
 * @param {Object|Array} leftValue
 * @param {Object|Array} rightValue
 * @param {String} mergeType
 */
function combineArrays(leftValue, rightValue, mergeType) {
    switch (mergeType) {
        case 'overwrite':
            return rightValue;
        case 'insertFirst':
            return rightValue.concat(leftValue);
        case 'insertLast':
            return leftValue.concat(rightValue);
        // merge
        default:
            //  assign, .. _.merge      
            return _.assign(leftValue, rightValue);
    }
}



