/**
 *    ,  
 */
'use strict';

var asyncLib = require('async');
var flatten = require('lodash/array/flatten');
var weights = require('./weights');
var isRelease = require('../debug/build-data').git.release;
var dateUtils = require('../../utils/date');
var logger = require('../../utils/logger').create('url-suggest/search-history');
var browserInfo = require('../../utils/browser-info');

var NATIVE_SEARCH_MAX_COUNT = 100;
var CUSTOM_SEARCH_MAX_COUNT = 4000;
var OUTPUT_COUNT = 10;

/**
 * Searches browser history, returns items sorted by weights
 * @param {String} query
 * @param {Function} callback
 */
module.exports = function (query, callback) {
    historySearch(query, function (err, historyItems) {
        if (err) {
            logger.error('Error in history search: %j', err);
            callback(err);
        } else {
            var items = filterByWeights(query, historyItems);
            printInDebug(query, items);
            callback(null, items);
        }
    });
};

/**
 * @param {String} query
 * @param {Function} callback
 */
function historySearch(query, callback) {
    var tasks = [nativeHistorySearch.bind(null, query)];
    if (browserInfo.isChrome()) {
        // Use custom history search for chrome because of
        // chrome.history.search's bad behavior with small queries.
        // @see https://st.yandex-team.ru/VB-4925#1444231848000
        tasks.push(customHistorySearch.bind(null, query));
    }
    asyncLib.parallel(tasks, function (err, data) {
        callback(err, data ? flatten(data) : []);
    });
}

/**
 * Retrieves all recent history items and filters them by passed query.
 * @param {String} query
 * @param {Function} callback
 */
function customHistorySearch(query, callback) {
    chrome.history.search({
        text: '',
        maxResults: CUSTOM_SEARCH_MAX_COUNT,
        startTime: Date.now() - dateUtils.WEEK_MS
    }, function (historyItems) {
        var items = filterUnmatched(historyItems, query);
        callback(null, items);
    });
}

/**
 * Searches history using chrome.history.search with passed query
 * @param {String} query
 * @param {Function} callback
 */
function nativeHistorySearch(query, callback) {
    chrome.history.search({
        text: query,
        maxResults: NATIVE_SEARCH_MAX_COUNT
    }, function (historyItems) {
        callback(null, historyItems);
    });
}

/**
 * Filters all items without query in url or title
 * @param {Array<chrome.history.HistoryItem>} historyItems
 * @param {String} query
 * @returns {Array<chrome.history.HistoryItem>}
 */
function filterUnmatched(historyItems, query) {
    return historyItems.filter(function (item) {
        return (item.url && item.url.indexOf(query) !== -1)
            || (item.title && item.title.indexOf(query) !== -1);
    });
}

/**
 * @param {String} query
 * @param {Array<chrome.history.HistoryItem>} histroryItems
 * @returns {Array<Object>}
 */
function filterByWeights(query, histroryItems) {
    var items = histroryItems.map(convertToItemWithWeight);
    return weights.calc(query, items, OUTPUT_COUNT, true);
}

/**
 * @param {chrome.history.HistoryItem} historyItem
 * @returns {Object}
 */
function convertToItemWithWeight(historyItem) {
    return weights.getItem(
        historyItem.url,
        historyItem.title,
        2 * historyItem.typedCount + historyItem.visitCount
    );
}

/**
 * @param {String} query
 * @param {Array<Object>} items
 */
function printInDebug(query, items) {
    if (!isRelease) {
        weights.printItems('Search history: ' + query, items);
    }
}



