/**
 *   -   
 */

'use strict';

var logger = require('../../utils/logger').create('Pickup/top-sites');

//        ,    .
var COUNT = 8;
//     (  VB-5516)
var BOOST_DELTA = 10;

/**
 *  
 * @param {Function} callback
 */
exports.get = function (callback) {
    chrome.topSites.get(function (items) {
        if (chrome.runtime.lastError) {
            callback(chrome.runtime.lastError);
        } else if (!Array.isArray(items)) {
            callback(new Error('top sites is not array'));
        } else {
            items = items
                .slice(0, COUNT)
                .map(formatItem);
            callback(null, items);
        }
    });
};

/**
 *   -   -  -- + 
 * @param {Array} items
 */
exports.setVisits = function (items) {
    var firstNonTopSite = items.find(function (item) {
       return !item.topSite;
    });
    var maxVisits = firstNonTopSite && firstNonTopSite.visits || 0;
    var topSiteVisits = maxVisits + BOOST_DELTA;
    logger.info('Setting visits: %i + %i(boost) = %i', maxVisits, BOOST_DELTA, topSiteVisits);
    return items.map(function (item) {
        if (item.topSite) {
           item.visits = topSiteVisits;
        }
        return item;
    });
};

function formatItem(item, index) {
    item.index = index;
    item.topSite = true;
    return item;
}



