/**
 * Loads example list from server
 */
'use strict';

var xhr = require('../../utils/xhr');
var curly = require('../../utils/curly');
var i18n = require('../i18n');
var branding = require('../branding');

var REQUEST_TIMEOUT_MS = 5000;
var REQUEST_TYPE = 'json';

/**
 * @param {Function} callback
 */
exports.load = function (callback) {
    sendRequest(function (error, json) {
        callback(error, json ? json.data : []);
    });
};

/**
 * @param {Function} callback
 */
function sendRequest(callback) {
    xhr({
        url: createUrl(),
        timeout: REQUEST_TIMEOUT_MS,
        parse: REQUEST_TYPE
    }, callback);
}

/**
 * @returns {String}
 */
function createUrl() {
    return curly.insertUrlVars(branding.search.examplesUrl, {locale: i18n.locale});
}



