/**
 *     : INSTALL, UPGRADE  NORMAL
 */

'use strict';

var versions = require('./versions');
var utils = require('../../utils/utils');
var platformAbout = require('../../platform/about');
var getMigrationData = require('../../platform/get-migration-data');
var migration223 = require('../migration/2.23.0');
var logger = require('../../utils/logger').create('Startup/runstate');

var STATES = {
    INSTALL: 0,
    UPGRADE: 1,
    NORMAL: 2
};

var state;
var migrationData;
var isReady = false;

exports.detect = function (callback) {
    var previousVersion = versions.getPrevious();
    if (!previousVersion) {
        initNoPrevVersion(callback);
    } else {
        initHasPrevVersion(previousVersion, callback);
    }
};

exports.isInstall = function () {
    return state === STATES.INSTALL;
};

exports.isUpgrade = function () {
    return state === STATES.UPGRADE;
};

exports.isNormal = function () {
    return state === STATES.NORMAL;
};

/**
 *    
 * @returns {boolean}
 */
exports.isReady = function () {
    return isReady;
};

exports.setReady = function () {
    isReady = true;
};

exports.getMigrationDataCommonBackend = function () {
    return migrationData;
};

function initNoPrevVersion(callback) {
    if (platformAbout.isChr || platformAbout.isFx) {
        //  Chrome/Firefox  previousVersion   install
        state = STATES.INSTALL;
        callback();
    } else {
        //  IE  previousVersion -    getMigrationData:
        //   - ,  upgrade,  - install
        checkMigrationData223(callback);
    }
}

function initHasPrevVersion(previousVersion, callback) {
    var currentVersion = versions.getCurrent();
    //   versionsHistory -  upgrade  normal
    state = utils.versionCompare(currentVersion, previousVersion) > 0 ? STATES.UPGRADE : STATES.NORMAL;
    //    upgrade   < 2.23,     getMigrationData,  
    //     2.23 (     ,     /,    )
    var isUpgradeToCommonBackend = exports.isUpgrade()
        && utils.versionCompare(previousVersion, migration223.COMMON_BACKND_VERSION) < 0;

    if (isUpgradeToCommonBackend) {
        checkMigrationData223(callback);
    } else {
        callback();
    }
}

/**
 * migrationData  ,       .     state.
 */
function checkMigrationData223(callback) {
    getMigrationData(function (aMigrationData) {
        if (aMigrationData) {
            logger.info('Got migrationData: %j', aMigrationData);
            state = STATES.UPGRADE;
            migrationData = aMigrationData;
            //    ,    versions.getPrevious()
            migration223.migrateVersionsHistory(aMigrationData);
        } else {
            logger.info('Empty migrationData');
            state = STATES.INSTALL;
        }

        callback();
    });
}



