/**
 *    
 */
'use strict';

var platformBranding = require('../../platform/branding');
var blocks = require('./blocks');
var i18n = require('../i18n');
var logger = require('../../utils/logger').create('Branding');
var platformClid = require('../../platform/clid');
var curly = require('../../utils/curly');
var _ = require('../../utils/lodash');
var remoteBrandingStorage = require('./remote/storage');
var channel = require('../../utils/channel');

var cache = {};

exports.REGION_BRANDINGS = {
    YANDEX: 'yandex',
    UA: 'ua',
    TB: 'tb',
};

/**
 * Init
 */
exports.init = function (callback) {
    platformBranding.init().then(function () {
        reset();
        logger.info('Branding initialized. Regional = %s, custom = %s, available = [%s]',
            exports.getRegionBrandId(),
            exports.getCustomBrandId() || '(empty)',
            platformBranding.getAvailableBrandIds().join(', ')
        );
        remoteBrandingStorage.onUpdated.addListener(onRemoteBrandingLoaded);
        callback();
    });
};

/**
 *     .   .
 *    ,   .
 *
 * @param {String} name
 * @returns {Object}
 */
exports.getBlock = function (name) {
    if (!cache[name]) {
        cache[name] = blocks.getMerged(name, {
            i18n: cache.i18n,
            expandUrl: exports.expandUrl,
        });
    }
    return cache[name];
};

/**
 *   
 *    platformBuildInfo.getRegionBrandId(),      
 * (      )
 * @returns {string}
 */
exports.getRegionBrandId = function () {
    return platformBranding.getData().vars.brandId;
};

/**
 *   /  
 *    platformBuildInfo.getCustomBrandId(),        
 * @returns {string}
 */
exports.getCustomBrandId = function () {
    var regionBrandId = exports.getRegionBrandId();
    return exports.vars.brandId !== regionBrandId ? exports.vars.brandId : '';
};

/**
 *        
 *     .
 * ,     brandings/yandex/assets/backgrounds/image.png,  
 * path = assets/backgrounds/image.png,     .
 *
 * @param {String} path
 * @returns {string}
 */
exports.getFileUrl = function (path) {
    if (typeof path !== 'string' || !path) {
        return path;
    }
    path = path.replace(/^\/+/, '');
    var regionBrandId = exports.getRegionBrandId();
    return platformBranding.getFileUrl(regionBrandId + '/' + path);
};

/**
 *     :
 * - regionTld
 * - regionId
 * - localeTld
 * - locale
 * - clidX
 * - (   vars)
 *    ,   ,      
 *
 */
exports.expandUrl = function (url, vars) {
    vars = _.merge({
        regionId: exports.vars.regionId,
        regionTld: exports.vars.regionTld,
        locale: i18n.locale,
        localeTld: i18n.localeTld,
    }, vars);
    return curly.insertUrlVars(url, vars, {
        getter: urlVarGetter
    });
};

/**
 * On remote branding loaded event
 */
exports.onRemoteLoaded = channel.create('branding.onRemoteLoaded');

function initBaseBlocks() {
    cache.i18n = blocks.getMerged('i18n');
    cache.vars = blocks.getMerged('vars');
}

function makeGetters() {
    blocks.getNames().forEach(makeGetter);
}

function makeGetter(blockName) {
    Object.defineProperty(exports, blockName, {
        configurable: true,
        get: function () {
            return exports.getBlock(blockName);
        }
    });
}

function urlVarGetter(obj, path) {
    return path.indexOf('clid') === 0
        ? platformClid.getByName(path)
        : _.get(obj, path);
}

function reset() {
    cache = {};
    initBaseBlocks();
    makeGetters();
}

function onRemoteBrandingLoaded() {
    reset();
    exports.onRemoteLoaded.dispatch();
}



