/**
 *   o   
 * @see https://wiki.yandex-team.ru/Data/Logs/Redir/techevents/#schetchikirasshirenijjdljabrauzerov
 * @see https://wiki.yandex-team.ru/Data/Logs/Redir/ClicksWeb/
 */

'use strict';

var defaults = require('lodash/object/defaultsDeep');
var experiments = require('../experiments');
var bannerId = require('../cookies/bannerid');
var settings = require('../settings/settings');
var platform = require('../../platform');
var platformAbout = require('../../platform/about');
var platformBuildInfo = require('../../platform/build-info');
var platformClids = require('../../platform/clid');
var dayuseHelper = require('./dayuse-helper');
var DayusePoller = require('./dayuse-poller');
var xhr = require('../../utils/xhr');
var curly = require('../../utils/curly');
var branding = require('../branding');
var logger = require('../../utils/logger').create('Statistics/dayuse-url');

var STAT_URL_TEMPLATE = 'https://yandex.{regionTld}/clck/click/';
var LAST_SENDING_TIME_SETTING = 'lastDayUseExpSendTime';

var poller;

exports.init = function () {
    poller = new DayusePoller(LAST_SENDING_TIME_SETTING);
    poller.onTrigger.addListener(sendStat);
};

function getDefautConfig() {
    return {
        dtype: 'elduse',
        path: 'tech.yaelements.dayuse',
        slots: [0, 0, 0],
        vars: {
            ui: '',
            bro: platformAbout.browserName,
            clid1: '',
            dayuse: 0,
            productname: platformAbout.yasoft,
            gchid: platformAbout.extensionId,  // https://st.yandex-team.ru/VB-6179
            ver: platformBuildInfo.getVersion().replace(/\./g, '-')
        }
    };
}

function sendStat() {
    //     Firefox      .
    // @see {@link https://st.yandex-team.ru/VB-5060}
    // @see {@link https://st.yandex-team.ru/VB-5084}
    if (platformAbout.id === 'fx' && !settings.get('sendUsageStat')) {
        return;
    }

    var configForUrl = getConfigForUrl();
    var url = createStatUrl(configForUrl);
    logger.info('Send to %s', url);
    xhr({url: url}, onStatResponse);
}

function getConfigForUrl() {
    var config = {
        slots: getSlots(),
        vars: getVars()
    };
    return defaults(config, getDefautConfig());
}

function getSlots() {
    var expList = experiments.getExperimentsList();
    var currentExp = expList[0];
    var slot = currentExp ? currentExp.testId : 0;
    var bucket = currentExp ? currentExp.bucket : 0;
    return [slot, slot, bucket];
}

function getVars() {
    var vars = {
        dayuse: dayuseHelper.getDayuseCount(),
        clid1: platformClids.getByNumber(1),
        ui: platform.guid
    };
    var bannerIdValue = bannerId.getBannerId();
    if (bannerIdValue) {
        vars.bnrd = bannerIdValue;
    }
    return vars;
}

function createStatUrl(config) {
    return Object.keys(config).reduce(function (result, configKey) {
        return result + processConfigParam(configKey, config[configKey]) + '/';
    }, getBaseUrl()) + '*';
}

function getBaseUrl() {
    return curly.insertUrlVars(STAT_URL_TEMPLATE, {regionTld: branding.vars.regionTld});
}

function processConfigParam(name, value) {
    var resultPart = name + '=';
    if (Array.isArray(value)) {
        return resultPart + processArrayValue(value);
    } else if (typeof value === 'object' && value !== null) {
        return resultPart + processObjectValue(value);
    }
    return resultPart + value;
}

function processArrayValue(array) {
    return array.join(',');
}

function processObjectValue(object) {
    return Object.keys(object).map(function (key) {
        return '-' + key + '=' + object[key];
    }).join(',');
}

function onStatResponse(error) {
    if (error) {
        logger.error('Error while sending statistics: %j', error);
    } else {
        poller.updateLastSendTime();
    }
}



