'use strict';

var urlUtils = require('../../utils/url');

/**
 *        
 */
function getFoundMap(substrArr, str) {
    return substrArr.reduce(function (res, s) {
        var index = str.indexOf(s);
        if (index >= 0) {
            res[s] = true;
        }
        return res;
    }, {});
}

function multiplyWeight(item, koef, comment) {
    // make log for debugging
    var msg = 'x ' + koef + (comment ? '(' + comment + ')' : '');
    item.log = item.log || [item.weight];
    item.log.push(msg);
    item.weight = item.weight * koef;
}

function hasUrlSymbols(query) {
    return /[\/\.]/.test(query);
}

function multiplyUrlQuery(query, item) {
    //  URL     ,      100.
    if (item.foundInUrl[query]) {
        multiplyWeight(item, 100, 'url with full query');
    } else if (item.foundInTitle[query]) {
        //   ,      ,     50
        multiplyWeight(item, 50, 'title with full query');
    }
    return item;
}

function multiplyMultiWords(query, item) {
    //        ,     10.
    var notQuery = function (v) {return v !== query;};
    var countInUrl = Object.keys(item.foundInUrl).filter(notQuery).length;
    var countInTitle = Object.keys(item.foundInTitle).filter(notQuery).length;
    multiplyWeight(item, 10, 'koef');
    multiplyWeight(item, (countInUrl + countInTitle), 'x word count matched in title / url');
    //     ,     100.
    if (item.foundInUrl[query] || item.foundInTitle[query]) {
        multiplyWeight(item, 100, 'full phrase match');
    }
    return item;
}

function multiplySingleWord(query, item) {
    //  html-encoded   
    var str = (item.url + ' ' + item.title)
        .replace(/&quot;/g, '\"')
        .replace(/&#039;/g, '\'');

    //      : ,   ,    .
    // : token1.token2.token3.token4/token5/token6/token7 1 2 3
    var tokens = str.split(/[\.\/\s\-"':,;]+/);
    //  'http(s)'
    tokens = tokens.slice(1);
    //  'www'
    if (tokens[0] === 'www') {
        tokens = tokens.slice(1);
    }
    //        - ,
    //      ,
    //    ,      .
    //   - 100,   - 90,  - 80, .
    //    1,      1.
    tokens.every(function (token, pos) {
        if (token && token.indexOf(query) === 0) {
            multiplyWeight(item, Math.max((100 - 10 * pos), 1), 'token #' + (pos + 1) + ' match');
            return false;
        } else {
            return true;
        }
    });
    return item;
}

module.exports = {
    /**
     *  item  calc
     * @param {String} url
     * @param {String} title
     * @param {Integer} weight
     * @returns {{url: *, title: *, weight: *}}
     */
    getItem: function (url, title, weight) {
        return {
            url: url,
            title: title,
            weight: weight
        };
    },
    /**
     *      :
     * https://wiki.yandex-team.ru/VisualBookmarks/tz#polevvodaadresasajjtaisadzhest
     *
     * @param  {String} query  
     * @param  {Array} items    {url, title, weight},  weight -  
     * @param  {Integer} count -   
     * @param  {Boolean} doFilter   ,       ,   
     * @return {Array}        {url, title, weight, domain}
     *
     *     :
     * -    : 2 * typedCount + visitCount
     * -    : 1
     * -     : 1
     * -   : 0.9-0.1,    
     *
     */
    calc: function (query, items, count, doFilter) {
        query = query.trim().toLowerCase();

        //   
        var words = query.split(/\s+/);
        words = words.length > 1 ? words : [];

        // :  + 
        var searches = [query].concat(words);

        //  ,     URI ( ..   )
        items = items.filter(function (item) {
            item.uri = urlUtils.createURI(item.url);
            return Boolean(item.uri);
        });

        items = items.map(function convertTo(item) {
            // URL    (, , ,   , );
            var uri = item.uri;
            var readable = item.url;
            try {
                // try..catch ..      "URI malformed", VBCHROME-619
                readable = uri.readable();
            } catch (e) {}

            var title = item.title || '';

            var resItem = {
                url: readable.toLowerCase(), //  punycode + decodeURI + lowercase
                uri: uri,
                sourceTitle: title,           //   
                title: title.toLowerCase(),
                weight: item.weight,
            };

            //        ,   
            resItem.foundInUrl = getFoundMap(searches, resItem.url);
            //        ,   
            resItem.foundInTitle = getFoundMap(searches, resItem.title);

            return resItem;
        });

        //  :   ,       
        //    ,   -
        if (doFilter !== false) {
            items = items.filter(function (item) {
                //  ,          
                return Object.keys(item.foundInUrl).length
                    || Object.keys(item.foundInTitle).length;
            }).filter(function (item) {
                //      ,  
                return Boolean(item.uri.hostname());
            });
        }

        items = items.map(function (item) {
            //             .
            if (words.length && (item.foundInUrl[query] || item.foundInTitle[query])) {
                multiplyWeight(item, words.length, 'words.length');
            }

            //           (  https://example.ru/),
            //      2;
            if (item.uri.path() === '/' && item.uri.search() === '' && item.uri.hash() === '') {
                multiplyWeight(item, 2, 'canonic url');
            }
            return item;
        });

        //             :
        // 1.   URL  (/, .)
        if (hasUrlSymbols(query)) {
            items = items.map(multiplyUrlQuery.bind(null, query));

        // 2.     
        } else if (words.length) {
            items = items.map(multiplyMultiWords.bind(null, query));

        // 3.    .
        } else {
            items = items.map(multiplySingleWord.bind(null, query));
        }

        //   ,    
        var hostnames = items.reduce(function (res, item) {
            var host = item.uri.hostname();
            if (!res[host] || res[host].weight < item.weight) {
                res[host] = item;
            }
            return res;
        }, {});

        //   
        items = Object.keys(hostnames).map(function (hostname) {
            var item = hostnames[hostname];
            var resItem = {
                // frontend should get encoded url, VBCHROME-630
                url: item.uri.toString(),
                title: item.sourceTitle,
                domain: hostname,
                weight: item.weight,
                log: Array.isArray(item.log) ? item.log.join(' ') : '',
            };

            return resItem;
        });

        //   
        items.sort(function (a, b) {
            return b.weight - a.weight;
        });

        // 
        if (count) {
            items = items.slice(0, count);
        }

        return items;
    },

    /**
     *      (  )
     */
    printItems: function (title, items) {
        var console = window.console; // eslint-disable-line no-console
        // eslint-disable-next-line no-console
        if (console && console.table) {
            if (items && items.length) {
                console.log(title); // eslint-disable-line no-console
                // eslint-disable-next-line no-console
                console.table(items, ['weight', 'url', 'title', 'log']);
            } else {
                console.log(title, '(empty result)'); // eslint-disable-line no-console
            }
        }
    }
};



