/**
 * Generic url utils module
 * Works with two types of urls: url and urn
 */
'use strict';

var URI = require('urijs');
var GenericModule = require('../generic-module.js');

var urlImpl = require('./url-impl');
var urnImpl = require('./urn-impl');

var URL_TYPE = 'url';
var URN_TYPE = 'urn';

/**
 * @param {String} string
 * @returns {URI|null}
 */
function createURI(string) {
    try {
        return URI(string);
    } catch (e) {
        return null;
    }
}

/**
 * This function can take arbitrary amount of arguments
 * It checks format of the first one (assuming it's a string)
 * @returns {String|null}
 */
function typeChecker() {
    if (arguments.length === 0) {
        return null;
    }
    var uri = createURI(arguments[0]);
    if (!uri) {
        return null;
    }
    if (uri.is(URL_TYPE)) {
        return URL_TYPE;
    }
    if (uri.is(URN_TYPE)) {
        return URN_TYPE;
    }
    return null;
}

var urlGeneric = new GenericModule(typeChecker);
urlGeneric.addImplementation(URL_TYPE, urlImpl);
urlGeneric.addImplementation(URN_TYPE, urnImpl);
urlGeneric.setDefaultType(URL_TYPE);

module.exports = urlGeneric;



