/**
 *     
 * https://wiki.yandex-team.ru/VisualBookmarks/tz/backup/#obuchajushhijjblok
 */

'use strict';

var Block = require('../block');
var frontendSettings = require('../../frontend/settings');

var ID = 'vbfilebackup';
var CONFIG = {
    maxShownCount: 1,
    maxRefuseCount: 0,
    duration: 1,
    interval: 0,
    silent: 10,
    afterInstall: 0
};

module.exports = function () {
    var block = new Block(ID, CONFIG);
    block.setChecker(checker.bind(null, block));
    frontendSettings.onRequested.addListener(onSettingsRequested.bind(null, block));
    return block;
};

/**
 *    
 */
function checker(block) {
    block.updateNeedShowing(true);
}

/**
 * When settings opened, block is refused
 */
function onSettingsRequested(block) {
    if (block.isShowing()) {
        block.refuse(Block.REFUSE_REASON.ACCEPT);
    }
}



