/**
 *     backgrounds
 */

// Background object format:
// {
//    preview: 'chrome-ext://.../brandings/default/fastdial/backgrounds/earth.jpg',
//    image: 'chrome-ext://.../brandings/default/fastdial/backgrounds/earth.jpg',
//    id: 'earth.Abanic',
//    color: 'ffffff',
//    isUser: false
// }

'use strict';

var cloudLoader = require('./cloud-loader');
var fontColors = require('./font-colors');
var requestAll = require('./request-all');
var bgSettings = require('../settings/backgrounds');
var bgRotation = require('./rotation');

module.exports = {
    name: 'backgrounds',

    init: function () {
        cloudLoader.init();
        bgRotation.init();
    },

    /**
     * Synchronously retrieves current background
     * @returns {Object} {id:..., image: ..., color: ...}
     */
    requestCurrent: function () {
        var backgroundId = bgSettings.getBackgroundImage();
        var backgroundPath = bgSettings.getBackgroundImagePath();
        var color = fontColors.getCached(backgroundId);

        return {
            id: backgroundId,
            image: backgroundPath,
            color: color || fontColors.DEFAULT_COLOR
        };
    },

    sync: cloudLoader.sync,

    requestAll: requestAll
};



