/**
 * Helper for creating modules with generic methods
 * It's just a realization of multimethods (https://en.wikipedia.org/wiki/Multiple_dispatch)
 * on a level of module methods (not just single functions)
 * @see utils/url/index module for example
 */
'use strict';

var MethodsTable = require('./methods-table');

/**
 * @param {Function} typeChecker
 */
function GenericModule(typeChecker) {
    this._getType = typeChecker;
    this._methodsTable = new MethodsTable();
}

/**
 * @param {String} type
 * @param {Object} module - {name: method} map
 */
GenericModule.prototype.addImplementation = function (type, module) {
    Object.keys(module).forEach(function (methodName) {
        this._methodsTable.add(type, methodName, module[methodName]);
        if (!this[methodName]) {
            this[methodName] = createGenericMethod(
                methodName, this._methodsTable, this._getType
            );
        }
    }.bind(this));
};

/**
 * @param {String} type
 */
GenericModule.prototype.setDefaultType = function (type) {
    this._methodsTable.setDefaultType(type);
};

/**
 * @param {String} methodName
 * @param {MethodsTable} methodsTable
 * @param {Function} getType
 * @returns {Function}
 */
function createGenericMethod(methodName, methodsTable, getType) {
    return function () {
        var type = getType.apply(null, arguments);
        var method = methodsTable.get(type, methodName);
        if (method) {
            return method.apply(null, arguments);
        }
        return undefined;
    };
}

module.exports = GenericModule;



