/**
 * Module handles web pages events transmitted via custom events and content script
 * e.g. https://st.yandex-team.ru/ZEN-3077#1473945493000
 */
'use strict';

const navigations = require('../navigations');
const platformAbout = require('../../platform/about');
const messaging = require('./messaging');
const events = require('./events');

module.exports = {
    init () {
        messaging.on(events.frontend.PAGE_VBNAVIGATE, function (request, sender) {
            navigateToVb(sender.tab.id);
        });
    }
};

/**
 * @param {Number} tabId
 */
function navigateToVb(tabId) {
    navigations.go({
        url: platformAbout.visbookmarksUrl,
        target: navigations.TARGET.TAB,
        tabId: tabId
    });
}



