/**
 *  /   
 */

'use strict';

const backup = require('../backup/manual');
const clicker = require('./clicker');

const SYSTEM_FILEBACKUP_SAVED = 'system.filebackup.saved';
const SYSTEM_FILEBACKUP_LOADED = 'system.filebackup.loaded';

module.exports = {
    init() {
        backup.onLoaded.addListener(() => send(SYSTEM_FILEBACKUP_LOADED));
        backup.onMade.addListener(() => send(SYSTEM_FILEBACKUP_SAVED));
    }
};

/**
 *    
 *
 * @param {String} path
 */
function send(path) {
    clicker.send({path});
}



