/**
 *    
 */
'use strict';

const Block = require('../block');
const settings = require('../../settings/settings');
const {IS_VOICE_USED} = require('../../voice/consts');
const i18n = require('../../i18n');

const ID = 'vbvoice';
const CONFIG = {
    maxShownCount: 1,
    maxRefuseCount: 0,
    duration: 1,
    interval: 0,
    silent: 10,
    afterInstall: 0
};

module.exports = function () {
    var block = new Block(ID, CONFIG);
    block.setChecker(() => checker(block));
    updateEnabled(block);
    listenSearchFormVisibility(block);
    return block;
};

/**
 *    
 */
function checker(block) {
    //        
    const isVoiceUsed = settings.get(IS_VOICE_USED) === true;
    //     ru  
    const isRuLocale = i18n.browserLocale === 'ru';

    block.updateNeedShowing(!isVoiceUsed && isRuLocale);
}

/**
 * ,     
 * see: VB-5863
 * @param {Block} block
 */
function listenSearchFormVisibility(block) {
    settings.onChanged.addListener(function (info) {
        if (info.key === 'showSearchForm') {
            updateEnabled(block);
        }
    });
}

/**
 *  ""     ,     
 * see: VB-5863
 * @param {Block} block
 */
function updateEnabled(block) {
    block.updateEnabled(settings.get('showSearchForm'));
}



