/**
 * i18n
 */

'use strict';

var platformLocale = require('../../platform/locale');
var logger = require('../../utils/logger').create('I18n');

var AVAILABLE_LOCALES = {
    be: {tld: 'by'},
    en: {tld: 'com'},
    ru: {tld: 'ru'},
    tr: {tld: 'com.tr'},
    uk: {tld: 'ua'},
};

var FALLBACK_LOCALE = 'ru';

var locale;

var i18n = module.exports = {
    /**
     * Init
     * @param {String} defaultLocale
     */
    init: function (defaultLocale) {
        if (!defaultLocale) {
            logger.error('No default locale passed! Fallback to: %s', FALLBACK_LOCALE);
            defaultLocale = FALLBACK_LOCALE;
        }
        var browserLocale = i18n.browserLocale;
        locale = AVAILABLE_LOCALES[browserLocale] ? browserLocale : defaultLocale;
        logger.info('Platform locale: %s, app locale: %s', browserLocale, locale);
    },

    /**
     * Returns original browser locale
     * @returns {String}
     */
    get browserLocale() {
        return platformLocale.get();
    },

    /**
     * Returns current locale with fallback
     * @returns {String}
     */
    get locale() {
        return locale;
    },

    /**
     * Returns current locale tld
     * @returns {String}
     */
    get localeTld() {
        return AVAILABLE_LOCALES[locale].tld;
    }
};



