/**
 *        JSON
 */
'use strict';

const collection = require('../thumbs/collection');
const settings = require('../settings/settings');
const SETTING_NAMES = require('./consts').SETTING_NAMES;

module.exports = function getCurrentData() {
    return {
        thumbs: getThumbs(),
        settings: getSettings(),
    };
};

function getSettings() {
    return SETTING_NAMES.reduce(function (memo, name) {
        memo[name] = settings.get(name);
        return memo;
    }, {});
}

function getThumbs() {
    return collection.items.map(makeThumbObject);
}

function makeThumbObject(item) {
    return {
        url: item.url,
        title: item.title,
        pinned: item.pinned,
        visits: item.visits,
    };
}



