/**
 * :   
 */

'use strict';

var logger = require('../../utils/logger').create('Frontend/settings-checkboxes');
var settings = require('../settings/settings');
var messaging = require('./messaging');
var frontendBookmarks = require('./bookmarks');
var frontendThumbSender = require('./thumb-sender');
var informers = require('../informers');
var events = require('./events');

//   
var NAMES = [
    'uiThumbsType',
    'showBookmarks',
    'showSearchForm',
    'showAdvertisement',
    'geolocation',
    'boroda',
    'sendUsageStat',
    'backgroundRotation',
];

/**
 * 
 */
exports.init = function () {
    settings.onChanged.addListener(onSettingChanged);
    //       yandex 
    if (informers.isSupported()) {
        NAMES.push('showInformers');
    }
    NAMES.push('showZen');
};

/**
 *  
 * {String} name
 * {*} value
 */
exports.apply = function (name, value) {
    if (isValidName(name)) {
        applySettingValue(name, value);
    } else {
        logger.error('Unknown setting: %s', name);
    }
};

/**
 *   
 */
exports.getValues = function () {
    return NAMES.reduce(function (result, name) {
        result[name] = settings.get(name);
        return result;
    }, {});
};

/**
 *     
 *
 * @param {String} name
 */
function isValidName(name) {
    return NAMES.indexOf(name) >= 0;
}

/**
 *  
 *
 * @param {String} name
 * @param {Boolean|Number} value
 */
function applySettingValue(name, value) {
    var oldValue = settings.get(name);
    if (oldValue !== value) {
        logger.info('Change setting %s from %s to %s', name, oldValue, value);
        settings.set(name, value);
    }
}

/**
 *     
 * https://st.yandex-team.ru/VB-5412
 *
 * @param {String} name
 * @param {Boolean|Number} newValue
 * @param {Function} callback
 */
function getData(name, newValue, callback) {
    if (name === 'showBookmarks' && newValue === true) {
        frontendBookmarks.getRootItems(callback);
    } else if (name === 'uiThumbsType') {
        callback(frontendThumbSender.getAll());
    } else {
        callback(null);
    }
}

/**
 *        
 * ( sendStat,   , ..  )
 *
 * @param {Object} info
 */
function onSettingChanged(info) {
    if (isValidName(info.key)) {
        sendSettingChanged(info.key, info.oldValue, info.newValue);
    }
}

function sendSettingChanged(name, oldValue, newValue) {
    getData(name, newValue, function (data) {
        messaging.send(events.backend.SETTING_CHANGE, {
            id: name,
            oldValue: oldValue,
            newValue: newValue,
            data: data,
        });
    });
}



