'use strict';

var settings = require('../modules/settings/settings');
var miscUtils = require('../utils/misc');

var logger = require('../utils/logger').create('Platform');

var GUID_SETTING = 'statUID';

module.exports = {
    /**
     *     (  )
     * @return {string}
     */
    get guid() {
        var guid = settings.get(GUID_SETTING);
        if (!guid) {
            guid = '{' + miscUtils.guid() + '}';
            settings.set(GUID_SETTING, guid);
            logger.info('Generated guid: %s', guid);
        }
        return guid;
    },

    /**
     *     -
     *     content-script
     * @param {object} options
     * @param {int} options.tabId
     * @param {bool} options.isYandexHost
     */
    getBarnavigParams: function (options, callback) {
        if (!options) {
            callback();
            return;
        }
        chrome.tabs.sendMessage(options.tabId, {
            contentScriptAction: 'getBarnavigParams',
            isYandexHost: options.isYandexHost
        }, callback);
    },

    /**
     * Export data for yandex browser VB-5584
     * @param {Object} data
     */
    export: function (data) {
        chrome.storage.local.set({ybimport: data});
    }
};



