/**
 * Filer proxy
 */
'use strict';

const events = require('../offscreen/events');
const offscreenManager = require('../../utils/offscreen/offscreen-manager');

class Filer {
    constructor() {}

    /**
     * Override Filer.pathToFilesystemURL method.
     */
    pathToFilesystemURL(path) {
        return chrome.runtime.getURL(path);
    }

    /**
     * Proxy request to the Filer.init method in the offscreen document.
     */
    init(opt_initObj, opt_successCallback, opt_errorHandler) {
        const callback = (message) => {
            const {errObj} = message;
            if (errObj) {
                const err = new Error(errObj.message);
                opt_errorHandler && opt_errorHandler(err);
            } else {
                opt_successCallback && opt_successCallback();
            }
        };

        const msgBody =
            {type: events.backend.FILER_INIT, data: {opt_initObj}};
        return offscreenManager.sendMessage(
            msgBody, 'Filer init timed out', callback, opt_errorHandler);
    }

    /**
     * Proxy request to the Filer.ls method in the offscreen document.
     */
    ls(dirEntryOrPath, successCallback, opt_errorHandler) {
        const callback = (message) => {
            const {errObj} = message;
            if (errObj) {
                const err = new Error(errObj.message);
                opt_errorHandler && opt_errorHandler(err);
            } else {
                successCallback(message.entries);
            }
        };

        const msgBody =
            {type: events.backend.FILER_LS, data: {dirEntryOrPath}};
        return offscreenManager.sendMessage(
            msgBody, 'Filer ls timed out', callback, opt_errorHandler);
    }

    /**
     * Proxy request to the Filer.mkdir method in the offscreen document.
     */
    mkdir(path, opt_exclusive, opt_successCallback, opt_errorHandler) {
        const callback = (message) => {
            const {errObj, entry} = message;
            if (errObj) {
                const err = new Error(errObj.message);
                opt_errorHandler && opt_errorHandler(err);
            } else {
                opt_successCallback && opt_successCallback(entry);
            }
        };

        const msgBody =
            {type: events.backend.FILER_MKDIR, data: {path, opt_exclusive}};
        return offscreenManager.sendMessage(
            msgBody, 'Filer mkdir timed out', callback, opt_errorHandler);
    }

    /**
     * Proxy request to the Filer.open method in the offscreen document.
     */
    open(entryOrPath, successCallback, opt_errorHandler) {
        const callback = (message) => {
            const {errObj, file} = message;
            if (errObj) {
                const err = new Error(errObj.message);
                opt_errorHandler && opt_errorHandler(err);
            } else {
                successCallback(file);
            }
        };

        const msgBody =
            {type: events.backend.FILER_OPEN, data: {entryOrPath}};
        return offscreenManager.sendMessage(
            msgBody, 'Filer open timed out', callback, opt_errorHandler);
    }

    /**
     * Proxy request to the Filer.create method in the offscreen document.
     */
    create(path, opt_exclusive, successCallback, opt_errorHandler) {
        const callback = (message) => {
            const {errObj} = message;
            if (errObj) {
                const err = new Error(errObj.message);
                opt_errorHandler && opt_errorHandler(err);
            } else {
                successCallback();
            }
        };

        const msgBody =
            {type: events.backend.FILER_CREATE, data: {path, opt_exclusive}};
        return offscreenManager.sendMessage(
            msgBody, 'Filer create timed out', callback, opt_errorHandler);
    }

    /**
     * Proxy request to the Filer.mv method in the offscreen document.
     */
    mv(src, dest, opt_newName, opt_successCallback, opt_errorHandler) {
        const callback = (message) => {
            const {errObj, entry} = message;
            if (errObj) {
                const err = new Error(errObj.message);
                opt_errorHandler && opt_errorHandler(err);
            } else {
                opt_successCallback && opt_successCallback(entry);
            }
        };

        const msgBody =
            {type: events.backend.FILER_MV, data: {src, dest, opt_newName}};
        return offscreenManager.sendMessage(
            msgBody, 'Filer mv timed out', callback, opt_errorHandler);
    }

    /**
     * Proxy request to the Filer.rm method in the offscreen document.
     */
    rm(entryOrPath, successCallback, opt_errorHandler) {
        const callback = (message) => {
            const {errObj} = message;
            if (errObj) {
                const err = new Error(errObj.message);
                opt_errorHandler && opt_errorHandler(err);
            } else {
                successCallback();
            }
        };

        const msgBody =
            {type: events.backend.FILER_RM, data: {entryOrPath}};
        return offscreenManager.sendMessage(
            msgBody, 'Filer rm timed out', callback, opt_errorHandler);
    }

    /**
     * Proxy request to the Filer.cd method in the offscreen document.
     */
    cd(dirEntryOrPath, opt_successCallback, opt_errorHandler) {
        const callback = (message) => {
            const {errObj, entry} = message;
            if (errObj) {
                const err = new Error(errObj.message);
                opt_errorHandler && opt_errorHandler(err);
            } else {
                opt_successCallback && opt_successCallback(entry);
            }
        };

        const msgBody =
            {type: events.backend.FILER_CD, data: {dirEntryOrPath}};
        return offscreenManager.sendMessage(
            msgBody, 'Filer cd timed out', callback, opt_errorHandler);
    }

    /**
     * Proxy request to the Filer.cp method in the offscreen document.
     */
    cp(src, dest, opt_newName, opt_successCallback, opt_errorHandler) {
        const callback = (message) => {
            const {errObj, entry} = message;
            if (errObj) {
                const err = new Error(errObj.message);
                opt_errorHandler && opt_errorHandler(err);
            } else {
                opt_successCallback && opt_successCallback(entry);
            }
        };

        const msgBody =
            {type: events.backend.FILER_CP, data: {src, dest, opt_newName}};
        return offscreenManager.sendMessage(
            msgBody, 'Filer cp timed out', callback, opt_errorHandler);
    }

    /**
     * Proxy request to the Filer.write method in the offscreen document.
     */
    write(entryOrPath, writeData, opt_successCallback, opt_errorHandler) {
        const callback = (message) => {
            const {errObj, entry} = message;
            if (errObj) {
                const err = new Error(errObj.message);
                opt_errorHandler && opt_errorHandler(err);
            } else {
                opt_successCallback && opt_successCallback(entry);
            }
        };

        const msgBody =
            {type: events.backend.FILER_WRITE, data: {entryOrPath, writeData}};
        return offscreenManager.sendMessage(
            msgBody, 'Filer write timed out', callback, opt_errorHandler);
    }

    /**
     * Proxy request to the Filer.df method in the offscreen document.
     */
    df() {
        // Do nothing, not being used.
    }

    /**
     * Proxy request to the Filer.getEntry method in the offscreen document.
     */
    getEntry(fileName, opt_successCallback, opt_errorHandler) {
        const callback = (message) => {
            const {errObj, entry} = message;
            if (errObj) {
                const err = new Error(errObj.message);
                opt_errorHandler && opt_errorHandler(err);
            } else {
                opt_successCallback && opt_successCallback(entry);
            }
        };

        const msgBody =
            {type: events.backend.FILER_GET_ENTRY, data: {fileName}};
        return offscreenManager.sendMessage(
            msgBody, 'Filer getEntry timed out', callback, opt_errorHandler);
    }
}

module.exports = Filer;



