'use strict';

var Block = require('../block');
var rotation = require('../../backgrounds/rotation');
var settings = require('../../settings/settings');

var ID = 'dailyback';
var CONFIG = {
    maxShownCount: 1,
    maxRefuseCount: 0,
    duration: 1,
    interval: 0,
    silent: 10,
    afterInstall: 1
};

module.exports = function () {
    var block = new Block(ID, CONFIG);
    block.setAcceptor(acceptor);
    block.setChecker(checker.bind(null, block));
    return block;
};

/**
 *         
 * @param {Object} block
 */
function checker(block) {
    block.updateNeedShowing(!settings.hasUserValue('backgroundRotation'));
}

/**
 *  
 */
function acceptor() {
    rotation.setSelected(true);
    rotation.rotate();
}



