/**
 *  
 */

'use strict';

var versions = require('./versions');
var installDate = require('./install-date');
var utils = require('../../utils/utils');
var platform = require('../../platform');
var waterfall = require('async').waterfall;
var platformConfigure = require('../../platform/configure');
var backup = require('../backup');
var backgroundSelector = require('../backgrounds/selector');
var bannerid = require('../cookies/bannerid');
var pickup = require('../pickup/pickup');
var installUpgrade = require('./install-upgrade');
var logger = require('../../utils/logger').create('Startup/install');
var brandingRemoteLoader = require('../branding/remote/loader');

/**
 * preInit
 * @param {Function} callback
 */
exports.preInit = function (callback) {
    installUpgrade.preInit(callback);
};

/**
 * Run
 * @param {Function} callback
 */
exports.run = function (callback) {
    logger.info('Clean install of version %s', versions.getCurrent());
    logger.info('Guid: %s', platform.guid);

    installDate.setCurrent();

    // todo: -     ,      
    //    ,    
    waterfall([
        function (callback) {
            bannerid.processCookie()
                .then(function (banneridValue) {
                    callback(null, banneridValue);
                })
                .catch(utils.noargs(callback));
        },
        function (banneridValue, callback) {
            if (banneridValue) {
                brandingRemoteLoader
                    .tryLoad({attempt: 0})
                    .then(utils.noargs(callback))
                    .catch(utils.noargs(callback));
            } else {
                callback();
            }
        },
        function (callback) {
            //     
            backup.tryRestoreFromCookies()
                .then(function (success) {
                    callback(null, success);
                })
                .catch(function (e) {
                    logger.error(e.stack);
                    callback(null, false);
                });
        },
        function (restoreSuccess, callback) {
            if (restoreSuccess) {
                //     dryRun -     ,   , VB-4784
                pickup.run({dryRun: true});
                callback();
            } else {
                //  
                // (    , ..        ,
                //     , VB-5027)
                pickup.run({fast: true}, utils.noargs(callback));
            }
        },
    ], function () {
        //   .   ,      
        // (   ).
        //    init,      backgroundChanged
        backgroundSelector.selectDefaultBackground();
        //  soft-export install 
        platformConfigure({softExport: 'send'});
        //   
        versions.addToHistory(versions.getCurrent());
        installUpgrade.run();
        callback();
    });
};



