'use strict';

var channel = require('../../utils/channel');
var curly = require('../../utils/curly');
// TODO: find out if session-wide random is needed
var AVATAR_URL = 'https://yapic.yandex.ru/get/{uid}/islands-middle?rnd=' + Math.random().toString(36);

/**
 * A class to work with user data
 * @param {?Object} account
 * @constructor
 */
function User(account) {
    this._hasAuth = false;
    this._lastAuthTime = 0;

    this._uid = '';
    this._name = '';
    this._login = '';

    /**
     * @type {Channel}
     */
    this.onLogout = channel.create('User.onLogout');

    /**
     * @type {Channel}
     */
    this.onLogin = channel.create('User.onLogin');
    if (account) {
        this.authorize(account);
    }
}

/**
 * @param {Object} json
 * @param {Object} json.account
 * @param {number} json.lastAuthTime
 * @param {boolean} json.hasAuth
 * @returns {User}
 */
User.fromJSON = function (json) {
    var user = new User(json);
    user._lastAuthTime = json.lastAuthTime;
    user._hasAuth = json.hasAuth;
    return user;
};

User.prototype = {
    /**
     * @returns {string}
     */
    get uid() {
        return this._uid;
    },

    /**
     * @returns {number}
     */
    get lastAuthTime() {
        return this._lastAuthTime;
    },

    /**
     * @returns {string}
     */
    get name() {
        return this._name;
    },

    /**
     * @returns {string}
     */
    get displayName() {
        return this._name || this._login;
    },

    /**
     * @returns {string}
     */
    get login() {
        return this._login;
    },

    /**
     * @returns {boolean}
     */
    get hasAuth() {
        return this._hasAuth;
    },

    /**
     * users from social networks (empty login)
     *
     * @returns {boolean}
     */
    get isSocial() {
        return !this.login;
    },

    get avatarUrl() {
        return curly.insertUrlVars(AVATAR_URL, this);
    }
};

User.prototype.toJSON = function () {
    return {
        uid: this._uid,
        login: this._login,
        name: this._name,
        lastAuthTime: this._lastAuthTime,
        hasAuth: this._hasAuth,
    };
};

/**
 * @returns {User}
 */
User.prototype.logout = function () {
    var wasAuth = this._hasAuth;
    this._hasAuth = false;
    if (wasAuth) {
        this.onLogout.dispatch(this);
    }
    return this;
};

/**
 * @param {Object} account
 * @returns {User}
 */
User.prototype.authorize = function (account) {
    this._uid = account.uid;
    this._name = account.displayName && account.displayName.name || '';
    this._login = account.login;

    var wasAuth = this._hasAuth;
    this._hasAuth = true;
    this._lastAuthTime = Date.now();
    if (!wasAuth) {
        this.onLogin.dispatch();
    }

    return this;
};

module.exports = User;



