/**
 *  
 */

'use strict';

var collection = require('../thumbs/collection');
var pickup = require('../pickup/pickup');
var versions = require('./versions');
var logger = require('../../utils/logger').create('Startup/normalStart');

/**
 * preInit
 * @param {Function} callback
 */
exports.preInit = function (callback) {
    callback();
};

/**
 * Run
 * @param {Function} callback
 */
exports.run = function (callback) {
    logger.info('Normal start of version %s', versions.getCurrent());
    collection.loadFromDB(function (err) {
        if (err || collection.length === 0) {
            logger.warn('Re run pickup as collection empty');
            pickup.run({fast: true}, callback);
        } else {
            // todo:           ,
            //  collection.loadMissingData,        
            // https://github.yandex-team.ru/desktop/visbookmarks-backend/pull/104#issuecomment-228943
            callback();
        }
    });
};



