/**
 * Helper for creating URIjs instances
 */
'use strict';

var URI = require('urijs');
var logger = require('../logger').create('utils/uri-creator');

var DEFAULT_PROTOCOL = 'http';

/**
 *   URI   :
 * -   ,   ,  URI
 * -   ,   DEFAULT_PROTOCOL   URI
 * @param {String} [urlOrDomain]
 * @returns {URI|null}
 */
module.exports = function (urlOrDomain) {
    if (!urlOrDomain) {
        return null;
    }
    try {
        return createURIObject(urlOrDomain);
    } catch (e) {
        logger.error('Can not create URI for %s %s', urlOrDomain, e.message);
        return null;
    }
};

/**
 * Creates URIjs object.
 * @param {String} urlOrDomain
 * @returns {URIjs}
 */
function createURIObject(urlOrDomain) {
    urlOrDomain = convertWin1251ToUnicode(urlOrDomain);
    var uri = new URI(urlOrDomain);
    if (!uri.protocol()) {
        //  toString       (   
        //  urlOrDomain)
        // https://github.com/medialize/URI.js/issues/313
        uri = new URI(uri.protocol(DEFAULT_PROTOCOL).toString());
    }
    return uri;
}

/**
 *    "https://ru.wikipedia.org/wiki/%CF%E0%EA%E8%F1%F2%E0%ED"
 *  "https://ru.wikipedia.org/wiki/%D0%9F%D0%B0%D0%BA%D0%B8%D1%81%D1%82%D0%B0%D0%BD"
 * ("https://ru.wikipedia.org/wiki/").
 *   `decodeURIComponent`        ,
 *             windows-1251.
 *
 * @see {@link https://st.yandex-team.ru/VB-5112}
 *
 * @param {string} url
 * @returns {string}
 */
function convertWin1251ToUnicode(url) {
    try {
        decodeURIComponent(url);
    } catch (e) {
        if (/%[A-F0-9]{2}/.test(url)) {
            var WIN1251_UNICODE_CHAR_SHIFT = 0x0410 - 0xC0; // Unicode "A" - ANSI "A"

            url = url.replace(/%([A-F0-9]{2})/g, function (match, charCode) {
                var charString = String.fromCharCode(parseInt('0x' + charCode, 16) + WIN1251_UNICODE_CHAR_SHIFT);
                return encodeURIComponent(charString);
            });
        }
    }
    return url;
}



