/**
 * BlacklistChecker         
 */
'use strict';

var urlUtils = require('../../utils/url');

/**
 * Creates instance of BlacklistChecker
 * @constructor
 * @param {String[]} domains
 * @param {RegExp[]} regexps
 */
function BlacklistChecker(domains, regexps) {
    this.domains = domains;
    this.regexps = regexps;
}

/**
 * Checks presence of url in blacklist (by domains and regexps).
 * Second parameter considered as domain of passed url.
 * Without second parameter, domain of url would be calculated
 * using urlUtils.getNormalDomain
 * @param {String} url
 * @param {?String} domain
 */
BlacklistChecker.prototype.contains = function (url, domain) {
    domain = domain || urlUtils.getNormalDomain(url);
    return checkByDomains(domain, this.domains)
        || checkByRegexps(url, this.regexps);
};

/**
 * Checks presence of domain in blacklist domains
 * @param {String} domain
 * @param {String[]} domainList
 */
function checkByDomains(domain, domainList) {
    return domainList.indexOf(domain) !== -1;
}

/**
 * Checks presence of url in blacklist by regexps
 * @param {String} url
 * @param {RegExp[]} regexpList
 */
function checkByRegexps(url, regexpList) {
    return regexpList.some(function (regexp) {
        return regexp.test(url);
    });
}

module.exports = BlacklistChecker;



