/**
 * Logic for restoring tabs and windows
 */
'use strict';

var browserInfo = require('../../utils/browser-info');

/**
 * @param {Array<Object>} closedTabs
 * @param {Number} windowId
 * @param {Function} [callback]
 * @returns {Number} - index of restored window (or -1)
 */
exports.restoreWindow = function (closedTabs, windowId, callback) {
    for (var i = 0; i < closedTabs.length; i++) {
        var tab = closedTabs[i];
        if (tab.id === windowId) {
            createWindow(tab.urls, tab.windowId, callback);
            return i;
        }
    }
    return -1;
};

/**
 * @param {Array<Object>} closedTabs
 * @param {String} tabId
 * @param {Function} [callback]
 * @returns {Number} - index of restored tab (or -1)
 */
exports.restoreTab = function (closedTabs, tabId, callback) {
    for (var i = 0; i < closedTabs.length; i++) {
        var tab = closedTabs[i];
        if (tab.id === tabId) {
            createTab(tab.url, tabId, callback);
            return i;
        }
    }
    return -1;
};

/**
 * @param {Strign} url
 * @param {String} restoredTabId
 * @param {Function} [callback]
 */
function createTab(url, restoredTabId, callback) {
    chrome.tabs.create({url: url}, function (tab) {
        if (callback) {
            callback(chrome.runtime.lastError, restoredTabId, tab);
        }
    });
}

/**
 * @param {Array<String>} urls
 * @param {Number} restoredWinId
 * @param {Function} callback
 */
function createWindow(urls, restoredWinId, callback) {
    var createData = {url: urls};

    // Property "focused" is unsupported by Firefox for windows.create.
    if (!browserInfo.isFirefox()) {
        createData.focused = true;
    }

    chrome.windows.create(createData, function (window) {
        if (callback) {
            callback(chrome.runtime.lastError, restoredWinId, window);
        }
    });
}



