/**
 * Screenshots in-memory-cache
 */
'use strict';

var pick = require('lodash/object/pick');

var cache = {};

var screenshotFileds = [
    'screenshotImage',
    'screenshotBgColor',
    'screenshotFontColor',
    'screenshotUpdateTime',
    'screenshotFsPath'
];

exports.updateFromThumbs = function (thumbs) {
    thumbs.forEach(function (thumb) {
        if (thumb.screenshotImage) {
            exports.set(thumb.url, pick(thumb, screenshotFileds));
        }
    });
};

exports.get = function (url) {
    return cache[url];
};

exports.set = function (url, data) {
    cache[url] = data;
};

exports.remove = function (url) {
    delete cache[url];
};

exports.clear = function () {
    cache = {};
};

exports.forEach = function (fn) {
    Object.keys(cache).forEach(fn);
};

exports.getFirstMatch = function (urls) {
    for (var i = 0; i < urls.length; i++) {
        var cachedData = exports.get(urls[i]);
        if (cachedData) {
            return cachedData;
        }
    }
    return null;
};



