/**
 * Loads screenshot into img element and adjusts it's dimensions using canvas api
 * Takes screenshot url, returns data-uri (base64 string)
 */
'use strict';

var loadImage = require('../../utils/load-image');

var WIDTH = 300;
var HEIGHT = 150;
var SCROLL_WIDTH = 10;
var SCREENSHOT_FORMAT = 'image/png';

/**
 * Loads screenshot into img element and adjusts it's dimensions
 * @param {String} imageUrl
 * @param {Function} callback
 */
module.exports = function (imageUrl, callback) {
    loadImage({url: imageUrl}, function (error, imgElement) {
        if (error) {
            callback(error);
        } else {
            adjustImage(imgElement, callback);
        }
    });
};

/**
 * Adjusts img dimensions.
 * For better quality uses two steps.
 * @see VBCHROME-451
 * @param {HTMLImageElement} img
 * @param {Function} callback
 */
function adjustImage(img, callback) {
    var ctx1 = create2DContext(img.width * 0.5, img.height * 0.5);
    ctx1.drawImage(img, 0, 0, ctx1.canvas.width, ctx1.canvas.height);

    var ctx2 = create2DContext(WIDTH, HEIGHT);
    var srcWidth = ctx1.canvas.width - SCROLL_WIDTH;
    var destHeight = Math.ceil(ctx1.canvas.height * WIDTH / ctx1.canvas.width);
    ctx2.drawImage(ctx1.canvas, 0, 0, srcWidth, ctx1.canvas.height, 0, 0, WIDTH, destHeight);

    callback(null, ctx2.canvas.toDataURL(SCREENSHOT_FORMAT));
}

function create2DContext(width, height) {
    var canvas = document.createElement('canvas');
    canvas.width = width;
    canvas.height = height;
    return canvas.getContext('2d');
}



