'use strict';

var customLogo = require('../custom-logo');

/**
 * @returns {Object|undefined}
 */
exports.getCurrent = function () {
    var current = customLogo.getCurrent();
    if (current) {
        return {
            title: current.title,
            href: current.href,
            imgUrl: current.localUrl
        };
    }
};



