/**
 * Replaces placeholders in passed strings
 */
'use strict';

var logModule = require('lggr');

var replacer = new logModule.Replacer();

/**
 * @returns {String}
 */
module.exports = function () {
    // inline arguments slice
    // it allows runtime optimizations for js engines
    var args = [];
    for (var i = 0; i < arguments.length; i++) {
        args[i] = arguments[i];
    }
    if (typeof args[0] !== 'string') {
        return toString(args);
    }
    return replacer.replace(args[0], args.slice(1));
};

function toString(args) {
    if (args.length === 0) {
        return 'undefined';
    }
    return args.map(function (item) {
        if (item === undefined) {
            return 'undefined';
        }
        try {
            return JSON.stringify(item);
        } catch (e) {
            return String(item);
        }
    }).join(' ');
}



