/**
 * Table with [name + type -> implementation] relation
 */
'use strict';

function MethodsTable() {
    this._defaultType = null;
    this._table = Object.create(null);
}

MethodsTable.prototype = {
    /**
     * @param {String} type
     */
    setDefaultType: function (type) {
        this._defaultType = type;
    },

    /**
     * @param {String} type
     * @param {String} name
     * @param {Function} implementation
     */
    add: function (type, name, implementation) {
        if (!this._table[name]) {
            this._table[name] = {};
        }
        this._table[name][type] = implementation;
    },

    /**
     * @param {String} type
     * @param {String} name
     * @returns {Function|undefined};
     */
    get: function (type, name) {
        if (this._table[name]) {
            return this._table[name][type]
                || this._table[name][this._defaultType];
        }
        return undefined;
    }
};

module.exports = MethodsTable;



