/**
 * Logic for updating closet-tabs module state
 */
'use strict';

var take = require('lodash/array/take');
var merge = require('lodash/object/merge');
var channel = require('../../utils/channel');
var urlUtils = require('../../utils/url');
var storage = require('./state-storage');

var CLOSED_TABS_LIMIT = 1000;

exports.onUpdated = channel.create();

/**
 * @param {Object} tab
 */
exports.unshift = function (tab) {
    var closedTabs = storage.get();
    closedTabs.unshift(tab);
    updateState(closedTabs);
};

/**
 * @param {Number} index
 */
exports.remove = function (index) {
    var closedTabs = storage.get();
    if (index < closedTabs.length) {
        closedTabs.splice(index, 1);
        updateState(closedTabs);
    }
};

/**
 * @returns {Array<Object>}
 */
exports.get = function () {
    return storage.get();
};

exports.clear = function () {
    updateState([]);
};

/**
 * @param {Object} tab
 */
exports.upsert = function (tab) {
    var list = storage.get();

    //       ,  ,     
    //  ,    ,        .
    // ,        
    // https://st.yandex-team.ru/VB-5756
    var existingTabIndex = findSameTabIndex(list, tab);
    if (existingTabIndex !== -1) {
        tab = merge(list.splice(existingTabIndex, 1)[0], tab);
    }

    list.unshift(tab);
    updateState(list);
};

/**
 * Removes all tabs with specified window id and closedWithWindow = true
 * @param {Number} windowId
 * @returns {Array<Object>} - list of tabs with specified windowId
 */
exports.removeClosedWithWindowTabs = function (windowId) {
    var tabs = storage.get();
    var result = {windowTabs: [], restTabs: []};
    result.restTabs = tabs.filter(function (tab) {
        if (isTabClosedWithWindow(tab, windowId)) {
            result.windowTabs.push(tab);
            return false;
        }
        return true;
    });
    updateState(result.restTabs);
    return result.windowTabs;
};

/**
 * @param {Number} oldId
 * @param {Number} newId
 */
exports.replaceWindowIds = function (oldId, newId) {
    var tabs = storage.get();
    tabs.forEach(function (tab) {
        if (tab.windowId === oldId) {
            tab.windowId = newId;
        }
    });
    updateState(tabs);
};

/**
 * @param {Array<Object>} list
 * @param {Object} tab
 * @returns {number}
 */
function findSameTabIndex(list, tab) {
    for (var i = 0; i < list.length; i++) {
        var existingTab = list[i];
        if (isSameTabs(existingTab, tab)) {
            return i;
        }
    }
    return -1;
}

/**
 * @param {Object} tab1
 * @param {Object} tab2
 * @returns {Boolean}
 */
function isSameTabs(tab1, tab2) {
    return tab1.windowId === tab2.windowId &&
        urlUtils.isEqualUrls(tab1.url, tab2.url);
}

/**
 * @param {Object} tab
 * @param {Number} windowId
 * @returns {Boolean}
 */
function isTabClosedWithWindow(tab, windowId) {
    return tab.windowId === windowId && tab.closedWithWindow;
}

/**
 * @param {Array<Object>} closedTabs
 */
function updateState(closedTabs) {
    closedTabs = take(closedTabs, CLOSED_TABS_LIMIT);
    storage.set(closedTabs);
    exports.onUpdated.dispatch(closedTabs);
}



