'use strict';

var weights = require('./weights');
var isRelease = require('../debug/build-data').git.release;

var INITIAL_WEIGHT = 1;
var OUTPUT_COUNT = 7;

module.exports = function (query, callback) {
    chrome.bookmarks.search(query, function (bookmarks) {
        var items = bookmarks.map(convertToItem);
        items = weights.calc(query, items, OUTPUT_COUNT, true);
        callback(null, items);
        printInDebug(query, items);
    });
};

function convertToItem(bookmark) {
    return weights.getItem(
        bookmark.url,
        bookmark.title,
        INITIAL_WEIGHT
    );
}

function printInDebug(query, items) {
    if (!isRelease) {
        weights.printItems('Search bookmarks: ' + query, items);
    }
}



