/**
 *   window.fetch.     xhr
 */
'use strict';

const xml = require('./xml');
const xhrUtils = require('./xhr-utils');
const logger = require('./logger').create('Utils/fetch');
const isEmpty = require('lodash/lang/isEmpty');
const isString = require('lodash/lang/isString');
const isObject = require('lodash/lang/isObject');
const pick = require('lodash/object/pick');
const urlUtils = require('./url');

const CONTENT_TYPE = 'content-type';
const APPLICATION_JSON = 'application/json';

/**
 *  window.fetch
 * {@link https://developer.mozilla.org/en-US/docs/Web/API/GlobalFetch/fetch}
 *
 * 
 * {Object} params - query 
 *
 * @type {string[]}
 */
const OPTIONS = [
    'params',
    'body',
    'method',
    'headers',
    'mode',
    'credentials',
    'cache',
    'redirect',
    'referrer',
    'integrity',
];

/**
 *   xhr
 *
 * @type {string[]}
 */
const POSTPROCESSOR_OPTIONS = [
    'responseType',
    'logError',
    'timeout',
];

/**
 *  . ,  ,     
 *  - ,     .
 *
 * @example
 `
 fetchWrapper.addInterceptor(request => {
        request.headers.set('x-auth-token', 'token');
    });

 fetchWrapper.addInterceptor(request => {
        request.credentials = 'include';
    });
 `
 *
 * @type {Array<function>}
 */
const interceptors = [];

/**
 *   
 *
 * @type {Object}
 */
const DEFAULT_OPTIONS = {
    //    
    credentials: 'include',
};

module.exports = fetchWrapper;

/**
 *   window.fetch,    xhr
 * 
 * {@link https://developer.mozilla.org/en-US/docs/Web/API/GlobalFetch/fetch}
 *
 * @param {String|Request} url
 * @param {Object} options
 * @param {String} options.mode
 * @param {Object} options.params query-
 * @param {String} options.credentials
 * @param {String} options.cache
 * @param {String} options.redirect
 * @param {String} options.referrer
 * @param {String} options.integrity
 * @param {Number} options.timeout
 * @param {String} options.method
 * @param {*} options.body
 * @param {String} options.responseType  blob, text, json, xml, raw (  Response)
 * @param {Boolean} options.logError
 * @param {Object} options.headers
 *
 * @returns {Promise}
 */
function fetchWrapper(url, options = {}) {
    let [fetchOptions, postprocessorOptions] = parseOptions(options);
    fetchOptions = Object.assign({}, DEFAULT_OPTIONS, fetchOptions);
    return new Fetch(url, fetchOptions, postprocessorOptions).promise;
}

shortcutFactory('post', postArgsProvider, fetchWrapper);
shortcutFactory('json', jsonArgsProvider, fetchWrapper);
shortcutFactory('json', jsonArgsProvider, fetchWrapper.post);
shortcutFactory('blob', blobArgsProvider, fetchWrapper);
shortcutFactory('blob', blobArgsProvider, fetchWrapper.post);

/**
 *    
 *
 * @param {function} interceptor
 */
fetchWrapper.addInterceptor = function (interceptor) {
    interceptors.push(interceptor);
};

/**
 *   
 */
fetchWrapper.removeAllInterceptors = function () {
    interceptors.length = 0;
};

/**
 * @param {String} url
 * @param {Object} init
 * @param {Object} postprocessorOptions
 * @constructor
 */
function Fetch(url, init = {}, postprocessorOptions = {}) {
    this.postprocessorOptions = postprocessorOptions;
    this.rejectedByTimeout = false;

    init.headers = init.headers || {};
    url = urlUtils.setQuery(url, init.params);

    this._prepareBody(init);

    const request = new Request(url, init);
    this._executeInterceptors(request);

    this.promise = new Promise((resolve, reject) => {
        fetch(request)
            .then(response => this._processRawResponse(response))
            .then(response => this._transformToResponseType(response))
            .catch(response => this._processError(response))
            .then(resolve)
            .catch(reject);

        if (postprocessorOptions.timeout) {
            this._setTimeout(reject);
        }
    });
}

/**
 *     
 *
 * @param {Request} request
 * @private
 */
Fetch.prototype._executeInterceptors = function (request) {
    interceptors.forEach(interceptor => interceptor(request));
};

/**
 * E init.body     ,    
 *  JSON     Content-Type = application/json
 *
 *    body  FormData, fetch   
 * Content-Type = multipart/form-data
 *
 *  Blob Content-Type    type
 *
 * @param {Object} init
 */
Fetch.prototype._prepareBody = function (init) {
    const {body} = init;

    if (body === null || body === undefined) {
        return;
    }

    if (shouldBeBodyStringifiedAsJson(body)) {
        try {
            init.body = JSON.stringify(body);
            init.headers[CONTENT_TYPE] = APPLICATION_JSON;
        } catch (e) {
            // do nothing
        }
    }
};

/**
 *   ,       fetch   
 *   ,   .
 *
 * @returns {Number}
 * @private
 */
Fetch.prototype._setTimeout = function (reject) {
    const {timeout} = this.postprocessorOptions;

    this.timeoutHandler = setTimeout(() => {
        this.rejectedByTimeout = true;
        reject(this._logError(`Aborted by timeout ${timeout} ms`));
    }, timeout);
};

/**
 *   
 *
 * @param {Response} response
 * @returns {Promise}
 * @private
 */
Fetch.prototype._processRawResponse = function (response) {
    if (this.rejectedByTimeout) {
        return Promise.reject();
    }

    clearTimeout(this.timeoutHandler);
    //   fetch      ,    
    //       then
    return this._checkStatus(response);
};

/**
 *  
 *
 * @param {Response|Error|string} response
 * @returns {Promise}
 * @private
 */
Fetch.prototype._processError = function (response) {
    clearTimeout(this.timeoutHandler);

    if (response instanceof Response) {
        return this._parseError(response)
            .then(msg => Promise.reject(msg));
    } else {
        return Promise.reject(response);
    }
};

/**
 *     content-type = application/json,     
 *
 * @param {Response} response
 * @returns {Promise}
 * @private
 */
Fetch.prototype._parseError = function (response) {
    if (isContentType(response.headers, APPLICATION_JSON)) {
        return response.json();
    } else {
        return response.text();
    }
};

/**
 *  
 *
 * @param {String} msg
 */
Fetch.prototype._logError = function (msg) {
    if (this.postprocessorOptions.logError) {
        logger.error(msg);
    }

    return msg;
};

/**
 * fetch   reject      .  
 *   .
 *
 * @param {Response} response
 * @returns {Promise}
 * @private
 */
Fetch.prototype._checkStatus = function (response) {
    if (xhrUtils.isError(response.status)) {
        return Promise.reject(response);
    }

    return Promise.resolve(response);
};

/**
 *    responseType.    ,   
 *   .
 *
 * @param {Response} response
 * @returns {Object|String|ArrayBuffer|Response|XML|Blob}
 * @private
 */
Fetch.prototype._transformToResponseType = function (response) {
    let {responseType} = this.postprocessorOptions;

    if (isEmpty(responseType)) {
        return response.text();
    }

    switch (responseType.toLocaleLowerCase()) {
        case 'raw':
            return response;
        case 'blob':
            return response.blob();
        case 'json':
            return response.json();
        case 'arraybuffer':
            return response.arrayBuffer();
        case 'xml':
            return response.text()
                .then(result => xml.fromString(result));
        default:
            return response.text();
    }
};

/**
 * @param {Headers} headers
 * @param {string} type
 * @returns {boolean}
 */
function isContentType(headers, type) {
    const contentType = headers.get(CONTENT_TYPE);

    return contentType === type;
}

/**
 *         
 *
 * @param {Object} options
 * @returns {Array}
 */
function parseOptions(options) {
    const fetchOptions = pick(options, OPTIONS);
    const postprocessorOptions = pick(options, POSTPROCESSOR_OPTIONS);

    return [fetchOptions, postprocessorOptions];
}

/**
 *         JSON
 *
 * @param {*} obj
 * @returns {boolean}
 */
function shouldBeBodyStringifiedAsJson(obj) {
    if (isString(obj)) {
        return false;
    }

    if (obj instanceof Blob) {
        return false;
    }

    if (obj instanceof FormData) {
        return false;
    }

    return isObject(obj);
}

/**
 *         
 *
 * @param {String} name  
 * @param {Function} argsProvider   ,
 *     
 * @param {Function} func      
 */
function shortcutFactory(name, argsProvider, func) {
    func[name] = function () {
        const args = argsProvider.apply(null, arguments);
        return func.apply(null, args);
    };
}

/**
 *       Blob
 *
 * @param {String} url
 * @param {Object} options
 * @returns {Array}
 */
function blobArgsProvider(url, options = {}) {
    return [url, Object.assign({}, options, {responseType: 'blob'})];
}

/**
 *       JSON 
 *
 * @param {String} url
 * @param {Object} options
 * @returns {Array}
 */
function jsonArgsProvider(url, options = {}) {
    return [url, Object.assign({}, options, {responseType: 'json'})];
}

/**
 *     post
 *
 * @param {String} url
 * @param {Object} options
 * @returns {Array}
 */
function postArgsProvider(url, options = {}) {
    return [url, Object.assign({}, options, {method: 'post'})];
}



