/**
 *      
 */

'use strict';

var {visbookmarksUrl} = require('../platform/about');
var thenChrome = require('then-chrome');

/**
 *       
 * @return {Promise<Map<Number, Array<Tab>>>}
 */
exports.getFrontendTabs = function () {
    return thenChrome.windows.getAll({
            populate: true
        })
        .then(windows => {
            return windows.reduce((windowsMap, {id: windowId, tabs}) => {
                tabs = tabs.filter(tab => visbookmarksUrl === tab.url);

                if (tabs.length) {
                    windowsMap.set(windowId, tabs);
                }

                return windowsMap;
            }, new Map());
        });
}

exports.getOpen = function (callback) {
    chrome.windows.getAll({
        populate: true
    }, function (windows) {
        if (chrome.runtime.lastError) {
            callback(chrome.runtime.lastError);
        } else {
            callback(null, getTabsFromWindows(windows));
        }
    });
};

function getTabsFromWindows(windows) {
    return windows.reduce(function (tabList, windowElem) {
        return tabList.concat(windowElem.tabs);
    }, []);
}



