/**
 *      
 */
'use strict';

var logger = require('../../utils/logger').create('Search Suggest');
var branding = require('../branding');
var xhr = require('../../utils/xhr');
var curly = require('../../utils/curly');

//     
var searchRequest;

var SEARCH_MAIN_TIMEOUT_MS = 5000;

/**
 * Returns suggest for query
 * @param {String} queryString
 * @param {Function} callback
 */
exports.get = function (queryString, callback) {
    logger.info('Loading suggest for query: %j', queryString);
    var suggestURL = curly.insertUrlVars(branding.search.suggest, {searchTerms: queryString});
    searchRequest = xhr({
        url: suggestURL,
        timeout: SEARCH_MAIN_TIMEOUT_MS,
        logError: true,
        parse: 'text',
    }, function (err, responseText, evt) {
        //        -   
        if (err || searchRequest !== evt.target) {
            callback('');
        } else {
            callback(responseText);
        }
    });
};



