/**
 * Sync API with chrome.storage.sync as backend
 */
'use strict';

var channel = require('../../utils/channel');
var logger = require('../../utils/logger').create('sync-api');
var isEmpty = require('lodash/lang/isEmpty');
var thenChrome = require('then-chrome');

var AREA_NAME = 'sync';

// NOTE
// We need this random key and value for ensuring
// that received events are from remote, not from us.
// All change events with known random values are considered as ours and ignored.
var RANDOM_ID_KEY = '_randomID_';
var randomIDValue = Math.random();

/**
 * Initializes API
 */
exports.init = function () {
    chrome.storage.onChanged.addListener(handleOnChangedEvent);
};

/**
 * Returns snapshot of all data from sync-backend
 * @returns {Promise<Object>}
 */
exports.get = function () {
    return thenChrome.storage.sync.get(null);
};

/**
 * Sets new snapshot to sync-backend
 * @param {Object} snapshot
 * @returns {Promise<Object>}
 */
exports.set = function (snapshot) {
    assertTimestamp(snapshot, 'set');
    snapshot[RANDOM_ID_KEY] = randomIDValue;
    return thenChrome.storage.sync.set(snapshot);
};

/**
 * chrome.storage.onChanged event channel
 * Dispatches only changes in 'sync' area
 * @see - https://developer.chrome.com/extensions/storage#event-onChanged
 */
exports.onChanged = channel.create();

/**
 * @param {Object} changes
 * @param {String} areaName
 */
function handleOnChangedEvent(changes, areaName) {
    if (changes && areaName === AREA_NAME && !isLocalChange(changes)) {
        var data = getNewData(changes);
        if (!isEmpty(data)) {
            exports.onChanged.dispatch(data);
        }
    }
}

/**
 * @param {Object} changes
 * @returns {Object}
 */
function getNewData(changes) {
    return Object.keys(changes).reduce(function (result, key) {
        if (key !== RANDOM_ID_KEY) {
            result[key] = changes[key].newValue;
        }
        return result;
    }, {});
}

/**
 * @param {Object} changes
 * @returns {Boolean}
 */
function isLocalChange(changes) {
    return changes[RANDOM_ID_KEY] && changes[RANDOM_ID_KEY].newValue === randomIDValue;
}

/**
 * @param {Object} snapshot
 * @param {String} method
 */
function assertTimestamp(snapshot, method) {
    if (!snapshot.timestamp) {
        logger.error('%s; You must specify timestamp in passed data', method);
    }
}



