/**
 * 
 *    ,      ..
 */

'use strict';

var logger = require('../../utils/logger').create('Navigations');
var channel = require('../../utils/channel');

//   
var TARGET = exports.TARGET = {
    TAB: 'tab',
    NEW_TAB: 'new tab',
    NEW_ACTIVE_TAB: 'new active tab',
    NEW_WINDOW: 'new window',
};

/**
 * Do navigation
 * @param {Object} params
 * @param {String} params.url
 * @param {String} params.target 'tab|new tab|new window'
 * @param {Number} [params.tabId]
 * @param {Number} [params.thumbPosition]
 */
exports.go = function (params) {
    params = params || {};
    var url = params.url;

    if (!url) {
        logger.error('Can not navigate empty url, params %j', params);
        return;
    }

    var isThumb = params.thumbPosition !== undefined;
    if (isThumb) {
        logger.info('Navigate to %s %s (from thumb %i)', params.target, url, params.thumbPosition);
    } else {
        logger.info('Navigate to %s %s', params.target, url);
    }

    var callback = onNavigated.bind(null, params);

    switch (params.target) {
        case TARGET.TAB:
            chrome.tabs.update(params.tabId, {url: url}, callback);
            break;
        case TARGET.NEW_WINDOW:
            chrome.windows.create({url: url}, callback);
            break;
        case TARGET.NEW_ACTIVE_TAB:
            chrome.tabs.create({url: url, active: true}, callback);
            break;
        case TARGET.NEW_TAB:
        /* falls through */
        default:
            chrome.tabs.create({url: url, active: false}, callback);
            break;
    }
};

/**
 * On thumb navigated event
 */
exports.onThumbNavigated = channel.create();

/**
 * Callback called after tab/window navigation
 * @param {Object} params initial navigation params {url, target, tabId, thumbPosition}
 * @param {Object} data chrome.window or chrome.tab depending on target
 */
function onNavigated(params, data) {
    if (params.thumbPosition !== undefined) {
        var isWindow = Array.isArray(data.tabs);
        var dispatchData = {
            url: params.url,
            // chrome window always contains at least one tab
            tabId: isWindow ? data.tabs[0].id : data.id,
            thumbPosition: params.thumbPosition,
        };
        exports.onThumbNavigated.dispatch(dispatchData);
    }
}



