/**
 *    ,  .
 *    
 */

'use strict';

var asyncLib = require('async');
var utils = require('../../utils/utils');
var channel = require('../../utils/channel');
var pick = require('lodash/object/pick');
var merge = require('lodash/object/merge');
var pluck = require('lodash/collection/pluck');

var titleLoader = require('../site-info/title');
var faviconLoader = require('../favicon');
var tableauLoader = require('../site-info/tableau');
var cloudLogo = require('../cloud-logo');
var screenshotsCache = require('../screenshots/cache');
var assert = require('../../utils/assert');
var dateUtils = require('../../utils/date');

var logger = require('../../utils/logger').create('Thumb');
var urlUtils = utils.url;

// Levels for thumb's props.
// When reseting thumb props with some level,
// all props with this and higher livels would be reseted.
var DOMAIN_LEVEL = 0;
var URL_LEVEL = 1;

var THUMB_FIELDS_DESCRIPTORS = [
    {name: 'url', level: DOMAIN_LEVEL},
    {name: 'pinned', level: DOMAIN_LEVEL, default: 0},
    {name: 'title', level: URL_LEVEL},
    {name: 'visits', level: DOMAIN_LEVEL, default: 0},

    {name: 'faviconUrl', level: DOMAIN_LEVEL},
    {name: 'faviconColor', level: DOMAIN_LEVEL},
    {name: 'faviconFsPath', level: DOMAIN_LEVEL},

    {name: 'tableauImage', level: DOMAIN_LEVEL},
    {name: 'tableauColor', level: DOMAIN_LEVEL},
    {name: 'tableauFsPath', level: DOMAIN_LEVEL},
    {name: 'tableauUpdateTime', level: DOMAIN_LEVEL},

    {name: 'cloudColor', level: DOMAIN_LEVEL},
    {name: 'cloudImage', level: DOMAIN_LEVEL},
    {name: 'cloudImageSub', level: DOMAIN_LEVEL},
    {name: 'cloudImageFsPath', level: DOMAIN_LEVEL},
    {name: 'cloudImageSubFsPath', level: DOMAIN_LEVEL},

    {name: 'screenshotImage', level: URL_LEVEL},
    {name: 'screenshotBgColor', level: URL_LEVEL},
    {name: 'screenshotFontColor', level: URL_LEVEL},
    {name: 'screenshotUpdateTime', level: URL_LEVEL},
    {name: 'screenshotFsPath', level: URL_LEVEL}
];

var THUMB_FIELDS = pluck(THUMB_FIELDS_DESCRIPTORS, 'name');

/**
 * Constructor
 * @constructor
 */
function Thumb(url) {
    this._resetProps(DOMAIN_LEVEL);
    if (url) {
        this.url = url;
    }
    this.onChanged = channel.create();
}

Thumb.prototype = {
    /**
     *      ,   
     */
    set url(newUrl) {
        assert.notEmpty(newUrl, 'Can not set empty url');
        newUrl = urlUtils.getNormalUrl(newUrl);
        if (this._url && !urlUtils.isEqualUrls(this._url, newUrl)) {
            logger.info('Url %s changed to %s', this._url, newUrl);
            if (this._checkForNewDomain(newUrl)) {
                this._resetProps(DOMAIN_LEVEL);
            } else {
                this._resetProps(URL_LEVEL);
            }
        }
        this._url = newUrl;
    },

    get url() {
        return this._url;
    },

    set pinned(value) {
        this._pinned = value ? 1 : 0;
        if (this.onChanged) {
            this.onChanged.dispatch(this);
        }
    },

    get pinned() {
        return this._pinned;
    }
};

/**
 *      json
 */
Thumb.fromJSON = function (json) {
    if (!json || !json.url) {
        throw new Error('Invalid json');
    }
    var thumb = new Thumb();
    var allowedFields = pick(json, THUMB_FIELDS);
    return merge(thumb, allowedFields);
};

/**
 *       json  
 */
Thumb.prototype.toJSON = function () {
    if (!this.url) {
        throw new Error('Cant get json for thumb without url');
    }
    return pick(this, THUMB_FIELDS);
};

/**
 *   
 */
Thumb.prototype.update = function (force, callback) {
    callback = callback || utils.noop;
    if (urlUtils.isAllowedForDownload(this.url)) {
        this._runUpdateTasks(force, callback);
    } else {
        var err = new Error('Can not update thumb using bad url: ' + this.url);
        logger.info('Update error: %s', err.message);
        callback(err);
    }
};

Thumb.prototype._runUpdateTasks = function (force, callback) {
    asyncLib.parallel([
        this._updateCloud.bind(this, force),
        this._updateTableau.bind(this, force),
        this._updateTitle.bind(this, force),
        this._updateFavicon.bind(this, force),
        this._updateScreenshot.bind(this, force)
    ], (error, results) => {
        var errors = [];
        var msgs = [];

        results.forEach(([error, msg] = []) => {
            if (error) {
                errors.push(error);
            }
            if (msg) {
                msgs.push(msg);
            }
        });

        if (msgs.length) {
            logger.info('Fields %s were updated for %s', msgs.join(', '), this.url);
        }

        callback(errors);
    });
};

/**
 *    
 */
Thumb.prototype._updateCloud = function (force, callback) {
    if (force || !this.cloudImage || !this.cloudColor) {
        this._loadCloudData(callback);
    } else {
        callback();
    }
};

Thumb.prototype._loadCloudData = function (callback) {
    cloudLogo.get(this.url, function (err, data) {
        var changed = this._batchChange(data, [
            {dest: 'cloudImage', src: 'logo'},
            {dest: 'cloudImageSub', src: 'logoSub'},
            {dest: 'cloudImageFsPath', src: 'logoPath'},
            {dest: 'cloudImageSubFsPath', src: 'logoSubPath'},
            {dest: 'cloudColor', src: 'color'},
        ]);

        callback(null, [err, changed ? 'cloud data' : '']);
    }.bind(this));
};

Thumb.prototype._updateTableau = function (force, callback) {
    if (force || this._isTableauNeedUpdate()) {
        this._loadTableauData(callback);
    } else {
        callback();
    }
};

Thumb.prototype._isTableauNeedUpdate = function () {
    return !this.tableauUpdateTime
        || Math.abs(Date.now() - this.tableauUpdateTime) > dateUtils.WEEK_MS;
};

Thumb.prototype._loadTableauData = function (callback) {
    tableauLoader.getManifest(this.url, function (err, data) {
        // Set tableauUpdateTime even after errors.
        // Tableau manifest is a very rare thing in the internet,
        // we don't want constantly attempt to load it.
        this.tableauUpdateTime = Date.now();
        var changed = this._batchChange(data, [
            {dest: 'tableauImage', src: 'url'},
            {dest: 'tableauColor', src: 'color'},
            {dest: 'tableauFsPath', src: 'filePath'},
        ]);

        callback(null, [err, changed ? 'tablo data' : '']);
    }.bind(this));
};

Thumb.prototype._updateFavicon = function (force, callback) {
    if (force || !this.faviconUrl) {
        this._loadFaviconData(callback);
    } else {
        callback();
    }
};

Thumb.prototype._loadFaviconData = function (callback) {
    faviconLoader.getFavicon(this.url, function (err, data) {
        var changed = this._batchChange(data, [
            {dest: 'faviconUrl', src: 'url'},
            {dest: 'faviconColor', src: 'color'},
            {dest: 'faviconFsPath', src: 'filePath'},
        ]);

        callback(null, [err, changed ? 'favicon' : '']);
    }.bind(this));
};

Thumb.prototype._updateTitle = function (force, callback) {
    //       ,
    // ..   ,     
    // todo: ,     
    if (this.title === undefined) {
        this._loadTitleData(callback);
    } else {
        callback();
    }
};

Thumb.prototype._loadTitleData = function (callback) {
    titleLoader.getTitle(this.url, function (err, data) {
        var changed = this._batchChange(data, [{dest: 'title'}]);
        callback(null, [err, changed ? 'title' : '']);
    }.bind(this));
};

Thumb.prototype._updateScreenshot = function (force, callback) {
    if (force || !this.screenshotImage) {
        this._loadScreenshotData(callback);
    } else {
        callback();
    }
};

Thumb.prototype._loadScreenshotData = function (callback) {
    var cache = screenshotsCache.get(this.url);
    var changed = this._batchChange(cache, [
        {dest: 'screenshotImage'},
        {dest: 'screenshotBgColor'},
        {dest: 'screenshotFontColor'},
        {dest: 'screenshotUpdateTime'},
        {dest: 'screenshotFsPath'},
    ]);

    callback(null, [null, changed ? 'screenshot' : '']);
};

Thumb.prototype._checkForNewDomain = function (newUrl) {
    var oldDomain = this.url ? urlUtils.getNormalDomain(this.url) : '';
    var newDomain = urlUtils.getNormalDomain(newUrl);
    return newDomain !== oldDomain;
};

Thumb.prototype._resetProps = function (level) {
    THUMB_FIELDS_DESCRIPTORS.forEach(function (fieldDescriptor) {
        if (fieldDescriptor.level >= level) {
            this._resetProperty(fieldDescriptor);
        }
    }, this);
};

Thumb.prototype._resetProperty = function (fieldDescriptor) {
    if (fieldDescriptor.default !== undefined) {
        this[fieldDescriptor.name] = fieldDescriptor.default;
    } else {
        delete this[fieldDescriptor.name];
    }
};
/**
 * Sets fields from passed object if current value differs
 * @param {Object} srcObj object with new values
 * @param {Array} fieldsMap array of {dest, src} props map
 */
Thumb.prototype._batchChange = function (srcObj, fieldsMap) {
    if (!srcObj) {
        return false;
    }

    var changed = false;
    fieldsMap.forEach(function (item) {
        var destKey = item.dest;
        var srcKey = item.src || destKey;
        var srcValue = srcObj[srcKey];
        if (this[destKey] !== srcValue) {
            this[destKey] = srcValue;
            changed = true;
        }
    }, this);

    if (changed) {
        this.onChanged.dispatch(this);
    }

    return changed;
};

module.exports = Thumb;



